<template>
  <div id="app">
    <el-row>
      <el-col :span="24">
        <Sidebar :menuList="menuData"/>
        <!-- <el-menu router :default-active="$route" class="el-menu-vertical-demo">
          <el-menu-item v-for="item in menuData" :key="item.location" :index="'/home'+item.location">
            <i class="el-icon-menu"></i>
            <span slot="title">{{item.title}}</span>
          </el-menu-item>
        </el-menu> -->
      </el-col>
    </el-row>
  </div>
</template>

<script>
import Sidebar from "./SideBar.vue"
export default {
  components: { Sidebar },
  data() {
    return {
      menuData: [],
      defaultProps: {
        children: "children",
        label: "title"
      }
    }
  },
  created() {
    this.init()
  },
  methods: {
    init() {
      this.menuData = []
      this.$get("/sys/function/queryFunctionList", {
        funType: "1"
      }).then(data => {
        this.menuData = data.data.result
        // console.log("this.menuData", this.menuData)
      })
    },
    handleOpen(key, keyPath) {
      // console.log(key, keyPath)
    },
    handleClose(key, keyPath) {
      // console.log(key, keyPath)
    }
  }
}
</script>

<style scoped lang="css">
.iconfont {
  margin-right: 5px;
  width: 24px;
  text-align: center;
  font-size: 22px;
  vertical-align: middle;
  color: #909399;
}
#app{

}
</style>



