  },
  filters: {
    roleFilter: function(val) {
      let obj = roleNameData
      return obj[val]
    },
    YYYYmmDD_Filter(dateStr, fmt) {
      // 
      if (!dateStr) {
        return ""
      }

      let nowDate = new Date(dateStr)
      fmt = fmt || "yyyy-MM-dd hh:mm:ss"

      var o = {
        "M+": nowDate.getMonth() + 1, // 
        "d+": nowDate.getDate(), // 
        "h+": nowDate.getHours(), // 
        "m+": nowDate.getMinutes(), // 
        "s+": nowDate.getSeconds(), // 
        "q+": Math.floor((nowDate.getMonth() + 3) / 3), // 
        "S": nowDate.getMilliseconds() // 
      }
      if (/(y+)/.test(fmt)) { fmt = fmt.replace(RegExp.$1, (nowDate.getFullYear() + "").substr(4 - RegExp.$1.length)) }
      for (var k in o) { if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length))) }
      return fmt
    }
  },
  computed: {
    isOrgId() {
      // if (this.isUserType && this.orgModel.length == 0) {
      // return false
      // } else {
      return true
      // }
    }
  },
  mounted() {
    this.isUserType = this.$Base64.decode(localStorage.getItem("userType")) == 1
  },
  created() {
    // if (this.$Base64.decode(localStorage.getItem("userType")) == 1) {
    //   this.activeName = "1"
    // } else {
    //   this.activeName = "2"
    // }
    if (this.$route.query.is === "a0b923820dcc509a") {
      this.activeName = "1"
    }
    if (this.$route.query.is === "9d4c2f636f067f89") {
      this.activeName = "2"
    }
    if (this.$route.query.is === "4b5ce2fe28308fd9") {
      this.activeName = "3"
    }
    if (this.$route.query.is === "a2f3e71d9181a67b") {
      this.activeName = "4"
    }
    if (this.$route.query.is === "bbce2345d7772b06") {
      this.activeName = "5"
    }
    if (this.$route.query.is === "5a880faf6fb5e608") {
      this.activeName = "6"
    }
    if (this.$route.query.is === "ceea167a5a36dedd") {
      this.activeName = "7"
    }
    if (this.$route.query.is === "fb98ab9159f51fd0") {
      this.activeName = "8"
    }
    if (this.$route.query.is === "2e2d7fbdea1afc51") {
      this.activeName = "9"
    }
    let dg = (arr, parent) => {
      for (let i = 0; i < arr.length; i++) {
        if (arr[i].children) {
          dg(arr[i].children, arr[i])
        }
        if (arr[i].type == 2) {
          arr.splice(i, 1)
          i--
        }
      }
      if (parent && parent.children && parent.children.length === 0) {
        delete parent.children
      }
    }
    this.$get("/sys/org/queryTree").then(data => {
      if (data.data.success) {
        let list = JSON.parse(JSON.stringify(data.data.result))
        dg(list)
        this.options = list
        this.options1 = list
        // this.options = data.data.result
        // this.orgModel
      }
    })
    this.$get("/sys/org/getAllAreaTreeNodes").then(res => {
      if (res.data.success) {
        this.showNewPage = true
        let list = JSON.parse(JSON.stringify(res.data.result))
        dg(list)
        // this.options1 = list
        // this.options1 = res.data.result
        // this.orgModel
      } else {
        this.showNewPage = false
      }
    })
    let This = this
    setTimeout(function() {
      This.initTree()
      This.getUserList()
      This.getRoleList()
      This.userDialog = false
      This.createFalse = true
    }, 16)
  },
  beforeRouteUpdate(to, from, next) {
    if (to.query.is === "a0b923820dcc509a") {
      this.activeName = "1"
    }
    if (to.query.is === "9d4c2f636f067f89") {
      this.activeName = "2"
    }
    if (to.query.is === "4b5ce2fe28308fd9") {
      this.activeName = "3"
    }
    if (to.query.is === "a2f3e71d9181a67b") {
      this.activeName = "4"
    }
    if (to.query.is === "bbce2345d7772b06") {
      this.activeName = "5"
    }
    if (to.query.is === "5a880faf6fb5e608") {
      this.activeName = "6"
    }
    if (to.query.is === "ceea167a5a36dedd") {
      this.activeName = "7"
    }
    if (to.query.is === "fb98ab9159f51fd0") {
      this.activeName = "8"
    }
    if (to.query.is === "2e2d7fbdea1afc51") {
