    }
    this.menuTree = []
    this.initTree()
    this.getUserList()
    this.getRoleList()
    this.isShow = true
    next()
  },
  methods: {
    changeExamineType() {
      this.isLeading = false
    },
    // 
    reflesh() {
      this.getDetail(this.detailRowId, "nodeClick")
    },
    // 
    logCurrentChange(size) {
      this.logSize = size
      this.editLog()
    },
    // 
    logSizeChange(page) {
      this.logCurrentPage = page
      // console.log(page)
    },
    initTree(val) {
      this.$get("/sys/function/queryTree", {
        funType: this.activeName
      }).then(data => {
        this.menuTree = data.data.result
        if (val) {
          this.treeDefaultCheckedKeys = [val]
          // console.log(this.treeDefaultCheckedKeys)
        }
      })
    },
    getUserList() {
      let orgModel = this.orgModel.length == 0 ? "" : this.orgModel[this.orgModel.length - 1]
      this.PZloading = true
      this.$get("/sys/config/queryUser", {
        funType: this.activeName,
        orgId: orgModel
      }).then(data => {
        if (data.data.success) {
          this.PZloading = false
          let arr = data.data.result
          for (let i = 0; i < arr.length; i++) {
            arr[i].id = arr[i].roleId
          }
          this.fromData = arr
          if (this.showNewPage) {
            this.fromData1 = JSON.parse(JSON.stringify(arr))
          } else {
            this.fromData1 = arr
          }
          // for (let i = 0; i < this.process.length; i++) {
          //   this.process[i].fromData1 = arr
          // }
        }
      })
    },
    handleChange() {
      if (this.orgModel.length < 4 && this.orgModel[1] != "271") {
        this.orgModel = []
      } else {
        this.getUserList()
        this.getRoleList()
        this.getDetail(this.funId)
      }
    },
    // 
    handleChange1(event, index) {
      // console.log(index, event)
      // 
      this.process[index].rolesData = []
      this.process[index].usersData = []
      let orgModel = event.length == 0 ? "" : event[event.length - 1]
      // 
      this.PZloading = true
      this.$get("/sys/config/queryUser", {
        funType: this.activeName,
        orgId: orgModel
      }).then(data => {
        if (data.data.success) {
          this.PZloading = false
          let arr = data.data.result
          for (let i = 0; i < arr.length; i++) {
            arr[i].id = arr[i].roleId
          }
          this.process[index].fromData1 = arr
        }
      })
      // 
      this.$get("/sys/config/queryRole", {
        funType: this.activeName,
        orgId: orgModel
      }).then(data => {
        if (data.data.success) {
          this.process[index].roleData = data.data.result
          let obj = data.data.result
          for (let item of obj) {
            roleNameData[item.id] = item.roleName
          }
        }
      })
    },
    noNegative(row, val) {
      row.wordCount = val.target.value.replace(/\-/g, "")
      row.wordCount = val.target.value.replace(/\D/g, "")
      // return val.replace(/\-/g, "")
    },
    getRoleList() {
      let orgModel = this.orgModel.length == 0 ? "" : this.orgModel[this.orgModel.length - 1]
      this.$get("/sys/config/queryRole", {
        funType: this.activeName,
        orgId: orgModel
      }).then(data => {
        if (data.data.success) {
          // for (let i = 0; i < this.process.length; i++) {
          //   this.process[i].roleData = JSON.parse(JSON.stringify(data.data.result))
          // }
          this.roleData = data.data.result
          let obj = data.data.result
          for (let item of obj) {
            roleNameData[item.id] = item.roleName
          }
        }
      })
    },
    tabClick() {
      this.menuTree = []
      this.initTree()
      this.getUserList()
      this.getRoleList()
      this.isShow = true
    },

    // 
    save() {
      for (let i = 0; i < this.process.length; i++) {
        if (this.process[i].isMaterial) {
          // console.log(this.process[i].material)
          if (this.process[i].material.length == "0") {
            this.$message.error("")
            return
          }
          if (this.process[i].material[0].name == "" || this.process[i].material[0].wordCount == "") {
            this.$message.error("")
            return
          }
        }
        // if (this.showNewPage) {
        //   if (this.process[i].orgModel.length === 0) {
        //     this.$message.error(`${i + 1}`)
        //     return
        //   }
        // }
        if (this.process[i].usersData.length === 0 && this.process[i].rolesData.length === 0 && !this.isUserType) {
          this.$message.error(`${i + 1}`)
          return
        }
        // if (this.process[i].rolesData.length === 0 && !this.isUserType) {
        //   this.$message.error(`${i + 1}`)
        //   return
        // }
        // if (this.process[i].usersData.length === 0 && !this.isUserType) {
        //   this.$message.error(`${i + 1}`)
        //   return
        // }
      }
      if ((this.process.length == 0 && this.examineType == "1" && !this.isLeading)) {
        this.$message.error("")
        return
      }
      let newArr = JSON.parse(JSON.stringify(this.process))
      // newArr.shift()
      for (let i = 0; i < newArr.length; i++) {
        newArr[i].roles = []
        if (newArr[i].rolesData.length > 0) {
          for (let j = 0; j < newArr[i].rolesData.length; j++) {
            newArr[i].roles.push({
              roleId: newArr[i].rolesData[j],
              roleName: roleNameData[newArr[i].rolesData[j]]
            })
          }
        }
        //  id 
        if (this.showNewPage) {
          if (newArr[i].orgModel.length > 0) {
            newArr[i].orgIdPath = newArr[i].orgModel.join(",")
            newArr[i].orgId = newArr[i].orgModel[(newArr[i].orgModel.length) - 1].toString()
          } else {
            newArr[i].orgIdPath = ""
            newArr[i].orgId = ""
          }
        }
        if (newArr[i].usersData.length > 0) {
          newArr[i].users = []
          for (let j = 0; j < newArr[i].usersData.length; j++) {
            newArr[i].users.push({
              userId: newArr[i].usersData[j].id,
              userName: newArr[i].usersData[j].displayName
            })
          }
        }
        delete newArr[i].rolesData
        delete newArr[i].usersData
        delete newArr[i].orgModel
        delete newArr[i].fromData1
        delete newArr[i].roleData
        if (!newArr[i].isMaterial) {
          newArr[i].material = []
        }
        newArr[i].isMaterial = Number(newArr[i].isMaterial)

        // material
      }
      let orgModel = this.orgModel.length == 0 ? "" : this.orgModel[this.orgModel.length - 1]

      if (this.saveLock) {
        return false
      }
      this.saveLock = true
      const loading = Loading.service({
        text: "..."
      })

      this.$post("/sys/config/doSave", {
        id: this.menuId,
        orgId: orgModel,
        auFlag: this.examineType,
        funId: this.funId,
        funName: this.funName || "",
        isLeading: this.isLeading ? "0" : "1",
        isProRepeat: this.isRepeat ? "0" : "1",
        areaId: this.infoCity,
        process: newArr
      }).then(data => {
        this.$mess(data)
        if (data.success) {
          // 
          this.saveLock = false
          loading.close()

          this.$sysCreatHtml(data.result, this.logObj)
          this.isShow = true
          this.getDetail(this.funId)
        } else {
          this.saveLock = false
          loading.close()
        }
      })
        .catch(error => {
          console.log(error)
          this.saveLock = false
          loading.close()
        })
      // TODO
    },
    saveUserTag() {
      let arr = []
      for (let i = 0; i < this.toData.length; i++) {
        for (let j = 0; j < this.toData[i].children.length; j++) {
          arr.push(this.toData[i].children[j])
        }
      }
      this.process[this.step].usersData = arr
      this.userDialog = false
    },
    saveRoleTag() {
      this.rolesData = []
      this.process[this.step].rolesData = JSON.parse(JSON.stringify(this.roleValue))
      this.roleDialog = false
    },
    addRole(index) {
      this.step = index
      if (this.showNewPage) {
        this.roleData = this.process[index].roleData
      }
      this.roleDialog = true
    },
    addUser(index) {
      this.step = index
      this.userDialog = true
    },
    treeAddUser(fromData, toData, obj) {
      this.toData = toData
      this.fromData = fromData
    },
    treeRemoveUser(fromData, toData, obj) {
      this.toData = toData
      this.fromData = fromData
    },
    tagClose(tag, index, index1) {
      if (!this.isUserType) {
        this.process[index].rolesData.splice(this.process[index].rolesData.indexOf(tag), 1)
        this.roleValue = this.process[index].rolesData
      }
    },
    tagUserClose(tag, step) {
      if (!this.isUserType) {
        this.$refs.tree.$refs["to-tree"].setCheckedKeys([tag.id])
        this.$refs.tree.removeToSource()
        let index = ""

        for (let i = 0; i < this.process[step].usersData.length; i++) {
          if (this.process[step].usersData[i].id == tag.id) {
            index = i
          }
        }
        this.process[step].usersData.splice(index, 1)
      }
    },
    addTable(index) {
      this.process[index].material.push({
        name: "",
        wordCount: ""
      })
    },
    // 
    handleNodeClick(row, node) {
      this.isShow = true
      // if ((this.activeName == "2" || this.activeName == "3") && node.level != 3) {
      //   return
      // }
      this.infoCity = null
      if (row.nodeType == 3 || row.nodeType == 4) {
        this.createFalse1 = true
        this.process = []
        this.funId = row.id
        this.funName = row.title
        if (this.funId) {
          this.getDetail(row.id, "nodeClick")
        }
      }
    },
    // 
    getDetail(rowId, nodeClick) {
      this.process = []
      this.detailRowId = rowId
      this.$get("/sys/config/configDetails", { funId: this.detailRowId, areaId: this.infoCity }).then(data => {
        if (data.data.success) {
          const result = data.data.result
          this.infoText.auFlag = result.checkConfig ? result.checkConfig.auFlag : null
          this.infoText.funLv = result.funLv
          this.infoText.areaName = result.areaName
          this.infoText.orgName = result.orgName
          this.infoCityArr = result.areaList
          // console.log("config/configDetails data.data:", data.data)
          if (!result) {
            return false
          }
          // 
          if (result.checkConfig) {
            if (result.checkConfig == "" || result.checkConfig.isProRepeat == "0") {
              this.isRepeat = true
            } else {
              this.isRepeat = false
            }

            if (result.checkConfig == "" || result.checkConfig.auFlag == "0") {
              this.noProInfo = false
              this.configText = ""
            } else {
              this.examineType = "1"
              this.noProInfo = true
              this.configText = ""
            }

            this.menuId = result.checkConfig.id
            this.isLeading = result.checkConfig.isLeading == 0 // 
          }else{
            this.menuId = ""
            this.isLeading = false // 
          }

          // 
          // if (result.isOper) {
          this.isOper = result.isOper
          // }

          // 
          if (result.proInfo.length > 0) {
            this.process = []
            if (!this.showNewPage) {
              for (let index = 0; index < result.proInfo.length; index++) {
                this.process.push({
                  name: result.proInfo[index].name,
                  dispatchRule: result.proInfo[index].dispatchRule,
                  isMaterial: Boolean(Number(result.proInfo[index].isMaterial)),
                  step: result.proInfo[index].step,
                  material: result.proInfo[index].materiaList,
                  orgModel: result.proInfo[index].orgIdPath ? result.proInfo[index].orgIdPath.split(",").map(cc => {
                    return parseInt(cc)
                  }) : [],
                  usersData: result.proInfo[index].userList.map(function(item) {
                    item.id = item.userId
                    item.displayName = item.userName
                    return item
                  }),
                  rolesData: result.proInfo[index].roleList.map(function(item) {
                    return item.roleId
                  })
                })
              }
            } else {
              for (let index = 0; index < result.proInfo.length; index++) {
                let obj = {
                  name: result.proInfo[index].name,
                  dispatchRule: result.proInfo[index].dispatchRule,
                  isMaterial: Boolean(Number(result.proInfo[index].isMaterial)),
                  step: result.proInfo[index].step,
                  material: result.proInfo[index].materiaList,
                  orgModel: result.proInfo[index].orgIdPath ? result.proInfo[index].orgIdPath.split(",").map(cc => {
                    return parseInt(cc)
                  }) : [],
                  usersData: result.proInfo[index].userList.map(function(item) {
                    item.id = item.userId
                    item.displayName = item.userName
                    return item
                  }),
                  rolesData: result.proInfo[index].roleList.map(function(item) {
                    return item.roleId
                  })
                }
                //  id 
                if (result.proInfo[index].orgIdPath) {
                  let orgMs = result.proInfo[index].orgIdPath.split(",")
                  let orgM = orgMs.length == 0 ? "" : orgMs[orgMs.length - 1]
                  this.PZloading = true
                  this.$get("/sys/config/queryUser", {
                    funType: this.activeName,
                    orgId: orgM
                  }).then(data => {
                    if (data.data.success) {
                      this.PZloading = false
                      let arr = data.data.result
                      for (let i = 0; i < arr.length; i++) {
                        arr[i].id = arr[i].roleId
                      }
                      obj.fromData1 = JSON.parse(JSON.stringify(arr))
                      // for (let i = 0; i < this.process.length; i++) {
                      //   this.process[i].fromData1 = arr
                      // }
                    }
                  })
                  this.$get("/sys/config/queryRole", {
                    funType: this.activeName,
                    orgId: orgM
                  }).then(data => {
                    if (data.data.success) {
                      // for (let i = 0; i < this.process.length; i++) {
                      //   this.process[i].roleData = JSON.parse(JSON.stringify(data.data.result))
                      // }
                      let obj1 = data.data.result
                      for (let item of obj1) {
                        roleNameData[item.id] = item.roleName
                      }
                      obj.roleData = JSON.parse(JSON.stringify(obj1))
                    }
                  })
                }
                // console.log(obj)
                this.process.push(obj)
              }
            }
            console.log(this.process)
          } else {
            if (nodeClick) {
              this.orgModel = []
            }
          }
        }
      })
    },
    edit() {
      if (this.isUserType && this.infoText.funLv === "" && this.infoCity === null) {
        this.$message.warning("")
      } else {
        let el = document.querySelector(".el-main")
        let input = document.querySelectorAll("input")
        let textInput = document.querySelectorAll("textarea")
        let link = document.querySelectorAll("link")
        let linkHref = ""
        linkHref = link.length > 0 ? link[0].href : "http://10.85.94.238:10540/openapiApp/download?key=bizamt/rdm/1562901848299M87.css"
        for (let i = 0; i < input.length; i++) {
          input[i].setAttribute("data-test", input[i].value)
        }

        for (let i = 0; i < textInput.length; i++) {
          textInput[i].setAttribute("data-test", textInput[i].value)
        }
        this.logObj.linkHref = linkHref
        this.logObj.el = `<!DOCTYPE html><html lang="en"><head><meta charset="utf-8"><meta name="apple-mobile-web-app-capable" content="yes"><meta name="viewport" content="width=device-width,initial-scale=1,user-scalable=0"><title></title><link href="${linkHref}" rel="stylesheet"></head>
                  <body><div style="position: fixed;width:100%;height:100%;z-index: 9999;"></div><div id="app"><section class="el-container" ><section  class="el-container is-vertical">
                  <div  class="ep-tabs"><main class="el-main">${el.innerHTML}</main></div></section></section>
                  </div></body></html>`
        this.isShow = false
      }
    },
    editLog() {
      // if (this.orgModel.length == 0) {
      //   this.$message.warning("")
      // } else {
      this.$get("sys/config/log", {
        pageSize: this.logSize,
        pageNum: this.logCurrentPage,
        configId: this.menuId
      }).then(res => {
        this.logList = res.data.result
      })
      this.logShow = true
      // }
    },
    checkHtml(index, row) {
      window.open(this.$api + "/sys/snapshot/download?logId=" + row.logId)
      // console.log(index, row)
    },
    add() {
      if (!this.isOper) {
        this.$message.error("")
        return
      }
      let index = this.process.length
      // console.log(index)

      this.process.push({
        name: "" + (Number(index) + 1),
        dispatchRule: "4",
        isMaterial: "",
        step: index,
        material: [],
        usersData: [],
        rolesData: [],
        // 
        fromData1: this.fromData1,
        roleData: this.roleData,
        orgModel: this.orgModel
      })
    },
    deleteTable(index, bigIndex) {
      this.process[bigIndex].material.splice(index, 1)
    },
    deleteIndex(index) {
      if (!this.isOper) {
        this.$message.error("")
        return false
      }
      this.process.splice(index, 1)
    },
    unshift(index) {
      let index1 = this.process.length
      this.process.splice(index, 0, {
        name: "" + (Number(index1) + 1),
        dispatchRule: "4",
        isMaterial: "",
        step: index,
        material: [],
        usersData: [],
        rolesData: [],
        fromData1: this.fromData1,
        roleData: this.roleData,
        orgModel: this.orgModel
      })
    },
    roleClose() {
      this.roleValue = []
      this.toData = []
    },
    roleOpen() {
      this.roleValue = this.process[this.step].rolesData
      // this.toData = this.process[this.step].rolesData
    },
    userClose() {
      this.toData = []
      this.fromData = []
      this.defaultCheckedKeys = []
    },
    userOpen() {
      if (this.showNewPage) {
        this.fromData = JSON.parse(JSON.stringify(this.process[this.step].fromData1))
      } else {
        this.fromData = JSON.parse(JSON.stringify(this.fromData1))
      }
      let arr = []
      for (let i = 0; i < this.process[this.step].usersData.length; i++) {
        arr.push(this.process[this.step].usersData[i].id)
      }

      this.defaultCheckedKeys = arr
    }
  }
}
</script>

<style scoped lang="less">
#app,
.el-container {
  height: 100%;
}

.el-aside {
  padding-top: 10px;
  padding-left: 10px;
  margin: -20px 0;
  margin-left: -10px;
  border-right: 1px solid #ccc;
}
.scroll {
  height: calc(100vh - 170px);
  overflow: auto;
}
.iconfont {
  font-size: 18px;
  color: #606266;
}
.size12 {
  font-size: 12px;
}
.size14 {
  font-size: 14px;
}
.size16 {
  font-size: 16px;
}
.h2 {
  font-size: 18px;
  background-color: #ecf5ff;
  line-height: 3;
  color: #606266;
  margin: 20px 0;
  padding-left: 20px;
}
/deep/ .el-form-item {
  margin-bottom: 0px;
}
/deep/ .el-transfer-panel__item {
  display: block !important;
}
.card {
  display: flex;
}
.cardP {
  min-width: 80px;
}
.inputP {
  margin-right: 20px;
}
.elCard {
  width: 100%;
  background-color: #f2f2f2;
}
.tag {
  background-color: #fff;
  margin-left: 70px;
  line-height: 50px;
  margin-top: 10px;
}
/deep/ .el-tag {
  margin-right: 5px;
}
.footer {
  padding-right: 60px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  background-color: #f2f2f2;
}
.add {
  width: calc(100%-120px);
  box-sizing: content-box;
  border: 1px dashed #ccc;
  line-height: 2.5;
  color: #409eff;
  cursor: pointer;
  text-align: center;
  margin-left: 120px;
}
/*  */
.el-transfer__buttons{
  margin: auto
}

.shenhe {
  display: flex;
}

/deep/ .is-disabled {
  .el-input__inner {
    color: #409EFF;
  }
}
</style>



