<template>
    <div class="SearchInputBox">

        <input class="InputContent" v-model="message" :placeholder="plc" />
        <!-- <div class="" @click="search"></div> -->
        <svg-icon icon-class="search" class="SearchBtn" @click="click" />
    </div>
</template>

<script>
export default {
  props: {
    plc: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      show: true,
      message: ""
    }
  },
  methods: {
    click() {
      this.$emit("SearchComponent", this.message)
    }
  }
}
</script>

<style lang="less" scoped>
    .SearchInputBox {
        border-top: 1px solid transparent;
        border-radius: 5px;
        border: 1px solid #ccc;
        width: 300px;
        height: 36px;
        padding-left: 5px;
        .InputContent {
            // width: 80%;
            float: left;
            height: 30px;
            margin-top: 2px;
            border: none;
        }
        .SearchBtn {
            float: right;
            line-height: 36px;
            margin-top: 3px;
            margin-left: 25px;
            height: 30px;
            width: 30px;
        }
    }
</style>



