    <div>
      <el-form
        ref="searchForm"
        :model="searchForm"
      >
        <el-row :gutter="10">
          <el-col :span="4">
            <el-input
              size="mini"
              placeholder=""
              v-model="searchForm.operatioName"
              prefix-icon="el-icon-search"
            ></el-input>
          </el-col>
          <el-col :span="4">
            <el-date-picker
              size="mini"
              style="width:100%"
              v-model="searchForm.arraignmentTime"
              type="daterange"
              align="right"
              unlink-panels
              range-separator=""
              start-placeholder=""
              end-placeholder=""
              value-format="yyyyMMdd"
              :picker-options="pickerOptions"
            ></el-date-picker>
          </el-col>
          <el-col
            :span="4"
            v-if="activeName==2"
          >
            <el-input
              size="mini"
              placeholder=""
              prefix-icon="el-icon-search"
              v-model="searchForm.arraignmentPeople"
            ></el-input>
          </el-col>
          <el-col :span="4">
            <el-select
              clearable
              style="width:100%"
              size="mini"
              v-model="searchForm.taskStatus"
              placeholder=""
              type="small"
            >
              <el-option
                value="0"
                label=""
              ></el-option>
              <el-option
                value="1"
                label=""
              ></el-option>
              <el-option
                value="2"
                label=""
              ></el-option>
              <el-option
                value="3"
                label=""
              ></el-option>
            </el-select>
          </el-col>
          <el-col
            :span="4"
            v-if="activeName==2"
          >
            <el-select
              clearable
              style="width:100%"
              size="mini"
              v-model="searchForm.checkResult"
              placeholder=""
              type="small"
            >
              <el-option
                value="0"
                label=""
              ></el-option>
              <el-option
                value="1"
                label=""
              ></el-option>
              <el-option
                value="2"
                label=""
              ></el-option>
            </el-select>
          </el-col>
          <el-col :span="4">
            <el-button
              size="mini"
              type="primary"
              @click="search"
            ></el-button>
          </el-col>
        </el-row>
      </el-form>
      <el-table
        :data="tableData"
        style="width: 100%"
        @row-click="rowClick"
      >
        <el-table-column
          align="center"
          prop="operatioName"
          label=""
        ></el-table-column>
        <el-table-column
          align="center"
          prop="arraignmentTime"
          label=""
        ></el-table-column>
        <el-table-column
          align="center"
          prop="arraignmentPeople"
          label=""
        ></el-table-column>
        <el-table-column
          align="center"
          prop="taskStatus"
          label=""
        >
          <template slot-scope="scope">
            <span :class="{'warning':scope.row.taskStatus=='0','success':scope.row.taskStatus=='1','error':scope.row.taskStatus=='2','info':scope.row.taskStatus=='3'}">{{scope.row.taskStatus | taskStatus}}</span>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          prop="proName"
          label=""
        ></el-table-column>
        <el-table-column
          align="center"
          prop="checkPeopleName"
          label=""
        >
          <template slot-scope="scope">
            <span>{{scope.row.checkPeopleName||''}}</span>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          v-if="activeName==2"
          prop="checkResult"
          label=""
        >
          <template slot-scope="scope">
            <span :class="{'info':scope.row.taskStatus=='0','success':scope.row.taskStatus=='1','error':scope.row.taskStatus=='2'}">{{scope.row.checkResult | checkResult}}</span>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          header-align="center"
          prop="date"
          label=""
          v-if="activeName==2"
        >
          <template slot-scope="scope">
            <el-button
              type="text"
              @click="assignClick(scope.row,true)"
              v-if="scope.row.checkResult =='0'"
            ></el-button>
            <el-button
              type="text"
              @click="assignClick(scope.row,false)"
              v-if="scope.row.dispatchRule =='2'&&scope.row.checkResult =='1'"
            ></el-button>
            <el-button
              type="text"
              @click="pointClick(scope.row)"
              v-if="scope.row.checkResult =='0'"
            ></el-button>
            <el-button
              type="text"
              @click="seeClick(scope.row)"
            ></el-button>
            <el-button
              type="text"
              @click="cancelClick(scope.row)"
              v-if="scope.row.checkResult !='0'&&scope.row.taskStatus !='1'"
            ></el-button>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          header-align="center"
          prop="date"
          label=""
          v-if="activeName==1"
        >
          <template slot-scope="scope">
            <el-button
              type="text"
              @click="assignClick(scope.row)"
              v-if="scope.row.taskStatus=='0'&&scope.row.step=='0'&&scope.row.dispatchRule =='2'"
            ></el-button>
            <el-button
              type="text"
              @click="seeClick2(scope.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <ep-page
        :total="total"
        @page="pageInit"
      ></ep-page>
    </div>

    <el-dialog
      title=""
      :visible.sync="personDialog"
      width="30%"
      @close="personDialogClose"
    >
      <el-form
        ref="dialogFormRef"
        :model="dialogForm"
        label-width="100px"
        :rules="diaLogrules"
      >
        <el-form-item label=":">
          <span>{{currentPeople}}</span>
        </el-form-item>
        <el-form-item
          label=":"
          prop="assignReason"
        >
          <el-input
            type="textarea"
            v-model="dialogForm.assignReason"
          ></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-radio-group
            v-if="assignorGroup.length>0"
            v-model="dialogForm.assignorName"
          >
            <el-radio
              :label="item.id"
              v-for="item in assignorGroup"
              :key="item.id"
            >{{item.displayName}}</el-radio>
          </el-radio-group>
          <div v-else></div>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="personDialog = false"> </el-button>
        <el-button
          type="primary"
          @click="diaLogSave('dialogFormRef')"
        > </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      diaLogrules: {
        assignReason: this.$rules("", 100)
      },
      isSub: true,
      currentPeople: "",
      activeName: "2",
      personDialog: false,
      selfSearchForm: {
        operatioName: ""
      },
      page: 1,
      size: 10,
      total: 0,
      searchForm: {
        operatioName: "",
        arraignmentTime: [],
        arraignmentPeople: "",
        taskStatus: "",
        checkResult: ""
      },
      assignorGroup: [],
      dialogForm: {
        assignReason: "",
        assignorName: ""
      },
      proId: "",
      busId: "",
      belowProId: "",
      tableData: [],
      pickerOptions: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit("pick", [start, end])
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
              picker.$emit("pick", [start, end])
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
              picker.$emit("pick", [start, end])
            }
          }
        ]
      }
    }
  },
  created() {
    this.activeName = this.$route.query.activeName || "2"
    this.search()
  },
  methods: {
    pageInit(page, size) {
      this.page = page
      this.size = size
      this.search()
    },
    handleClick() {
      this.tableData = []
      this.search()
    },
    rowClick(row) {
      this.busId = row.id
      this.proId = row.proId
    },
    seeClick(row) {
      this.$router.push({
        path: "/home/examineSee",
        query: {
          busId: row.id,
          proId: row.proId,
          checkResult: row.checkResult,
          taskStatus: row.taskStatus,
          logId: row.prseno,
          activeName: this.activeName
        }
      })
    },
    seeClick2(row) {
      this.$router.push({
        path: "/home/examineSee",
        query: {
          busId: row.id,
          proName: row.proName,
          logId: row.prseno,
          activeName: this.activeName
        }
      })
    },
    personDialogClose() {
      this.dialogForm.assignReason = ""
    },
    diaLogSave(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let assignorName = this.assignorGroup.filter(
            (item, index, arr) => item.id == this.dialogForm.assignorName
          )
          console.log(assignorName[0].displayName)
          let proId = ""
          if (this.isSub) {
            proId = this.proId
          } else {
            proId = this.belowProId
          }
          this.$post("/sys/business/manualAssign", {
            proId: proId,
            busId: this.busId,
            assignorId: this.dialogForm.assignorName,
            assignorName: assignorName[0].displayName,
            assignReason: this.dialogForm.assignReason
          }).then(data => {
            this.$mess(data)
            if (data.success) {
              this.personDialog = false
              this.search()
            }
          })
        }
      })
    },
    assignClick(row, bool) {
      console.log(row)

      this.isSub = bool
      this.belowProId = row.belowProId
      this.personDialog = true
      this.$get("/sys/business/getUserList", {
        proId: row.proId,
        busId: row.id
      }).then(data => {
        if (data.data.success) {
          this.assignorGroup = data.data.result
          if (this.assignorGroup.length > 0) {
            this.dialogForm.assignorName = this.assignorGroup[0].id
          }
          this.currentPeople = row.checkPeopleName
        }
      })
    },

    pointClick(row) {
      console.log(row)
      this.$router.push({
        path: "/home/examineSee",
        query: {
          busId: row.id,
          proId: row.proId,
          checkResult: row.checkResult,
          logId: row.prseno,
          activeName: this.activeName
        }
      })
    },
    cancelClick(row) {
      this.$get("/sys/business/withdraw", {
        busId: row.id,
        proId: row.proId
      }).then(data => {
        this.$mess(data.data)
        this.search()
      })
    },
    search() {
      let startArraignmentTime = ""
      let endArraignmentTime = ""
      if (this.searchForm.arraignmentTime != null && this.searchForm.arraignmentTime.length > 0) {
        startArraignmentTime = this.searchForm.arraignmentTime[0]
        endArraignmentTime = this.searchForm.arraignmentTime[1] + "23:59:59"
      }

      let taskStatus = this.searchForm.taskStatus || ""
      this.$get("/sys/business/queryTable", {

        operatioName: this.searchForm.operatioName,
        startArraignmentTime: startArraignmentTime,
        endArraignmentTime: endArraignmentTime,
        arraignmentPeople: this.searchForm.arraignmentPeople,
        taskStatus: taskStatus,
        checkResult: this.searchForm.checkResult || "",
        page: this.page,
        size: this.size,
        checkType: this.activeName,
        companyName: "",
        creditCode: "",
        busType: ""
      }).then(data => {
        if (data.data.pageList.success) {
          this.tableData = data.data.pageList.result
          this.total = data.data.count
        }
      })
    }
  }
}
</script>

<style scoped lang="css">
/deep/ .el-tabs__item {
  width: 100px;
  text-align: center;
}
/deep/ .el-radio-group {
  width: 100%;
  padding-left: 20px;
  box-sizing: border-box;
  border: 1px solid #ccc;
  padding: 10px 20px;
}
/deep/ .el-radio {
  display: block;
  margin-right: 0;
  line-height: 2;
}
</style>



