<template>
    <!--  -->
    <div class="MattersConfig">
        <!--  -->
        <div class="clearfix">
            <!-- <SearchComponent class="SearchComponent" @SearchComponent='getMsgFormSerachComproent'></SearchComponent> -->
            <el-button class="addMatters" type="primary" @click="addType"></el-button>
        </div>
        <!--  -->
        <div class="info">
            <div class="infoHeader">
                <span style="width:30%;height:25px"></span>
                <span style="width:20%;height:25px"></span>
                <span style="width:20%;height:25px"></span>
                <span style="width:28%;height:25px"></span>
            </div>
            <div>
                <div class="infoMationTemplate" v-for="item in list" :key="item.index">
                    <!--  -->
                    <span style="width:30%;height:25px;text-align:center">{{item.name}}</span>
                    <!--  -->
                    <span style="width:20%;height:25px;text-align:center">{{item.sortNo}}</span>
                    <!--  -->
                    <span style="width:20%;height:25px;text-align:center">{{item.mattersCount}}</span>
                    <span style="width:28%;height:25px;text-align:center">
                            <el-button size='small' type="primary" @click.stop="add(item)"></el-button>
                            <el-button size='small' type="success" @click.stop="edit(item)"></el-button>
                            <el-button size='small' type="danger" @click.stop="remove($event,item.id)"></el-button>
                    </span>
                </div>
            </div>
        </div>
        <el-dialog title="" :visible.sync="dialogFormVisible">
            <el-form :model="form">
                <el-form-item label="" :label-width="formLabelWidth" >
                    <el-input v-model="form.name" autocomplete="off" placeholder=""> </el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth" >
                    <el-input v-model="form.count" autocomplete="off" placeholder=""></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false"> </el-button>
                <el-button type="primary" :disabled="isDisabled" @click="submit"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
  data() {
    return {
      list: [],
      form: {
        name: "",
        count: ""
      },
      isDisabled: true,
      formLabelWidth: "120px",
      dialogFormVisible: false,
      activeName: "first",
      msgFormSon: "",
      activeNames: ["1"],
      currentPage4: 4,
      id: ""
    }
  },
  created() {
    // 
    this.getList()
  },
  watch: {
    // 
    form: {
      handler: function(val) {
        if (val.name != "" && val.count != "") {
          this.isDisabled = false
        } else {
          this.isDisabled = true
        }
      },
      deep: true
    }
  },
  methods: {
    // 
    addType() {
      this.form = {}
      this.dialogFormVisible = true
    },
    // 
    submit() {
      var formdata = {
        "bussType": 1,
        "name": this.form.name,
        "sortNo": Number(this.form.count)
      }
      console.log("ID", this.id)
      if (this.id != "" && this.id != null && this.id != undefined) {
        formdata.id = this.id
      } else {
        delete formdata.id
      }

      this.$post("/ybapp/pageLayout/saveSiteIconCategory", formdata).then((resData) => {
        if (resData.success) {
          this.dialogFormVisible = false
          console.log("", resData.result)
          // 
          this.getList()
        } else {}
      }).catch((error) => {
        console.log(error)
      })
    },
    // 
    getList() {
      this.$post("/ybapp/pageLayout/getSiteIconCategoryList", {
        "bussType": 1
      }).then((resData) => {
        if (resData.success) {
          console.log("", resData.result)
          this.list = resData.result.dataList
        }
      }).catch((error) => {
        console.log(error)
      })
    },
    handleClick(tab, event) {
      console.log(tab, event)
    },
    getMsgFormSerachComproent(data) {
      console.log("", data)
    },
    handleChange(val) {
      console.log(val)
    },
    add(item) {
      console.log(item)
    },
    edit(item) {
      console.log("")
      // 
      console.log(item)
      this.form.name = item.name
      this.form.count = item.sortNo
      this.id = item.id
      this.dialogFormVisible = true
    },
    // 
    remove(e, id) {
      console.log(id)
      this.$post("/ybapp/pageLayout/deleteSiteIconCategory", {
        "id": id
      }).then((resData) => {
        if (resData.success) {
          console.log("", resData)
          this.getList()
        }
      }).catch((error) => {
        console.log(error)
      })
    },
    lookInfomation(e) {
      console.log("")
    },
    changeConfig(e) {
      console.log("")
    }
  }
}
</script>

<style scoped lang="less">
    .MattersConfig {
        background-color: #fff;
        .SearchComponent {
            float: left;
        }
        .addMatters {
            float: left;
        }
        .info {
            margin-top: 15px;
            .infoHeader {
                background-color: #ddd;
                height: 45px;
                line-height: 45px;
                span {
                    text-align: center;
                    display: inline-block;
                }
            }
            .infoMationTemplate {
                cursor: pointer;
                border-bottom: 1px solid #eee;
                height: 40px;
                line-height: 40px;
                background-color: #fff;
                span {
                    display: inline-block;
                    text-align: center;
                }
            }
        }
        .block {
            position: fixed;
            bottom: 20px;
            left: 50%;
            transform: translateX(-50%);
        }
    }
</style>



