<template>
    <div class="phoneContent">
        <img src="../../assets/img/indexSketch.png" alt="">
        <div class="iconContent">
            <div class="iconBox" v-for="(item,index) in iconList" :key="index">
                <div class="photoBox">
                    <img v-if="item.imgUrl" :src="item.imgUrl" alt="" />
                </div>
                <div class="text">{{item.text}}</div>
            </div>
            <div class="iconBox">
                <svg-icon icon-class="more"></svg-icon>
                <div class="text"></div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  props: {
    iconList: {
      type: Array,
      default: []
    }
  }
}
</script>

<style lang="less" scoped>
.phoneContent{
    position: relative;
    width: 375px;
    min-width: 375px;
    border: 1px solid #EEE;
    img{
        width: 100%;
    }
    .iconContent{
        width: 375px;
        height: 150px;
        position: absolute;
        top: 320px;
        display: flex;
        flex-wrap: wrap;
        .iconBox{
            height: 70px;
            width: 70px;
            margin-left: 20px;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            align-items: center;
            .svg-icon{
                height: 34px;
                width: 34px;
            }
            .photoBox{
                height: 34px;
                width: 34px;
                img{
                    height: 100%;
                    width: 100%;
                }
            }
            .text{
                font-size: 14px;
                color: #3A4259;
                letter-spacing: 0;
                text-align: center;
                line-height: 14px;
            }
        }
    }
}
</style>



