      // console.log('this.pm_transfer',this.pm_transfer);

      const params = {
        status: this.PerformManage.status, // /
        areaList: [], // 
        matterList: [], // 
        blTimeList: [] // 
      }

      // 
      const _pmAreaId = this.pm_areaId
      // console.log('typeof(_pmAreaId[0])',_pmAreaId[0],typeof(_pmAreaId[0]));
      if (_pmAreaId[0]) {
        if (typeof _pmAreaId[0] === "number") {
          params.areaList.push(_pmAreaId[_pmAreaId.length - 1])
        } else {
          this.pm_areaId.forEach(function(item) {
            params.areaList.push(item[item.length - 1])
          })
        }
      }
      // params.areaList = JSON.stringify(params.areaList)
      // console.log('params.areaList:',params.areaList);

      this.pm_transfer.length && this.pm_transfer.forEach(function(item) {
        transferData_dict[item] && params.matterList.push(transferData_dict[item])
      })
      // console.log('params.matterList:',params.matterList);

      this.pm_time.forEach(function(item, index) {
        params.blTimeList.push({
          month: index + 1,
          begTime: self.reTime(item.time[0], "yyyy-MM-dd") + " 00:00:00",
          endTime: self.reTime(item.time[1], "yyyy-MM-dd" + " 23:59:59")
        })
      })
      // console.log('this.pm_time:',params.blTimeList);

      this.openFullScreen()
      // 
      this.$post("/matterExecute/doSave", params).then((data) => {
        this.$mess(data)
        this.dataSaveLock.close()

        if (data.success) {
          // console.log('bingo');
          // console.log('params.areaList,,',params.areaList);
          if (params.areaList.length > 1) {
