    initDate(dateList) {
      let count = 0
      function getDate(item, monthNumber) {
        const date = new Date()
        const monthOfDay = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31]
        const startTime = new Date(date.getFullYear(), monthNumber, 1, 0, 0, 0)
        const endTime = new Date(date.getFullYear(), monthNumber, monthOfDay[monthNumber], 23, 59, 59)
        item.time = [startTime, endTime]
      }
      function loop(item) {
        if (count < 12) {
          if (item) {
            item.time = [new Date(item.begTime), new Date(item.endTime)]
          } else {
            let _item = {}
            getDate(_item, count)
            dateList.push(_item)
          }
          count += 1
          loop(dateList[count])
        }
      }
      loop(dateList[count])
      // console.log(dateList);
      dateList.length = 12
      return dateList
    },
