        areaId: this.areaId,
        mattersIds: this.transferConfigModel
      }).then((data) => {
        this.$mess(data)
        if (data.success) {
          this.dialogConfig = false
        }
      })
    },
    // 
    classCancleTable(row, index) {
      this.initItemClass()
      // row.date = row.data1
      // row.isShow = false
      // this.thirdTableData.push()
    },
    // 
    addClass() {
      if (this.thirdTableData.length > 0 && this.thirdTableData[this.thirdTableData.length - 1].isShow) {
        return
      }
      this.thirdTableData.push({
        name: "",
        sortNo: "",
        isShow: true
      })
    },
    // 
    handlePictureCardPreview(file) {
      this.imageUrl = file.url
      this.iconDialog = true
    },
    handleExceed(files, fileList) {
      // console.log("", fileList)

      this.$message.warning("")
    },
    pcHandleRemove(file, fileList) {
      // console.log(file, fileList)
      this.pcForm.picUrl = ""
    },
    appHandleRemove(file, fileList) {
      // console.log(file, fileList)
      this.appForm.picUrl = ""
    },
    //  -> 
    configListClick(item, index) {
      // console.log(getTreeDataFilter);
      this.configTitle = item.name
      this.configListIndex = index
      this.opDaId = item.id
      this.$get("/siteMatters/siteMattersDetail", {
        id: item.id
      }).then((data) => {
        if (data.data.success) {
          // console.log(data.data);
          this.itemId = data.data.result.siteMattersDto.id
          this.isShow = true
          this.modifyTime = data.data.result.siteMattersDto.modifyTime
          this.modifierName = data.data.result.siteMattersDto.modifierName
          this.createTime = data.data.result.siteMattersDto.createTime
          this.creatorName = data.data.result.siteMattersDto.creatorName
          this.rightForm.name = data.data.result.siteMattersDto.name
          this.rightForm.sysFunctionId = data.data.result.siteMattersDto.sysFunctionId
          this.rightForm.shortName = data.data.result.siteMattersDto.shortName
          this.rightForm.manageCode = data.data.result.siteMattersDto.manageCode
          this.rightForm.iconCategoryId = data.data.result.siteMattersDto.iconCategoryId == 0 ? "" : data.data.result.siteMattersDto.iconCategoryId
          this.rightForm.siteBusinessType = data.data.result.siteMattersDto.siteBusinessType
          this.rightForm.unitChecked = data.data.result.orgAreas
          this.rightForm.personalChecked = data.data.result.personAreas
          this.pcForm.jumpUrl = data.data.result.pcBlock.jumpUrl
          this.pcForm.picUrl = data.data.result.pcBlock.picUrl
          this.pcForm.sortNumber = data.data.result.pcBlock.sortNumber
          this.pcForm.status = data.data.result.pcBlock.status
          this.appForm.jumpUrl = data.data.result.appBlock.jumpUrl
          this.appForm.picUrl = data.data.result.appBlock.picUrl
          this.appForm.sortNumber = data.data.result.appBlock.sortNumber
          this.appForm.status = data.data.result.appBlock.status
          this.basieUrl = data.data.result.pcBlock.basieUrl
          if (!this.basieUrl) {
            this.basieUrl = data.data.result.appBlock.basieUrl
          }
          this.pcFileList = []
          this.appFileList = []
          if (this.pcForm.picUrl) {
            this.pcFileList.push({
              url: this.basieUrl + this.pcForm.picUrl
            })
          }
          if (this.appForm.picUrl) {
            this.appFileList.push({
              url: this.basieUrl + this.appForm.picUrl
            })
          }

          this.rightForm.unitChecked = []
          if (data.data.result.orgAreas) {
            for (let i = 0; i < data.data.result.orgAreas.length; i++) {
              this.rightForm.unitChecked.push(String(data.data.result.orgAreas[i].areaPath).split(","))
            }
          }
          this.rightForm.personalChecked = []
          if (data.data.result.personAreas) {
            for (let i = 0; i < data.data.result.personAreas.length; i++) {
              this.rightForm.personalChecked.push(String(data.data.result.personAreas[i].areaPath).split(","))
            }
          }

          if (this.rightForm.unitChecked.length) {
            this.unitChecked = true
          } else {
            this.unitChecked = false
          }
          if (this.rightForm.personalChecked.length) {
            this.personalChecked = true
          } else {
            this.personalChecked = false
          }
          if (this.pcForm.jumpUrl) {
            this.itemConfigChecked1 = true
          } else {
            this.itemConfigChecked1 = false
          }
          if (this.appForm.jumpUrl) {
            this.itemConfigChecked2 = true
          } else {
            this.itemConfigChecked2 = false
          }
        }
      })

      //  ()
      if (this.isAdmin) {
        this.getSiteMattersPermissionTree(item.id)
      }
    },

    classEdit(row, index) {
      row.isShow = true
      row.data1 = row.date
      this.thirdTableData.push()
    },

    addMenu() {
      this.itemId = ""
      this.isShow = false
      this.rightForm.name = ""
      this.rightForm.shortName = ""
      this.rightForm.manageCode = "" // 
      this.rightForm.iconCategoryId = ""
      this.rightForm.siteBusinessType = ""
      this.unitChecked = true
      this.personalChecked = true
      this.itemConfigChecked1 = false
      this.itemConfigChecked2 = false
      this.pcForm.jumpUrl = ""
      this.pcForm.picUrl = ""
      this.appForm.jumpUrl = ""
      this.appForm.picUrl = ""
      this.appFileList = []
      this.pcFileList = []
    },
    edit() {
      this.isShow = false
    },
    itemSave() {
      if (this.itemConfigChecked2) {
        if (!this.appForm.jumpUrl) {
          this.$message.warning("")
          return
        }
      }
      if (this.itemConfigChecked1) {
        if (!this.pcForm.jumpUrl) {
          this.$message.warning("")
          return
        }
      }
      let arr = []
      if (!this.unitChecked) {
        this.rightForm.unitChecked = []
      }
      if (!this.personalChecked) {
        this.rightForm.personalChecked = []
      }
      for (let i = 0; i < this.rightForm.unitChecked.length; i++) {
        arr.push({
          accountTypeId: 1,
          areaId: this.rightForm.unitChecked[i][this.rightForm.unitChecked[i].length - 1],
          areaPath: this.rightForm.unitChecked[i].length < 6 ? this.rightForm.unitChecked[i].join() : this.rightForm.unitChecked[i]
        })
      }
      for (let i = 0; i < this.rightForm.personalChecked.length; i++) {
        arr.push({
          accountTypeId: 2,
          areaId: this.rightForm.personalChecked[i][this.rightForm.personalChecked[i].length - 1],
          areaPath: this.rightForm.personalChecked[i].length < 6 ? this.rightForm.personalChecked[i].join() : this.rightForm.personalChecked[i]
        })
      }

      const params = {
        id: this.itemId,
        bussType: this.$bussType,
        iconCategoryId: this.rightForm.iconCategoryId,
        siteBusinessType: this.rightForm.siteBusinessType,
        name: this.rightForm.name,
        shortName: this.rightForm.shortName,
        sysFunctionId: this.rightForm.sysFunctionId,
        sysFunctionName: this.rightForm.sysFunctionName,
        manageCode: this.rightForm.manageCode,
        appBlock: {
          jumpUrl: this.appForm.jumpUrl,
          isHome: 1,
          picUrl: this.appForm.picUrl,
          sortNumber: this.appForm.sortNumber,
          status: this.appForm.status
        },
        pcBlock: {
          jumpUrl: this.pcForm.jumpUrl,
          isHome: 1,
          picUrl: this.pcForm.picUrl,
          sortNumber: this.pcForm.sortNumber,
          status: this.pcForm.status
        },
        siteMattersAreaSaveVOS: arr
      }
      //  ()
      if (this.isAdmin) {
        // console.log("this.qxfp_tree_selected::", this.qxfp_tree_selected)
        // console.log("this.defaultCheck::", this.defaultCheck)
        params.saveSiteMattersPermissionListVO = {}
        if (this.qxfp_tree_selected.length) {
          params.saveSiteMattersPermissionListVO.areaIds = this.qxfp_tree_selected
        } else if (this.defaultCheck.length) {
          params.saveSiteMattersPermissionListVO.areaIds = this.defaultCheck
        }
      }
      this.$post("/siteMatters/saveSiteMatters", params).then((data) => {
        if (data.success) {
          this.getItemList()
          this.isShow = true
        }
        this.$mess(data)
      })
    },
    cancle() {
      this.isShow = true
    },
    appHandleAvatarSuccess(res, file) {
      this.appForm.picUrl = file.response.result.key
    },
    pcHandleAvatarSuccess(res, file) {
      this.pcForm.picUrl = file.response.result.key
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg" || "image/png"
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!isJPG) {
        this.$message.error(" JPGPNG !")
      }
      if (!isLt2M) {
        this.$message.error(" 2MB!")
      }
      return isJPG && isLt2M
    }
  }
}
</script>

