      // vertical-align: middle;
    }
    .cont {
      // vertical-align: middle;
      padding-left: 20px;
      overflow: hidden;
    }
  }
  .timeQuantumWrap {
    width: 700px;
  }
  .timeQuantum {
    margin-right: 30px;
    margin-bottom: 10px;
    float: left;
  }
