<template>
    <div class="mattersDetail">
        <p class="mattersTitle">{{form.name}}</p>
        <!--  -->
        <div class="baseInfo clearfix">
            <div class="infoTitle"></div>
            <div class="baseContent">
                <el-form :model="form">
                    <el-form-item label="ID: " :label-width="formLabelWidth" style="width:49%;float:left">
                        <el-input style="width:70%;" disabled v-model="form.id" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label=": " :label-width="formLabelWidth" style="width:49%;float:left">
                        <el-input style="width:70%;" disabled v-model="form.shortName"></el-input>
                    </el-form-item>
                    <el-form-item label=": " :label-width="formLabelWidth" style="width:49%;float:left">
                        <el-input style="width:70%;" disabled v-model="form.shortName"></el-input>
                    </el-form-item>
                    <el-form-item label=": " :label-width="formLabelWidth" style="width:49%;float:left">
                        <el-input style="width:70%;" disabled v-model="form.createTime"></el-input>
                    </el-form-item>
                    <el-form-item label=": " :label-width="formLabelWidth" style="width:49%;float:left">
                        <el-input style="width:70%;" disabled v-model="form.iconCategoryName"></el-input>
                    </el-form-item>
                    <el-form-item label=": " :label-width="formLabelWidth" style="width:49%;float:left">
                        <el-input style="width:70%;" disabled v-model="areaName"></el-input>
                    </el-form-item>
                    <el-form-item label=": " :label-width="formLabelWidth" style="width:86%;float:left">
                        <el-input type="textarea" disabled style="width:100%;" v-model="form.desc"></el-input>
                    </el-form-item>
                </el-form>
            </div>
        </div>
        <!--  -->
        <div class="configInfo">
            <div class="infoTitle"></div>
            <div class="configContent">
                <!--  -->
                <div class="configList" v-if="appBlock != null">
                    <div class="title"></div>
                    <div class="listLine">
                        <div class="block">
                            <span></span>
                            <img :src="appBlock.picUrl"/>
                        </div>
                        <div class="block">
                            <span></span>
                            <span>{{appBlock.jumpUrl}}</span>
                        </div>
                        <div class="block">
                            <span></span>
                            <span>{{appBlock.sortNumber}}</span>
                        </div>
                        <div class="block">
                            <el-button type="danger" v-if="appBlock.status == 1"></el-button>
                            <el-button type="primary" v-if="appBlock.status == 2"></el-button>
                        </div>
                    </div>
                </div>
                <!--  -->
                <div class="configList" v-if="pcBlock != null">
                    <div class="title"></div>
                    <div class="listLine">
                        <div class="block">
                            <span></span>
                            <img :src="pcBlock.picUrl"/>
                        </div>
                        <div class="block">
                             <span></span>
                            <span>{{pcBlock.jumpUrl}}</span>
                        </div>
                        <div class="block">
                            <span></span>
                            <span>{{pcBlock.sortNumber}}</span>
                        </div>
                        <div class="block">
                            <el-button type="danger" v-if="pcBlock.status == 1"></el-button>
                            <el-button type="primary" v-if="pcBlock.status == 2"></el-button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  data() {
    return {
      form: {},
      formLabelWidth: "120px",
      areaName: "", // 
      appBlock: null,
      pcBlock: null
    }
  },
  created() {
    this.form = JSON.parse(sessionStorage.getItem("MattersConfigItem"))
    this.form.createTime = this.util.formatDate(new Date(this.form.createTime), "yyyy-MM-dd")
    console.log("", this.form)
    this.getAreaDetail()
  },
  methods: {
    // 
    getAreaDetail() {
      let submitForm = {
        areaId: this.form.areaId,
        mattersId: this.form.id
      }
      this.$post("/ybapp/siteMattersArea/getAreaDetail", submitForm)
        .then(resData => {
          console.log(resData)
          if (resData.success) {
            this.areaName = resData.result.areaName
            if (resData.result.appBlock != null) {
              this.appBlock = resData.result.appBlock
            }
            if (resData.result.pcBlock != null) {
              this.pcBlock = resData.result.pcBlock
            }
          } else {
          }
        })
    }
  }
}
</script>

<style lang="less" scoped>
.mattersDetail{
    width: 100%;
    .mattersTitle{
        color: black;
        font-size: 30px;
        font-weight: 700;
    }
    // 
    .baseInfo{
        margin-top: 15px;
        height: auto;
        .infoTitle{
            line-height: 30px;
            padding-left: 15px;
            border-left: 2px solid #00AAEE;
        }
    }
    // 
    .configInfo{
        margin-top: 15px;
        height: auto;
        .infoTitle{
            line-height: 30px;
            padding-left: 15px;
            border-left: 2px solid #00AAEE;
        }
        .configContent{
            .configList{
                .title{
                    font-size: 15px;
                    color: #999;
                    margin-top: 10px;
                }
                .listLine{
                    margin-top: 10px;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    .block{
                        display: flex;
                        align-items: center;
                        img{
                            height: 100px;
                            width: 100px;
                            margin-left: 20px;
                        }
                    }
                }
            }
        }
    }
}
</style>



