<template>
  <div>
    <el-container>
      <el-header class="title">
        <h1></h1>
        <div>
          <el-dropdown class="login" @command="userOut">
            <span class="el-dropdown-link">
              {{loginName}}
              <i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <!-- <el-dropdown-item></el-dropdown-item> -->
              <el-dropdown-item style="padding:0 20px;" command="loginOut"></el-dropdown-item>
              <el-dropdown-item style="padding:0 20px;" command="changePassword"></el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <el-dropdown class="upload">
            <span class="el-dropdown-link">
              
              <i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <!-- <el-dropdown-item></el-dropdown-item> -->
              <el-dropdown-item>
                <ep-upload
                  text=""
                  upLoadUrl="/test/importUser"
                >
                </ep-upload>
              </el-dropdown-item>
              <el-dropdown-item>
                <ep-upload
                  text=""
                  upLoadUrl="/test/importRole"
                >
                </ep-upload>
              </el-dropdown-item>
              <el-dropdown-item>
                <ep-upload
                  text=""
                  upLoadUrl="/test/importorg"
                >
                </ep-upload>
              </el-dropdown-item>
              <el-dropdown-item>
                <ep-upload
                  text=""
                  upLoadUrl="/test/importCheck"
                >
                </ep-upload>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </el-header>
      <el-dialog title="" :visible.sync="dialogFormVisible" @close="handleClose">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="110px">
          <el-form-item label="" prop="oldPasswd">
            <el-input v-model="ruleForm.oldPasswd" :type="ruleForm.inputType" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="newPasswd">
            <el-input v-model="ruleForm.newPasswd" :type="ruleForm.inputType" placeholder="" @blur="newPasswd()"></el-input>
            <span class="points" v-if="ruleForm.showPoints">6</span>
          </el-form-item>
          <el-form-item label="" prop="confirmNewPasswd">
            <el-input v-model="ruleForm.confirmNewPasswd" :type="ruleForm.inputType" placeholder=""></el-input>
          </el-form-item>
          <el-form-item prop="showPasswd">
            <el-radio v-model="ruleForm.showPasswd" label="1" @change="getValue" class="show-passwd"></el-radio>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="updatePasswd"> </el-button>
        </div>
        <div class="input-again" v-if="ruleForm.incorrectPasswd">!</div>
      </el-dialog>
      <el-container>
        <el-aside width="200px" style="height: 100%;">
          <menu-bar style="height: 100%;overflow-y: auto;"></menu-bar>
        </el-aside>
        <el-main class="main">
          <Main></Main>
        </el-main>
      </el-container>
      <!-- <el-footer style="text-align:right;margin-top:20px;">
        V1.4
      </el-footer> -->
    </el-container>
  </div>
</template>

<script>
import MenuBar from "@/components/common/MenuBar"
import Main from "./Main.vue"
export default {
  data() {
    const validatePasswd = (rule, value, callback) => {
      let reg1 = /^(?:\d+|[a-zA-Z]+|[!@#$%^&*]+)$/
      let reg2 = /^(?![a-zA-z]+$)(?!\d+$)(?![!@#$%^&*]+$)[a-zA-Z\d!@#$%^&*]+$/
      let reg3 = /^(?![a-zA-z]+$)(?!\d+$)(?![!@#$%^&*]+$)(?![a-zA-z\d]+$)(?![a-zA-z!@#$%^&*]+$)(?![\d!@#$%^&*]+$)[a-zA-Z\d!@#$%^&*]+$/
      let judge = true
      if (value.length >= 6) {
        if (reg1.test(value) || reg2.test(value) || reg3.test(value)) {
          judge = false
        }
      }
      if (judge) {
        callback(new Error("6"))
      } else {
        callback()
      }
    }
    const validateNewAndOldPasswd = (rule, value, callback) => {
      let judge = false
      if ((value.length >= 6) && (this.ruleForm.oldPasswd.length >= 6)) {
        if (value == this.ruleForm.oldPasswd) {
          judge = true
        }
      }
      if (judge) {
        callback(new Error(""))
      } else {
        callback()
      }
    }
    const validateNewPasswd = (rule, value, callback) => {
      let judge = true
      if ((value.length >= 6) && (this.ruleForm.newPasswd.length >= 6)) {
        if (value == this.ruleForm.newPasswd) {
          judge = false
        }
      }
      if (judge) {
        callback(new Error(""))
      } else {
        callback()
      }
    }
    return {
      dialogFormVisible: false,
      ruleForm: {
        oldPasswd: "",
        newPasswd: "",
        confirmNewPasswd: "",
        showPasswd: "",
        inputType: "password",
        showPoints: true,
        incorrectPasswd: false
      },
      rules: {
        oldPasswd: [
          { required: true, message: "6", trigger: "blur" },
          { validator: validatePasswd, trigger: "blur"}
        ],
        newPasswd: [
          { required: true, message: "6", trigger: "blur" },
          { validator: validatePasswd, trigger: "blur"},
          { validator: validateNewAndOldPasswd, trigger: "blur"}
        ],
        confirmNewPasswd: [
          { required: true, message: "6", trigger: "blur" },
          { validator: validatePasswd, trigger: "blur"},
          { validator: validateNewPasswd, trigger: "blur"}
        ]
      }
    }
  },
  components: {
    MenuBar,
    Main
  },
  computed: {
    loginName() {
      return this.$Base64.decode(localStorage.getItem("uesr")) || ""
    }
  },
  methods: {
    setCookie(cname,cvalue,exdays){
      var d = new Date();
      d.setTime(d.getTime()+(exdays*24*60*60*1000));
      var expires = "expires="+d.toGMTString();
      document.cookie = cname+"="+cvalue+"; "+expires;
    },
    userOut(command) {
      if (command == "loginOut") {
        this.$get("/login", {
          cmd: "logout"
        }).then(data => {
          if (data.data.success) {
            this.setCookie('l', "", -1);
            this.setCookie('JSESSIONID', "", -1);
            this.setCookie('cat', "", -1);
            this.setCookie('ol', "", -1);

            localStorage.removeItem("uesr")
            localStorage.removeItem("userType")
            this.$router.push("/login")

          }
          this.$mess(data.data)
        })
      }
      if (command == "changePassword") {
        this.dialogFormVisible = true
      }
    },
    upload(command) {

    },
    handleClose() {
      this.ruleForm = {
        oldPasswd: "",
        newPasswd: "",
        confirmNewPasswd: "",
        showPasswd: "",
        inputType: "password",
        showPoints: true,
        incorrectPasswd: false,
      }
      this.$refs["ruleForm"].resetFields()
    },
    updatePasswd() {
      this.$refs["ruleForm"].validate(valid => {
        if (valid) {
          this.$get("/sys/user/updatePasswd", {
            oldPasswd: this.ruleForm.oldPasswd,
            newPasswd: this.ruleForm.newPasswd
          }).then(data => {
            if (data.data.result.success) {
              this.$router.push("/login")
              this.$message({
                type: "success",
                message: data.data.result.resultDes
              })
            } else {
              this.ruleForm.incorrectPasswd = true
              setTimeout(() => {
                this.ruleForm.incorrectPasswd = false
              }, 2000)
            }
          })
        } else {
          console.log("error submit!!");
          return false;
        }
      })
    },
    newPasswd() {
      this.ruleForm.showPoints = false
    },
    getValue(val) {
      if (this.ruleForm.showPasswd) {
        this.ruleForm.inputType = "text"
      }
    }
  }
}
</script>

<style scoped lang="css">
.title {
  background-color: #f2f2f2;
  margin-bottom: 20px;
  height: 57px !important;
  background: url(../assets/img/homebg.jpg) no-repeat center center;
  background-size: 100% 80px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.login {
  margin-right: 40px;
}
.upload {
  margin-right: 80px;
}
.title h1 {
  font-size: 26px;
  font-weight: bold;
  line-height: 1;
  line-height: 80px;
  margin-left: 80px;
  color: #fff;
  letter-spacing: 10px;
}
.main {
  border: 1px solid #ccc;
  min-height: 400px;
  /* padding-left: 0; */
}
.el-dropdown-link {
  cursor: pointer;
  color: #fff;
}
.el-icon-arrow-down {
  font-size: 12px;
}
/deep/ .el-tabs{
  min-width: 200px;
}
/deep/ .el-dropdown-menu__item {
  padding: 0;
}
.points {
  position: absolute;
  top: 100%;
  left: 0;
  padding-top: 4px;
  color: #ccc;
  font-size: 12px;
  line-height: 1;
}
.show-passwd {
  position: absolute;
  right: 0;
}
.input-again {
  position: absolute;
  width: 60%;
  height: 52px;
  top: 50%;
  left: 50%;
  margin-top: -56px;
  margin-left: -30%;
  border-radius: 10px;
  color: #fff;
  font-size: 16px;
  line-height: 52px;
  background: rgba(0,0,0,.3);
  text-align: center;
}
</style>



