<template>
  <div id="app">
    <el-form
      :inline="true"
      size="mini"
      :model="searchForm"
      class="demo-form-inline searchFrom"
      label-width="90px">
      <el-row>
        <el-col :span="5">
          <el-form-item label="/:" label-width="120px">
            <el-input v-model="searchForm.methodName" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="/:" label-width="140px">
            <el-input v-model="searchForm.keyWord" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="4">
          <el-form-item label="URL:" label-width="40px">
            <el-input v-model="searchForm.url" placeholder="URL"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="5">
          <el-form-item label=":" label-width="90px">
            <el-input v-model="searchForm.operatorName" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label=":" label-width="120px">
            <!-- <span class="funName"></span> -->
            <el-date-picker
              style="width:300px;"
              v-model="datePicker"
              type="daterange"
              align="right"
              unlink-panels
              range-separator=""
              start-placeholder=""
              end-placeholder=""
              :picker-options="pickerOptions"
              value-format="yyyy-MM-dd"
              size="mini"
            ></el-date-picker>
          </el-form-item>
        </el-col>
        <el-col :span="2">
          <el-form-item>
            <el-button type="primary" @click="onSearch"></el-button>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>


    <el-table :data="tableData" :height="tableHeight" style="width: 100%">
      <el-table-column width="120" align="center" prop="systemName" label="" fixed></el-table-column>
      <el-table-column width="140" align="center" prop="methodName" label="" fixed></el-table-column>
      <el-table-column width="140" align="center" prop="methodChName" label="" fixed></el-table-column>
      <el-table-column width="100" align="center" prop="operatorName" label="" fixed></el-table-column>
      <el-table-column width="160" align="center" prop="createTime" label="">
        <template slot-scope="scope">
            <span >{{scope.row.createTime | reTime}}</span>
        </template>
      </el-table-column>
      <el-table-column width="110" align="center" prop="operatorIp" label="IP"></el-table-column>
      <el-table-column width="150" align="center" prop="url" label="URL"></el-table-column>
      <el-table-column align="left" prop="operateDetailJson" label="">
        <template slot-scope="scope">
          <el-tooltip class="tooltip" effect="dark" content="" placement="top">
            <span class="copyTarget" @click="handleCopy(scope.row.operateDetailJson, scope.$index)">{{scope.row.operateDetailJson | cutOut50}}</span>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column align="left" prop="returnValue" label="">
        <template slot-scope="scope">
          <el-tooltip class="tooltip" effect="dark" content="" placement="top">
            <span class="copyTarget" @click="handleCopy(scope.row.returnValue, scope.$index)">{{scope.row.returnValue | cutOut50}}</span>
          </el-tooltip>
        </template>
      </el-table-column>
    </el-table>
    <ep-page :total="totalCount" @page="pageInit"></ep-page>
  </div>
</template>

<script>
/**
  *
     home/methodsOperationLog
    
 */
export default {
  data() {
    return {
      searchForm: {   //  
        methodName: "", // 
        keyWord: "", // /
        url: "", //   url
        operatorName: "", // 
        createTimeStart: "", // 
        createTimeEnd: "", // 
      },
      datePicker: [],   // 
      pickerOptions: {   //  - 
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit("pick", [start, end])
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
              picker.$emit("pick", [start, end])
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
              picker.$emit("pick", [start, end])
            }
          }
        ]
      },   //  

      tableData: [],    // table  
      pageNum: 1,
      pageSize: 10,
      totalCount: 0,    // table 
    }
  },
  created() {
    // 
    this.reTableView()
  },
  computed: {
    tableHeight() {
      return window.innerHeight - 230
    }
  },
  methods: {
    // dataPickerChange(){
    //   console.log(1111);
    // },
    handleCopy(str,index){
      const _input = document.createElement("input");   // input
      _input.value = str;  // 
      document.body.appendChild(_input);    // 
      _input.select();   // 
      document.execCommand("Copy");   // 
      document.body.removeChild(_input); // 

      this.$message({
        type:'success',
        message:''
      })
    },
    reTableView(pageNum, pageSize) {

      // 
      let params = {}
      for( let i in this.searchForm){
        if(this.searchForm[i]){
          params[i] = this.searchForm[i]
        }
      }
      // console.log('params::',params);
      this.$post("/sysInnerOperateLog/list", {
        pageNum: pageNum || this.pageNum,
        pageSize: pageSize || this.pageSize,
        ...params,
      }).then(data => {
        this.tableData = data.result.dataList
        this.totalCount = data.result.totalCount
      })
    },
    pageInit(pageNum, pageSize) {
      this.pageNum = pageNum
      this.pageSize = pageSize
      this.reTableView()
    },
    onSearch() {
      this.pageNum = 1
      this.pageSize = 10
      this.reTableView()
    }
  },
  watch: {
    datePicker: {
      deep: true,
      immediate: true,
      handler(val) {
        // console.log('datePicker::',val);
        if (val && val.length!==0) {
          this.searchForm.createTimeStart = val[0] + " 00:00:00"
          this.searchForm.createTimeEnd = val[1] + " 23:59:59"
        } else {
          this.searchForm.createTimeStart = ""
          this.searchForm.createTimeEnd = ""
        }
      }
    }
  },
  filters: {
    reTime: function (value) {
      function padLeftZero(str) {
        return ("00" + str).substr(str.length)
      }
      function formatDate(date, fmt) {
        if (/(y+)/.test(fmt)) {
          fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length))
        }
        let o = {
          "M+": date.getMonth() + 1,
          "d+": date.getDate(),
          "h+": date.getHours(),
          "m+": date.getMinutes(),
          "s+": date.getSeconds()
        }
        for (let k in o) {
          if (new RegExp(`(${k})`).test(fmt)) {
            let str = o[k] + ""
            fmt = fmt.replace(RegExp.$1, RegExp.$1.length === 1 ? str : padLeftZero(str))
          }
        }
        return fmt
      }
      if (!value) return ''
      return formatDate(new Date(value), "yyyy-MM-dd hh:mm:ss")
    },
    cutOut50(val){
      if(!val){return ''}
      if(val.length > 130){
        return val.substr(0,130) + '...'
      }else{
        return val
      }
    }
  }
}
</script>

<style scoped lang="css">
.copyTarget{
  padding: 5px;
  border-radius: 5px;
transition: all .3s;
}
.copyTarget:hover{ background-color: #fff; }
.copyTarget:active{ background-color: rgb(0, 148, 20); color: #fff;}
</style>



