<template>
  <div id="app">
    <div class="wrap">
      <h1 class="title"></h1>
      <el-form
        :rules="rules"
        ref="formLabelAlign"
        label-position="top"
        label-width="200px"
        :model="formLabelAlign"
      >
        <el-form-item prop="userName">
          <el-input style="width:300px;" placeholder="" v-model="formLabelAlign.userName">
            <i slot="prefix" class="iconfont icon-yonghu"></i>
          </el-input>
        </el-form-item>
        <el-form-item prop="passWord">
          <el-input show-password placeholder="" v-model="formLabelAlign.passWord">
            <i slot="prefix" class="iconfont icon-ziyuanxhdpi"></i>
          </el-input>
        </el-form-item>

        <a href="#" class="resetPass"></a>
        <el-form-item>
          <el-button
            class="loginButton"
            type="primary"
            @click="submitForm('formLabelAlign')"
            @keyup.enter.native="submitForm('formLabelAlign')"
          ></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      formLabelAlign: {
        userName: "",
        passWord: "",
        yzm: ""
      },
      rules: {
        userName: [
          { required: true, message: "", trigger: "blur" }
        ],
        passWord: [{ required: true, message: "", trigger: "blur" }],
        yzm: [{ required: true, message: "", trigger: "blur" }]
      }
    }
  },
  created() {
    var _self = this
    document.onkeydown = function(e) {
      let key = e.keyCode
      if (window.event == undefined) {
        key = e.keyCode
      } else {
        key = window.event.keyCode
      }
      if (key == 13) {
        _self.submitForm("formLabelAlign")
      }
    }
  },

  mounted() {},
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          var key = this.$crypto.enc.Utf8.parse("epsoftep400-8671") // 
          var iv = this.$crypto.enc.Utf8.parse("epsoftep400-8671") // 
          var ciphertext = this.$crypto.AES.encrypt(this.formLabelAlign.passWord, key, {
            iv: iv,
            mode: this.$crypto.mode.CBC, // 
            padding: this.$crypto.pad.ZeroPadding // 
          }).toString()
          
          this.$get("/doLogin", {
            loginName: this.formLabelAlign.userName,
            passWord: ciphertext
          }).then(data => {
            if (data.data.success) {
              this.$router.push({
                path: "/home"
              })
              let logonName = this.$Base64.encode(data.data.result.logonName)
              let userType = this.$Base64.encode(data.data.result.userType)
              localStorage.setItem("uesr", logonName)
              localStorage.setItem("userType", userType)
              document.onkeydown = undefined
            }
            this.$mess(data.data)
            this.formLabelAlign.zym = ""
          })
        } else {
          return false
        }
      })
    }
  }
}
</script>

<style scoped lang="css">
#app {
  background: url(../assets/img/indexBg.jpg) no-repeat center;
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
#app * {
  box-sizing: border-box;
}
.wrap {
  width: 562px;
  height: 450px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* margin-top: 100px; */
  background-color: #fff;
}
h1.title {
  font-size: 30px;
  margin-bottom: 58px;
  margin-top: 65px;
  color: #666;
}
.resetPass {
  float: right;
  font-size: 14px;
  margin-top: -6px;
  color: #1492ff;
}
.loginButton {
  width: 390px;
  border: none;
  background: url(../assets/img/button.png) no-repeat center !important;
  font-size: 18px;
  position: absolute;
  left: 0;
  right: 0;
  margin: 50px auto 0;
}
/deep/ .el-input__inner {
  /* border: none; */
  border-radius: 20px;
  background-color: #fff;
  padding-left: 45px;
  width: 390px;
}
.LoginInput /deep/ .el-input__inner {
  padding-left: 0px;
  background: url(../assets/img/userName.png) no-repeat 8px center !important;
  z-index: 999;
}
.LoginInputPass /deep/ .el-input__inner {
  padding-left: 50px;
  background: url(../assets/img/passWord.png) no-repeat 8px center !important;
  z-index: 999;
}
/deep/ .el-input__prefix {
  padding-left: 20px;
  /*  */
}
/deep/ .el-input__inner:focus {
  box-shadow: 0 0 4px 1px #12beff;
}
/deep/ .el-input__inner:focus + .el-input__prefix .iconfont{
  color: #12beff;
}
</style>



