<template>
  <div class="configContent">
    <div class="configTitle"></div>
    <div class="addContent"  v-if="radio=='1'">
      <div class="photoBox" v-for="(item, index) in picArr" :key="index">
        <div class="photo">
          
          <img v-if="item.imgUrl" :src="item.imgUrl" />
        </div>
<!--
         <svg-icon icon-class="delete" @click="deleteContent(item,index)"></svg-icon>
-->
        <select @change="selectName($event,index)" v-model="item.text">
          <option value='' disabled selected style='display:none;'></option>
          <option v-for="(innerItem,index) in optionList" :key="index">{{innerItem}}</option>
        </select>
      </div>
      <div class="svgBox">
        <svg-icon @click="plusContent" icon-class="plusPhoto"></svg-icon>
        <span>{{num}}</span>
      </div>
    </div>

    <div class="addContent" v-if="radio=='2'">
      <div class="photoBox" v-for="(item, index) in picArr1" :key="index">
        <div class="photo">
          
          <img v-if="item.imgUrl" :src="item.imgUrl" />
        </div>
        <!-- <svg-icon icon-class="delete" @click="deleteContent(item,index)"></svg-icon> -->
        <select @change="selectName1($event,index)" v-model="item.text">
          <option value='' disabled selected style='display:none;'></option>
          <option v-for="(innerItem,index) in optionList1" :key="index">{{innerItem}}</option>
        </select>
      </div>
      <div class="svgBox">
        <svg-icon @click="plusContent1" icon-class="plusPhoto"></svg-icon>
        <span>{{num1}}</span>
      </div>
    </div>
    <el-radio-group v-model="radio">
      <el-radio  :label="1"></el-radio>
      <el-radio  :label="2"></el-radio>
    </el-radio-group>
    <div class="btnContent">
      <el-button type="primary" @click="submit"></el-button>
      <el-button @click="cancel"></el-button>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    matterList: {
      type: Array,
      default: []
    },
    unitContent: {
      type: Array,
      default: []
    },
    personContent: {
      type: Array,
      default: []
    }
  },
  data() {
    return {
      contentList: [], // 
      chooseIndex: 0, // 
      radio: 1, // 
      // unitContent: [], // 
      // personContent: [], // 
      List: [],
      statusType: "" // 
    }
  },
  computed: {
    num: {
      get() {
        return this.matterList.length > 7 ? (7 - this.unitContent.length) : (this.matterList.length - this.unitContent.length)
      },
      set(val) {
        return
      }
    },
    num1: {
      get() {
        return this.matterList.length > 7 ? (7 - this.personContent.length) : (this.matterList.length - this.personContent.length)
      },
      set(val) {
        return
      }
    },
    picList() {
      let arr = []
      this.matterList.forEach(ele => {
        let obj = new Object()
        obj.imgUrl = ele.picUrl
        obj.text = ele.mattersName
        arr.push(obj)
      })
      return arr
    },
    optionList() {
      let arr = []
      this.matterList.forEach(ele => {
        arr.push(ele.mattersName)
      })
      return arr
    },
    picArr() {
      let arr = []
      for (let i = 0; i < this.unitContent.length; i++) {
        let obj = new Object()
        obj.imgUrl = this.unitContent[i].imgUrl
        obj.text = this.unitContent[i].text
        arr.push(obj)
      }
      return arr
    },
    picList1() {
      let arr = []
      this.matterList.forEach(ele => {
        let obj = new Object()
        obj.imgUrl = ele.picUrl
        obj.text = ele.mattersName
        arr.push(obj)
      })
      return arr
    },
    optionList1() {
      let arr = []
      this.matterList.forEach(ele => {
        arr.push(ele.mattersName)
      })
      return arr
    },
    picArr1() {
      let arr = []
      for (let i = 0; i < this.personContent.length; i++) {
        let obj = new Object()
        obj.imgUrl = this.personContent[i].imgUrl
        obj.text = this.personContent[i].text
        arr.push(obj)
      }
      return arr
    }
  },
  methods: {
    plusContent() {
      if (this.num <= 0) {
        return false
      }
      let newPicArr = []
      for (let i = 0; i < this.picArr.length; i++) {
        let obj = {
          text: this.picArr[i].text,
          imgUrl: this.picArr[i].imgUrl
        }
        newPicArr.push(obj)
      }
      for (let i = 0; i < this.unitContent.length; i++) {
        this.unitContent.pop()
      }
      this.unitContent.pop()
      for (let i = 0; i < newPicArr.length; i++) {
        let obj = {
          text: newPicArr[i].text,
          imgUrl: newPicArr[i].imgUrl
        }
        this.unitContent.push(obj)
      }
      this.unitContent.push({text: "", imgUrl: ""})
      this.num--
      this.$emit("iconConfig", this.picArr)
    },
    plusContent1() {
      if (this.num1 <= 0) {
        return false
      }
      let newPicArr = []
      for (let i = 0; i < this.picArr1.length; i++) {
        let obj = {
          text: this.picArr1[i].text,
          imgUrl: this.picArr1[i].imgUrl
        }
        newPicArr.push(obj)
      }
      for (let i = 0; i < this.personContent.length; i++) {
        this.personContent.pop()
      }
      this.personContent.pop()
      for (let i = 0; i < newPicArr.length; i++) {
        let obj = {
          text: newPicArr[i].text,
          imgUrl: newPicArr[i].imgUrl
        }
        this.personContent.push(obj)
      }
      this.personContent.push({imgUrl: "", text: ""})
      this.num1--
      this.$emit("iconConfig", this.picArr1)
    },
    // 
    /* deleteContent(item,index){
        console.log("item", item);
        console.log("index", index);
        this.picArr.splice(index, 1);
        this.picArr.forEach(ele=>{
          if (item.text == ele.text){

          }
        })
        this.num++;
        console.log('',this.picArr);
        this.$emit('iconConfig',this.picArr);
      }, */
    // 
    submit() {
      let submitForm = {siteHomeConfigList: []}
      let sortNumber = 1
      if (this.radio == "1") {
        this.statusType = 1 // 
        this.List = this.picArr
        console.log("list:", this.List)
      } else if (this.radio == "2") {
        this.statusType = 2 // 
        this.List = this.picArr1
      }
      this.List.forEach(ele => {
        let obj = new Object()
        obj.sortNumber = sortNumber // 
        sortNumber++
        console.log("list:", this.List)
        console.log("radio", this.radio)
        this.matterList.forEach(innerEle => { // 
          if (innerEle.mattersName == ele.text) {
            obj.areaId = innerEle.areaId
            obj.blockAppId = innerEle.blockAppId
            obj.mattersId = innerEle.mattersId
            obj.statusType = this.statusType
            // obj.imgUrl = innerEle.picUrl;
          }
        })
        submitForm.siteHomeConfigList.push(obj)
      })
      console.log(submitForm)
      this.$post("/ybapp/home/saveOrupdateHome", submitForm).then((resData) => {
        console.log(resData)
        if (resData.success) {
          this.$message({
            type: "success",
            message: ""
          })
        }
      }).catch((error) => {
        console.log(error)
      })
    },
    // 
    cancel() {
      this.matterList = []
      this.$emit("iconConfig", this.matterList)
    },
    selectName(event, index) {
      for (let i = 0; i < this.picArr.length; i++) {
        if (i == index) {
          if (event.target.value == this.picArr[i].text) {
            continue
          }
        }
        if (event.target.value == this.picArr[i].text) {
          this.picArr[i].text = ""
          this.picArr[i].imgUrl = ""
          this.$forceUpdate() // 
        }
      }
      this.picArr[index].text = event.target.value // 
      this.picList.forEach(ele => {
        if (event.target.value == ele.text) {
          this.picArr[index].imgUrl = ele.imgUrl // 
        }
      })
      console.log("picList", this.picList)
      this.$forceUpdate() // 
      this.$emit("iconConfig", this.picArr)
    },
    selectName1(event, index) {
      for (let i = 0; i < this.picArr1.length; i++) {
        if (i == index) {
          if (event.target.value == this.picArr1[i].text) {
            continue
          }
        }
        if (event.target.value == this.picArr1[i].text) {
          this.picArr1[i].text = ""
          this.picArr1[i].imgUrl = ""
          this.$forceUpdate() // 
        }
      }
      this.picArr1[index].text = event.target.value // 
      this.picList1.forEach(ele => {
        if (event.target.value == ele.text) {
          this.picArr1[index].imgUrl = ele.imgUrl // 
        }
      })
      console.log("picList1", this.picList1)
      this.$forceUpdate() // 
      this.$emit("iconConfig", this.picArr1)
    }
  }
}
</script>

<style lang="less" scoped>
  .configContent{
    width: 600px;
    .configTitle{
      font-size: 18px;
      font-weight: bold;
    }
    .addContent{
      margin-top: 20px;
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 50px;
      .photoBox{
        height: 140px;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        margin-right: 20px;
        margin-top: 20px;
        .photo{
          height: 100px;
          width: 100px;
          background: #EEE;
          text-align: center;
          line-height: 100px;
          color: #999;
          cursor: pointer;
          position: relative;
          img{
            position: absolute;
            height: 100px;
            width: 100px;
            left: 0;
            top: 0;
          }
        }
        .svg-icon{
          height: 20px;
          width: 20px;
          position: absolute;
          top: -5px;
          right: 0;
          cursor: pointer;
        }
        select{
          height: 25px;
          width: 120px;
          border: 1px solid #DDD;
          background: #FFF;
        }
      }
      .svgBox{
        height: 140px;
        width: 120px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        margin-right: 20px;
        margin-top: 20px;
        .svg-icon{
          height: 100px;
          width: 100px;
          cursor: pointer;
        }
        span{
          width: 100px;
          text-align: center;
          display: block;
          font-size: 14px;
          color: #999;
        }
      }
    }
    .btnContent{
      margin-top: 20px;
    }
  }
</style>



