<template>
  <div class="sidebar-container">
    <el-scrollbar wrap-class="scrollbar-wrapper">
      <el-menu @select="menuSelect" mode="vertical" :default-active="defaultActive">
        <!-- <sidebar-item v-for="menu in menuList" :key="menu.id" :item="menu" /> -->
        <div v-for="(item,index) in menuList" :key="index">
          <el-submenu :index="String(item.id)" v-if="item.nodeType==1">
            <template slot="title">
              <span>{{item.title}}</span>
            </template>
            <el-menu-item v-for="(menu,index) in item.children" :key="index" :index="'/home'+menu.location+'?is='+isQuery(menu.systemType)">{{menu.title}}</el-menu-item>
          </el-submenu>
          <el-menu-item v-else :index="'/home'+item.location">
            <span slot="title">{{item.title}}</span>
          </el-menu-item>
        </div>
      </el-menu>
    </el-scrollbar>
  </div>
</template>
<script>

import SidebarItem from "./SidebarItem"
export default {
  name: "Sidebar",
  components: { SidebarItem },
  props: {
    menuList: {
      type: Array,
      required: true
    }
  },
  computed: {
    defaultActive() {
      return this.$route.path
    }
  },
  methods: {
    isQuery(val) {
      if (val == 1) {
        return "a0b923820dcc509a"
      }
      if (val == 2) {
        return "9d4c2f636f067f89"
      }
      if (val == 3) {
        return "4b5ce2fe28308fd9"
      }
      if (val == 4) {
        return "a2f3e71d9181a67b"
      }
      if (val == 5) {
        return "bbce2345d7772b06"
      }
      if (val == 6) {
        return "5a880faf6fb5e608"
      }
      if (val == 7) {
        return "ceea167a5a36dedd"
      }
      if (val == 8) {
        return "fb98ab9159f51fd0"
      }
      if (val == 9) {
        return "2e2d7fbdea1afc51"
      }
      // 
      if (val == 10) {
        return "02a44259755d38e6"
      }
      // 
      if (val == 11) {
        return "d9caa6e02c990b0a"
      }
    },
    menuSelect(val) {
      // console.log(val)
      this.$router.push({path: val})
    }
  }
}
</script>
<style lang="css" scoped>
.sidebar-container{
  height: calc(100vh - 100px);
  overflow: auto;
}
</style>



