<template>
  <div id="app">
    <el-form ref="searchForm" :model="searchForm">
      <el-row :gutter="20">
        <el-col :span="3">
          <el-form-item>
            <el-cascader
              clearable
              expand-trigger="hover"
              :options="cascaderOptions"
              v-model="cascaderVal"
              @change="handleChange"
              size="mini"
              :props="cascaderProps"
              :show-all-levels="false"
              filterable
            ></el-cascader>
          </el-form-item>
        </el-col>

        <el-col :span="4">
          <el-form-item>
            <el-date-picker
              style="width:100%;"
              v-model="datePicker"
              type="daterange"
              align="right"
              unlink-panels
              range-separator=""
              start-placeholder=""
              end-placeholder=""
              :picker-options="pickerOptions"
              value-format="yyyy-MM-dd"
              size="mini"
            ></el-date-picker>
          </el-form-item>
        </el-col>
        <ep-input
          @input-message="onChangeSearchForm"
          colspan="3"
          :value="searchForm.functionTitle"
          placeholder=""
          :isIcon="true"
          size="mini"
          name="functionTitle"
        ></ep-input>
        <ep-input
          @input-message="onChangeSearchForm"
          colspan="4"
          :value="searchForm.description"
          placeholder=""
          :isIcon="true"
          size="mini"
          name="description"
        ></ep-input>

        <ep-select
          @select-message="onChangeSearchForm"
          colspan="4"
          :value="searchForm.operateUserId"
          placeholder=""
          :isIcon="true"
          size="mini"
          name="operateUserId"
          :options="optionsUserId"
          selectLable="displayName"
          selectKey="id"
        ></ep-select>
        <ep-select
          @select-message="onChangeSearchForm"
          colspan="4"
          :value="searchForm.rollbackFlag"
          placeholder=""
          :isIcon="true"
          size="mini"
          name="rollbackFlag"
        ></ep-select>
        <el-col :span="2" :offset="0">
          <el-form-item>
            <el-button @click="onSearch" type="primary" size="mini"></el-button>
            <!-- <el-button type="primary" size="mini" @click="addDialog"></el-button> -->
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>

    <el-table :data="tableData" :height="tableHeight" style="width: 100%">
      <el-table-column align="center" width="80" fixed>
        <template slot-scope="scope">
          <span style="display:inline-block;">{{scope.$index + 1}}</span>
          <span v-show="scope.row.rollbackFlag==0?false:true" class="black"></span>
        </template>
      </el-table-column>
      <el-table-column width="100" align="center" prop="id" label="" fixed></el-table-column>
      <el-table-column width="140" align="center" prop="functionTitle" label="" fixed></el-table-column>
      <el-table-column width="100" align="center" prop="areaId" label=""></el-table-column>
      <el-table-column width="150" align="center" prop="operateUserName" label=""></el-table-column>
      <el-table-column width="150" align="center" prop="createTime" label=""></el-table-column>
      <el-table-column width="400" align="center" prop="description" label=""></el-table-column>
      <el-table-column width="150" align="center" prop="rollbackUserName" label=""></el-table-column>
      <el-table-column width="150" align="center" prop="rollbackTime" label=""></el-table-column>
      <!-- <el-table-column align="center" label="">
        <template slot-scope="scope">
          <span>{{scope.row.effectiveSign | effectiveSign}}</span>
        </template>
      </el-table-column>-->

      <el-table-column width="140" align="center" label="" fixed="right">
        <template slot-scope="scope">
          <el-button @click="seeClick(scope.row)" type="text" size="small"></el-button>
          <el-button
            v-show="scope.row.rollbackFlag==0 && scope.row.rbFlag == '1'?true:false"
            @click="backClick(scope.row)"
            type="text"
            size="small"
          ></el-button>
          <el-button
            v-show="scope.row.rollbackFlag==0?true:false"
            @click="printClick(scope.row)"
            type="text"
            size="small"
          ></el-button>
          <!-- <ep-delete
            v-show="scope.row.maintainSign==0?false:true"
            :id="scope.row.id"
            parmeId="id"
            url="/sys/code/deleteCode"
            :init="true"
            @init="deleteInit"
          >
            <el-button type="text" size="small"></el-button>
          </ep-delete>-->
        </template>
      </el-table-column>
    </el-table>
    <ep-page :total="total" @page="pageInit"></ep-page>
  </div>
</template>

<script>
/**
  *
    home/sys/operateLog/queryTable
    sys/operateLog
    id id
    createTime		
    startCreateTime		
    endCreateTime		
    functionTitle		
    operateUserId		
    description		
    rollbackFlag		
    page		
    size		
 */
export default {
  data() {
    return {
      dateValue: "",
      dialogFormVisible: false,
      total: "",
      daterangeFrom: {
        daterangeValue: ""
      },
      optionsUserId: [],
      cascaderProps: {
        value: "id",
        label: "orgName",
        checkStrictly: true
      },
      cascaderVal: [],
      cascaderOptions: [],
      datePicker: [],
      tableData: [],
      diaLogrules: {
        codeType: this.$rules(""),
        codeValue: this.$rules(""),
        codeName: this.$rules(""),
        effectiveSign: this.$rules(""),
        maintainSign: this.$rules("")
      },
      pickerOptions: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit("pick", [start, end])
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
              picker.$emit("pick", [start, end])
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
              picker.$emit("pick", [start, end])
            }
          }
        ]
      },
      searchForm: {
        functionTitle: "",
        description: "",
        operateUserId: "",
        rollbackFlag: "",
        startCreateTime: "",
        endCreateTime: "",
        orgId: ""
      },
      diaLogForm: {
        codeType: "",
        codeName: "",
        codeValue: "",
        parameterSort: "",
        effectiveSign: "1",
        maintainSign: "1"
      },
      page: 1,
      size: 10,
      id: ""
    }
  },
  created() {
    // 
    this.$get("/sys/operateLog/getOrgTreeNodes", {
      userId: 1
    }).then(data => {
      console.log(data)
      if (data.data.success) {
        this.cascaderOptions = data.data.result.orgList
        // this.cascaderVal = data.data.result.idpath.split("/")
        this.init(this.page, this.size)
      }
    })
  },
  computed: {
    tableHeight() {
      return window.innerHeight - 230
    }
  },
  methods: {
    init(page, size) {
      console.log(this.cascaderVal, 123123)
      let orgId = this.cascaderVal.length ? this.cascaderVal[this.cascaderVal.length - 1] : ""
      this.$get("/sys/operateLog/queryLogList", {
        page: page,
        size: size,
        ...this.searchForm,
        orgId: orgId
      }).then(data => {
        this.tableData = data.data.pageList.result
        this.total = data.data.count
      })
    },
    getSysUserList() {
      let orgId = this.cascaderVal.length ? this.cascaderVal[this.cascaderVal.length - 1] : ""
      // 
      this.$get("/sys/operateLog/getSysUserList", {
        orgId: orgId
      }).then(data => {
        console.log(data)
        if (data.data.success) {
          this.optionsUserId = data.data.result
        }
      })
    },
    deleteInit() {
      this.init(this.page, this.size)
    },
    seeClick(row) {
      window.open(this.$api + "/snapshot/download?logId=" + row.id)
    },
    backClick(row) {
      this.$get("/sys/operateLog/doRollback", {
        logId: row.id
      }).then(data => {
        if (data.data.success) {
          this.init(this.page, this.size)
        }
        if (data.data.success) {
          this.$message({
            type: "success",
            message: data.data.resultDes
          })
        }
        if (!data.data.success) {
          this.$message({
            type: "error",
            message: data.data.resultDes
          })
        }
      })
    },
    printClick(row) {
      let newWindow = window.open()
      this.$get("/sys/operateLog/printReport", {
        logId: row.id
      }).then(res => {
        if (res.data.success) {
          newWindow.location.href = res.data.result
        }
      })
    },
    pageInit(page, size) {
      this.page = page
      this.size = size
      this.init(page, size)
    },
    onChangeDiaLogForm(data, type) {
      console.log(data, type)

      this.diaLogForm[type] = data
    },
    onChangeSearchForm(data, type) {
      console.log(data)
      console.log(this.searchForm)

      this.searchForm[type] = data
      console.log(data)
    },
    addDialog() {
      this.dialogFormVisible = true
      this.id = ""
      this.diaLogForm = {
        codeType: "",
        codeName: "",
        codeValue: "",
        parameterSort: "",
        effectiveSign: "1",
        maintainSign: "1"
      }
    },
    saveDialogForm(formName) {
      this.$refs[formName].validate(valid => {
        console.log(this.diaLogForm)

        if (valid) {
          this.$post("/sys/code/saveCode", {
            id: this.id,
            ...this.diaLogForm
          }).then(data => {
            this.$mess(data)
            if (data.success) {
              this.init(this.page, this.size)
              this.dialogFormVisible = false
              this.diaLogForm = {
                codeType: data.codeType,
                codeName: data.codeName,
                codeValue: data.codeValue,
                parameterSort: data.parameterSort,
                effectiveSign: data.effectiveSign,
                maintainSign: data.maintainSign
              }
            }
          })
        } else {
          return false
        }
      })
    },
    onSearch() {
      this.page = 1
      this.init(this.page, this.size)
    },
    handleChange(val) {
      console.log(val)
    }
  },
  watch: {
    // getSysUserList  cascaderVal
    datePicker: {
      deep: true,
      immediate: true,
      handler(val) {
        console.log(val)

        if (val != null) {
          this.searchForm.startCreateTime = val[0]
          this.searchForm.endCreateTime = val[1] + "23:59:59"
        } else {
          this.searchForm.startCreateTime = ""
          this.searchForm.endCreateTime = ""
        }
      }
    },
    cascaderVal: {
      deep: true,
      immediate: true,
      handler(val) {
        this.searchForm.operateUserId = ""
        this.getSysUserList()
      }
    }
  }
}
</script>

<style scoped lang="css">
/deep/.el-dialog__body {
  display: flex;
  justify-content: center;
}
.page {
  display: flex;
  justify-content: flex-end;
  margin-top: 10px;
}
.black {
  display: inline-block;
  background-color: rgba(244, 148, 71, 1);
  font-weight: 400;
  color: #ffffff;
  padding: 3px;
  font-size: 12px;
}
/* .el-table{
  height: calc(100vh - 100px);
  overflow: auto;
} */
</style>



