<template>
    <div class="sideNav">
        <el-menu
        class="el-menu-vertical-demo"
        @open="handleOpen"
        @close="handleClose">
            <el-submenu
              :default-active="$route.path"
              :index=" index +'' "
              :key="index" v-for="(par,index) in parentList"
            >
                <template slot="title">
                <span>{{par.label}}</span>
                </template>
                <el-menu-item-group >
                    <el-menu-item  @click="handleSubOpen(item)" :index=" index +'' " :key="item.index" v-for="(item,index) in childList[index]">{{item.label}}</el-menu-item>
                </el-menu-item-group>
            </el-submenu>
        </el-menu>
    </div>
</template>

<script>
export default {
  data() {
    return {
      parentList: [],
      childList: [],
      childId: "",
      mattersList: [],
      iconList: []
    }
  },
  created() {
    this.getList()
  },
  methods: {
    getList() {
      let tree = {
        "bussType": 1,
        "pageNum": 1,
        "pageSize": 10
      }
      this.$post("/ybapp/siteArea/tree", tree).then((resData) => {
        if (resData.success) {
          console.log("", resData.result)
          let dataTree = resData.result.dataList
          for (var i = 0; i < dataTree.length; i++) {
            this.parentList = dataTree
            this.childList[i] = this.parentList[i].children
          }
          console.log(this.childList)
        }
      }).catch((error) => {
        console.log(error)
      })
    },
    getArea() {
      this.$post("/ybapp/siteMattersArea/selectMattersList", {
        "areaId": this.childId
      }
      ).then((resData) => {
        if (resData.success) {
          console.log("", resData)
          this.mattersList = resData.result
          this.$emit("matters", this.mattersList)
          console.log("--mList", this.mattersList)
        }
      }).catch((error) => {
        console.log(error)
      })
    },
    getStatic() {
      this.$post("/ybapp/home/selectHomeConfigList", {
        "areaId": this.childId
      }
      ).then((resData) => {
        if (resData.success) {
          console.log("", resData)
          let staticList = resData.result
          this.$emit("static", staticList)
          // console.log("--sList", this.mattersList)
        }
      }).catch((error) => {
        console.log(error)
      })
    },
    handleOpen(key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath)
    },
    handleSubOpen(item) {
      this.childId = item.value
      console.log("---sub", item.value)
      this.getArea()
      this.getStatic()
      this.$emit("clearIcon", this.iconList)
    }
  }
}
</script>

<style lang="less" scoped>
.sideNav{
    width: 200px;
    height: 770px;
    overflow-y: auto;
    overflow-x: hidden;
    border: 1px solid #EEE;
    /deep/.el-menu{
        border-right: none;
        .el-menu-item{
            height: 45px;
            line-height: 45px;
        }
        .el-submenu{
            .el-submenu__title{
                height: 45px;
                line-height: 45px;
            }
        }
    }
}
</style>



