      this.$get("sm/selfmachines/getSelfmachine").then(res => {
        if (res.data.success) {
          this.machineList = res.data.result
        }
      })
    },
    // 
    areaIdChange(data) {
      let areaIdPath = data.length > 0 ? data[data.length - 1].split("/") : []
      this.selectAreaId = areaIdPath.length > 0 ? areaIdPath[areaIdPath.length - 1] : ""
      this.getItemList()
    },
    // 
    maIdChange(index, row) {
      if (row.areaIdList.length > 0) {
        row.areaId = row.areaIdList[row.areaIdList.length - 1]
      }
      // this.$get("/sm/selfmachines/getSSMSiteMattersList", {
      //   areaId: row.areaId,
      // }).then(res => {
      //   console.log(res)
      // })
      console.log(index, row)
    },
    addOrg(row) {
      this.rightForm.editModelTable.push({
        areaIdList: [],
        "areaId": "",
        "city": "",
        "region": "",
        "accountTypeId": "",
        "mattersId": "",
        "machineNumber": "",
        machineNumberList: []
      })
      // console.log(this.userInfo.orgList)
    },
    minusOrg(row, index) {
      this.rightForm.editModelTable.splice(index, 1)
    },
    isLastRow(row) {
      if (row === this.rightForm.editModelTable.length - 1) {
        return true
      }
      return false
    },
    // 
    headerHide({ row, column, rowIndex, columnIndex }) {
      if (rowIndex === 0) {
        return { textAlign: "center" }
      }
      if (rowIndex === 1) {
        return { display: "none" }
      }
    },
    // 
    rowspan() {
      this.spanArr = []
      this.position = 0
      this.modelTable.forEach((item, index) => {
        if (index === 0) {
          this.spanArr.push(1)
          this.position = 0
        } else {
          if (this.modelTable[index].city === this.modelTable[index - 1].city) {
            this.spanArr[this.position] += 1// 
            this.spanArr.push(0) // 0
          } else {
            this.spanArr.push(1)
            this.position = index
          }
        }
      })
    },
    // 
    spanMethod({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0) { // ()
        // this.spanArrtable
        // :[1,1,2,0,2,0]
        // 1  
        // 2  
        // 0  
        if (row.city === "") {
          return {
            rowspan: 0, // 
            colspan: 0 // 
          }
        }
        const _row = this.spanArr[rowIndex]
        const _col = _row > 0 ? 1 : 0
        return {
          rowspan: _row, // 
          colspan: _col // 
        }
      }
      if (columnIndex === 1) {
        if (row.region === "") {
          return {
            rowspan: 1, // 
            colspan: 2 // 
          }
        }
      }
    },
    sysFunChange(id) {
      this.rightForm.sysFunctionId = id
      this.rightForm.sysFunctionName = this.jbRewbDict[id]
    },
    getWBmenu() {
      this.$get("/sys/function/queryTree", {
        funType: 4
      }).then(res => {
        if (res.status === 200) {
          const result = res.data.result
          if (result.length) {
            const target = result[0].children
            const arr = []
            const obj = {}
            for (const index in target) {
              arr.push({
                sysFunctionId: target[index].id,
                sysFunctionName: target[index].title
              })
              obj[target[index].id] = target[index].title
            }
            this.jbRewb = arr
            this.jbRewbDict = obj
          }
        }
      })
    },
    // 
    getTree() {
      this.$get("/sm/selfmachines/getAreaTree").then(res => {
        if (res.data.success) {
          this.areaList = res.data.result
        }
      })
      this.$post("/siteArea/tree", {
        bussType: this.$bussType,
        pageNum: 1,
        pageSize: 999
      }).then(data => {
        if (data.success) {
          let arr = []
          for (let i = 0; i < data.result.dataList.length; i++) {
            arr.push(data.result.dataList[i].id)
          }
          this.userTreeData = data.result.dataList
        }
      })
    },
    tabsHandleClick(tab, event) {
      if (tab.name == "1") {
        this.getType()
        this.getItemList()
      }
      if (tab.name == "2") {
        this.initItemClass()
      }
    },

    // 
    // 
    getType() {
      this.$get("/siteBusinessType/getSiteBusinessTypeList").then(data => {
        if (data.data.success) {
          this.getSiteBusinessTypeList = data.data.result.dataList

          let arr = JSON.parse(JSON.stringify(data.data.result.dataList))
          for (let i = 0; i < arr.length; i++) {
            siteBusinessType[arr[i].id] = arr[i].name
          }
        }
      })
    },
    filterMethod(query, item) {
      // return item.pinyin.indexOf(query) > -1
    },
    getItemList() {
      let params = {}
      if (this.selectAreaId !== "") {
        params = {
          areaId: this.selectAreaId
        }
      }
      this.$get("/sm/selfmachines/getSSMSiteMattersList", params).then(res => {
        if (res.data.success) {
          this.configList = res.data.result
          this.transferDataConfig = res.data.result
          this.transferDataClass = res.data.result
          let item = {
            id: res.data.result[0].id,
            name: res.data.result[0].name
          }
          this.configListClick(item, 0)
        } else {
          this.configList = []
        }
      })
      // this.$get("/siteMatters/getSiteMattersList", {
      //   bussType: this.$bussType,
      //   openStatus: this.openStatus,
      //   PageNum: "1",
      //   PageSize: "9999"
      // }).then(data => {
      //   if (data.data.success) {
      //     this.configList = data.data.result.dataList
      //     this.transferDataConfig = data.data.result.dataList
      //     this.transferDataClass = data.data.result.dataList

      //     let item = {
      //       id: data.data.result.dataList[0].id,
      //       name: data.data.result.dataList[0].name
      //     }
      //     this.configListClick(item, 0)
      //   } else {
      //     this.configList = []
      //   }
      // })
    },
    // 
    // 
    // 
    initItemClass() {
      this.$get("/sm/selfmachines/getSSMSiteIconCategoryList", {
        bussType: this.$bussType
      }).then(data => {
        this.thirdTableData = data.data.result.dataList
        let arr = JSON.parse(JSON.stringify(data.data.result.dataList))
        for (let i = 0; i < arr.length; i++) {
          thirdTableData[arr[i].id] = arr[i].name
        }
      })
    },
    // 
    deleteClass(row) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$get("/siteIconCategory/deleteSiteIconCategory", {
          id: row.id
        }).then(data => {
          if (data.data.success) {
            // this.$mess(data)
            this.initItemClass()
          }
        })
      }).catch(() => {
        this.$message({
          type: "info",
          message: ""
        })
      })
    },
    // 
    classSaveTable(row, index) {
      if (!row.name) {
        this.$message.error("")
        return false
      }
      if (!row.sortNo) {
        this.$message.error("")
        return false
      }
      this.$post("/siteIconCategory/saveSiteIconCategory", {
        id: row.id,
        bussType: this.$bussType,
        name: row.name,
        sortNo: row.sortNo
      }).then(data => {
        this.$mess(data)
        if (data.success) {
          row.isShow = false
          this.initItemClass()
        }
      })

      // this.thirdTableData.push()
    },
    pageInit(page, size) {
      this.pageNum = page
      this.pageSize = size
      this.search()
    },
    searchCheckedType1() {
      this.initTableData()
    },
    searchNameBlur() {
      this.initTableData()
    },
    initTableData() {
      this.$get("/siteMattersArea/getSiteAreaMattersList", {
        bussType: this.$bussType,
        PageNum: this.pageNum,
        PageSize: this.pageSize,
        areaId: this.areaId,
        accountTypeId: this.accountTypeId,
        businessTypeId: this.searchCheckedType,
        name: this.searchName
      }).then(data => {
        if (data.data.success) {
          this.tableData = data.data.result.dataList
          this.totalCount = data.data.result.totalCount
          let arr = JSON.parse(JSON.stringify(data.data.result.dataList))
          this.transferConfigModel = []
          for (let i = 0; i < arr.length; i++) {
            this.transferConfigModel.push(arr[i].id)
          }
        }
      })
    },
    addItemClass(row) {
      this.iconCategoryId = row.id
      this.addItemClassDialog = true
      this.transferMolelClass = []
      this.$get("/sm/selfmachines/getSSMSiteMattersByCategoryIdNull", {
        bussType: 2
      }).then(res => {
        if (res.data.success) {
          this.$get("/sm/selfmachines/getSSMSiteMattersByCategoryId", {
            iconCategoryId: row.id,
            bussType: this.$bussType
          }).then(data => {
            if (data.data.success) {
              this.transferDataClass = res.data.result.concat(data.data.result)
              let arr = JSON.parse(JSON.stringify(data.data.result))
              for (let i = 0; i < arr.length; i++) {
                this.transferMolelClass.push(arr[i].id)
              }
            }
          })
        }
      })
    },
    saveItemClassDialog() {
      this.$post("/siteMatters/updateSiteMattersCategoryIdById", {
        iconCategoryId: this.iconCategoryId,
        ids: this.transferMolelClass,
        bussType: this.$bussType
      }).then(data => {
        if (data.success) {
          this.initItemClass()
          this.addItemClassDialog = false
        }
        this.$mess(data)
      })
    },
    handleNodeClick(data, node) {
      this.searchName = ""
      this.searchCheckedType = ""
      if (node.level == 2) {
        this.isConfig = true
        this.areaId = node.parent.data.id
        this.accountTypeId = data.id
        this.initTableData()
      } else {
        this.isConfig = false
      }
    },
    dialogConfigSave() {
      this.$post("/siteMattersArea/saveSiteAreaMatters", {
        accountTypeId: this.accountTypeId,
        areaId: this.areaId,
        mattersIds: this.transferConfigModel
      }).then(data => {
        this.$mess(data)
        if (data.success) {
          this.dialogConfig = false
        }
      })
    },
    // 
    classCancleTable(row, index) {
      this.initItemClass()
      // row.date = row.data1
      // row.isShow = false
      // this.thirdTableData.push()
    },
    // 
    addClass() {
      if (this.thirdTableData.length > 0 && this.thirdTableData[this.thirdTableData.length - 1].isShow) {
        return
      }
      this.thirdTableData.push({
        name: "",
        sortNo: "",
        isShow: true
      })
    },
    // 
    handlePictureCardPreview(file) {
      this.imageUrl = file.url
      this.iconDialog = true
    },
    handleExceed(files, fileList) {
      this.$message.warning("")
    },
    pcHandleRemove(file, fileList) {
      this.pcForm.picUrl = ""
    },
    configListClick(item, index) {
      this.configTitle = item.name
      this.configListIndex = index
      this.$get("/sm/selfmachines/siteSSMMattersDetail", {
        id: item.id
      }).then(data => {
        if (data.data.success) {
          this.itemId = data.data.result.siteMattersDto.id
          this.isShow = true
          this.modifyTime = data.data.result.siteMattersDto.modifyTime
          this.modifierName = data.data.result.siteMattersDto.modifierName
          this.createTime = data.data.result.siteMattersDto.createTime
          this.creatorName = data.data.result.siteMattersDto.creatorName
          this.rightForm.name = data.data.result.siteMattersDto.name
          this.rightForm.sysFunctionId = data.data.result.siteMattersDto.sysFunctionId
          this.rightForm.shortName = data.data.result.siteMattersDto.shortName
          this.rightForm.iconCategoryId = data.data.result.siteMattersDto.iconCategoryId == 0 ? "" : data.data.result.siteMattersDto.iconCategoryId
          this.rightForm.siteBusinessType = data.data.result.siteMattersDto.siteBusinessType
          this.rightForm.matterNumber = data.data.result.siteMattersDto.matterNumber
          this.rightForm.coordinationAreaType = data.data.result.siteMattersDto.coordinationAreaType
          this.rightForm.printType = data.data.result.siteMattersDto.printType
          this.rightForm.businessNumber = data.data.result.siteMattersDto.businessNumber
          this.rightForm.mattersCoding = data.data.result.siteMattersDto.mattersCoding
          this.pcForm.jumpUrl = data.data.result.siteBlock.jumpUrl
          this.pcForm.picUrl = data.data.result.siteBlock.picUrl
          this.pcForm.sortNumber = data.data.result.siteBlock.sortNumber
          this.pcForm.status = data.data.result.siteMattersDto.isTakesEffect
          this.pcForm.isRecommend = data.data.result.siteMattersDto.isRecommend
          this.basieUrl = data.data.result.siteBlock.basieUrl
          this.modelTable = data.data.result.siteMattersAreaSaveVOS
          this.rowspan()
          this.pcFileList = []
          if (this.pcForm.picUrl) {
            this.pcFileList.push({
              url: this.basieUrl + this.pcForm.picUrl
            })
          }

          if (this.pcForm.jumpUrl) {
            this.itemConfigChecked1 = true
          } else {
            this.itemConfigChecked1 = false
          }
        }
      })
    },

    classEdit(row, index) {
      row.isShow = true
      row.data1 = row.date
      this.thirdTableData.push()
    },

    addMenu() {
      this.isEmpty = false
      this.isEdit = true
      this.itemId = ""
      this.isShow = false
      this.rightForm.name = ""
      this.rightForm.shortName = ""
      this.rightForm.iconCategoryId = ""
      this.rightForm.siteBusinessType = "1"
      this.rightForm.sysFunctionId = ""
      this.rightForm.matterNumber = ""
      this.rightForm.coordinationAreaType = ""
      this.rightForm.printType = ""
      this.rightForm.businessNumber = ""
      this.rightForm.mattersCoding = ""
      this.itemConfigChecked1 = false
      this.itemConfigChecked2 = false
      this.pcForm.jumpUrl = ""
      this.pcForm.picUrl = ""
      this.pcForm.status = "1"
      this.pcForm.isRecommend = "0"
      this.pcFileList = []
      this.rightForm.editModelTable = [
        {
          //  id
          areaIdList: [],
          //  id
          "areaId": "",
          // 
          "city": "",
          // 
          "region": "",
          "accountTypeId": "",
          "mattersId": "",
          //  id
          "machineNumber": "",
          //  id
          machineNumberList: []
        }
      ]
    },
    edit() {
      this.isEdit = false
      let _this = this
      this.isShow = false
      this.rightForm.editModelTable = this.modelTable.map(item => {
        let parent
        let parentId
        if (item.city) {
          parent = _this.areaList.filter(it => {
            return item.city === "" ? it.areaName === "" : it.areaName === item.city
          })
          parentId = parent[0].id
        }
        return {
          //  id
          areaIdList: parentId ? [parentId, item.areaId] : [item.areaId],
          //  id
          "areaId": item.areaId,
          // 
          "city": item.city,
          // 
          "region": item.region,
          "accountTypeId": item.accountTypeId,
          "mattersId": item.mattersId,
          //  id
          "machineNumber": item.machineNumber,
          //  id
          machineNumberList: item.machineNumber.split(",")
        }
      })
    },
    itemSave() {
      console.log(this.rightForm.editModelTable)
      if (!this.pcForm.jumpUrl) {
        this.$message.warning("")
        return
      }
      if (!this.rightForm.editModelTable[0].areaIdList.length) {
        this.$message.warning("")
        return
      }
      if (!this.rightForm.editModelTable[0].machineNumberList.length) {
        this.$message.warning("")
        return
      }
      this.$refs["rightForm"].validate(validate => {
        if (validate) {
          console.log("")
          let arr = JSON.parse(JSON.stringify(this.rightForm.editModelTable))
          arr = arr.map(item => {
            item.machineNumber = item.machineNumberList.join(",")
            delete item.areaIdList
            delete item.machineNumberList
            return item
          })

          let params = {
            id: this.itemId,
            bussType: this.$bussType,
            iconCategoryId: this.rightForm.iconCategoryId,
            isRecommend: this.pcForm.isRecommend,
            isTakesEffect: this.pcForm.status,
            // modifierId: ,
            name: this.rightForm.name,
            // operatorId: ,
            shortName: this.rightForm.shortName,
            siteBusinessType: this.rightForm.siteBusinessType,
            sysFunctionId: this.rightForm.sysFunctionId,
            sysFunctionName: this.rightForm.sysFunctionName,
            matterNumber: this.rightForm.matterNumber,
            coordinationAreaType: this.rightForm.coordinationAreaType,
            printType: this.rightForm.printType,
            businessNumber: this.rightForm.businessNumber,
            mattersCoding: this.rightForm.mattersCoding,
            block: {
              // areaCode: ,
              // downCornerMarkerId: ,
              // floorId: ,
              // iconCategoryId: ,
              isHome: 1,
              jumpUrl: this.pcForm.jumpUrl,
              // name: ,
              picUrl: this.pcForm.picUrl,
              // platformCode: ,
              sortNumber: this.pcForm.sortNumber
              // status: ,
              // upCornerMarkerId: ,
            },
            siteMattersAreaSaveVOS: arr
          }
          console.log(params)
          this.$post("/sm/selfmachines/saveSSMSiteMatters", params).then(data => {
            if (data.success) {
              this.getItemList()
              this.isShow = true
            }
            this.$mess(data)
          })
        } else {
          console.log("")
          this.$message.warning("")
          return
        }
      })

      // this.isShow = true
    },
    cancle() {
      this.isShow = true
    },
    uploadError() {
      this.$message.error("")
    },
    pcHandleAvatarSuccess(res, file) {
      // file.response.result.key

      this.pcForm.picUrl = file.response.result.key
      // this.pcimageUrl = URL.createObjectURL(file.raw)
      // this.
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg" || "image/png"
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!isJPG) {
        this.$message.error(" JPGPNG !")
      }
      if (!isLt2M) {
        this.$message.error(" 2MB!")
      }
      // console.log()

      return isJPG && isLt2M
    }
  }
}
</script>

