}
.ep-main .content .item-content {
  display: inline-block;
  word-wrap: break-word;
  word-break: break-all;
}
.ep-main .rightContent {
  padding-left: 20px;
}
.ep-main .rightContent .title {
  border-left: 2px solid #409eff;
