}

/deep/  .el-tabs__header {
  margin: 0;
}

.avatar-uploader /deep/ .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.avatar-uploader /deep/ .el-upload:hover {
  border-color: #409eff;
}

.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}

.uploadHide /deep/ .el-upload--picture-card{
  display: none;
}

.uploadShow /deep/ .el-upload--picture-card {
  display: block;
}

.avatar {
  width: 178px;
  height: 178px;
  display: block;
}

.border-top {
  border-top: 1px solid #ccc;
}

.configInfo {
