<template>
  <div id="app">
    <el-form ref="searchForm" :model="searchForm">
      <el-row :gutter="20">
        <ep-input
          @input-message="onChangeSearchForm"
          colspan="4"
          :value="searchForm.codeType"
          placeholder=""
          :isIcon="true"
          size="mini"
          name="codeType"
        ></ep-input>
        <ep-input
          @input-message="onChangeSearchForm"
          colspan="4"
          :value="searchForm.codeName"
          placeholder=""
          :isIcon="true"
          size="mini"
          name="codeName"
        ></ep-input>
        <ep-input
          @input-message="onChangeSearchForm"
          colspan="4"
          :value="searchForm.parameterSort"
          placeholder=""
          :isIcon="true"
          size="mini"
          name="parameterSort"
        ></ep-input>
        <ep-select
          @select-message="onChangeSearchForm"
          colspan="4"
          :value="searchForm.effectiveSign"
          placeholder=""
          :isIcon="true"
          size="mini"
          name="effectiveSign"
        ></ep-select>
        <ep-select
          @select-message="onChangeSearchForm"
          colspan="4"
          :value="searchForm.maintainSign"
          placeholder=""
          :isIcon="true"
          size="mini"
          name="maintainSign"
        ></ep-select>
        <el-col :span="4" :offset="0">
          <el-form-item>
            <el-button @click="onSearch" size="mini"></el-button>
            <el-button type="primary" size="mini" @click="addDialog"></el-button>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <el-dialog title="" :visible.sync="dialogFormVisible" @close="closeDialog('diaLogForm')">
      <el-form :model="diaLogForm" ref="diaLogForm" label-width="100px" :rules="diaLogrules">
        <el-row :gutter="20">
          <ep-input
            @input-message="onChangeDiaLogForm"
            colspan="12"
            :value="diaLogForm.codeType"
            placeholder=""
            label=""
            name="codeType"
          ></ep-input>
          <ep-input
            @input-message="onChangeDiaLogForm"
            colspan="12"
            :value="diaLogForm.codeValue"
            placeholder=""
            label=""
            name="codeValue"
          ></ep-input>
        </el-row>
        <el-row :gutter="20">
          <ep-input
            @input-message="onChangeDiaLogForm"
            colspan="12"
            :value="diaLogForm.codeName"
            placeholder=""
            label=""
            name="codeName"
          ></ep-input>
          <ep-select
            @select-message="onChangeDiaLogForm"
            colspan="12"
            :value="diaLogForm.effectiveSign"
            placeholder=""
            name="effectiveSign"
            labelTitle=""
          ></ep-select>
        </el-row>
        <el-row :gutter="20">
          <ep-input
            @input-message="onChangeDiaLogForm"
            colspan="12"
            :value="diaLogForm.parameterSort"
            placeholder=""
            label=""
            name="parameterSort"
          ></ep-input>
          <ep-select
            @select-message="onChangeDiaLogForm"
            colspan="12"
            :value="diaLogForm.maintainSign"
            placeholder=""
            name="maintainSign"
            labelTitle=""
          ></ep-select>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="saveDialogForm('diaLogForm')"> </el-button>
      </div>
    </el-dialog>
    <el-table :data="tableData" :height="tableHeight" style="width: 100%">
      <el-table-column align="center" prop="id" sortable label=""></el-table-column>
      <el-table-column align="center" prop="codeType" label=""></el-table-column>
      <el-table-column align="center" prop="codeValue" label=""></el-table-column>
      <el-table-column align="center" prop="codeName" label=""></el-table-column>
      <el-table-column align="center" prop="parameterSort" label=""></el-table-column>
      <el-table-column align="center" label="">
        <template slot-scope="scope">
          <span>{{scope.row.effectiveSign | effectiveSign}}</span>
        </template>
      </el-table-column>
      <el-table-column align="center" label="">
        <template slot-scope="scope">
          <span>{{scope.row.maintainSign | maintainSign}}</span>
        </template>
      </el-table-column>
      <el-table-column align="center" label="">
        <template slot-scope="scope">
          <el-button
            v-show="scope.row.maintainSign==0?false:true"
            @click="editClick(scope.row)"
            type="text"
            size="small"
          ></el-button>
          <ep-delete
            v-show="scope.row.maintainSign==0?false:true"
            :id="scope.row.id"
            parmeId="id"
            url="/sys/code/deleteCode"
            :init="true"
            @init="deleteInit"
          >
            <el-button type="text" size="small"></el-button>
          </ep-delete>
        </template>
      </el-table-column>
    </el-table>
    <ep-page :total="total" @page="pageInit"></ep-page>
  </div>
</template>

<script>
/**
  *
    home/sys/code/queryTable
    sys/code
    id id
    codeType 
    codeValue 
    parameterSort 
    codeName	
    effectiveSign ::10
    maintainSign  ::10
    createTime 
    modifyTime  
    page 
    size 
 */
export default {
  data() {
    return {
      dateValue: "",
      dialogFormVisible: false,
      total: "",
      daterangeFrom: {
        daterangeValue: ""
      },
      tableData: [],
      diaLogrules: {
        codeType: this.$rules(""),
        codeValue: this.$rules(""),
        codeName: this.$rules(""),
        effectiveSign: this.$rules(""),
        maintainSign: this.$rules("")
      },
      pickerOptions: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit("pick", [start, end])
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
              picker.$emit("pick", [start, end])
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
              picker.$emit("pick", [start, end])
            }
          }
        ]
      },
      searchForm: {
        codeType: "",
        codeName: "",
        parameterSort: "",
        effectiveSign: "",
        maintainSign: ""
      },
      diaLogForm: {
        codeType: "",
        codeName: "",
        codeValue: "",
        parameterSort: "",
        effectiveSign: "1",
        maintainSign: "1"
      },
      page: 1,
      size: 10,
      id: ""
    }
  },
  created() {
    this.init(this.page, this.size)
  },
  computed: {
    tableHeight(){
      return window.innerHeight - 230
    }
  },
  methods: {
    init(page, size) {
      console.log(page, size)

      this.$get("/sys/code/queryTable", {
        page: page,
        size: size,
        ...this.searchForm
      }).then(data => {
        this.tableData = data.data.pageList.result
        this.total = data.data.count
      })
    },
    closeDialog(formRule) {
      this.$refs[formRule].resetFields()
    },
    deleteInit() {
      this.init(this.page, this.size)
    },
    editClick(row) {
      this.dialogFormVisible = true
      this.id = row.id
      this.diaLogForm = {
        codeType: row.codeType,
        codeName: row.codeName,
        codeValue: row.codeValue,
        parameterSort: row.parameterSort,
        effectiveSign: row.effectiveSign,
        maintainSign: row.maintainSign
      }
    },
    pageInit(page, size) {
      this.page = page
      this.size = size
      this.init(page, size)
    },
    onChangeDiaLogForm(data, type) {
      console.log(data, type)

      this.diaLogForm[type] = data
    },
    onChangeSearchForm(data, type) {
      console.log(data)
      console.log(this.searchForm)

      this.searchForm[type] = data
    },
    addDialog() {
      this.dialogFormVisible = true
      this.id = ""
      this.diaLogForm = {
        codeType: "",
        codeName: "",
        codeValue: "",
        parameterSort: "",
        effectiveSign: "1",
        maintainSign: "1"
      }
    },
    saveDialogForm(formName) {
      this.$refs[formName].validate(valid => {
        console.log(this.diaLogForm)

        if (valid) {
          this.$post("/sys/code/saveCode", {
            id: this.id,
            ...this.diaLogForm
          }).then(data => {
            this.$mess(data)
            if (data.success) {
              this.init(this.page, this.size)
              this.dialogFormVisible = false
              this.diaLogForm = {
                codeType: data.codeType,
                codeName: data.codeName,
                codeValue: data.codeValue,
                parameterSort: data.parameterSort,
                effectiveSign: data.effectiveSign,
                maintainSign: data.maintainSign
              }
            }
          })
        } else {
          return false
        }
      })
    },
    onSearch() {
      console.log(this.searchForm)

      this.init(this.page, this.size)
    }
  }
}
</script>

<style scoped lang="css">
/deep/.el-dialog__body {
  display: flex;
  justify-content: center;
}
.page {
  display: flex;
  justify-content: flex-end;
  margin-top: 10px;
}
</style>



