<template>
  <div class="container">
    <div id="go" class="table-content">
      <el-form
        :inline="true"
        size="mini"
        :model="searchInfo"
        class="demo-form-inline searchFrom"
        label-width="130px">
        <el-row>
          <el-col :span="10">
            <el-form-item label="">
              <el-input v-model="searchInfo.funName"
                clearable size="mini" placeholder=""></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item label="">
              <el-select v-model="searchInfo.funType" clearable size="mini"
                placeholder="" style="width:100%">
                <el-option
                  v-for="item in funTypeArr"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <!-- <el-col :span="5">
            <el-form-item label="">
              <el-select v-model="searchInfo.businessType" clearable size="mini"
                placeholder="">
                <el-option
                  v-for="item in businessTypeArr"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col> -->
          <el-col :span="5">
            <el-form-item label="">
              <el-select v-model="searchInfo.ruleStruts" clearable size="mini"
                placeholder="" style="width:100%">
                <el-option
                  v-for="item in ruleStrutsArr"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="5">
            <el-form-item label="">
              <el-select v-model="searchInfo.isBus" clearable size="mini"
                placeholder="" style="width:100%">
                <el-option
                  v-for="item in isBusArr"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item label="">
              <el-select v-model="searchInfo.funLevel" clearable size="mini"
                placeholder="" style="width:100%">
                <el-option
                  v-for="item in funLevelArr"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item label="">
              <el-select v-model="searchInfo.auFlag" clearable size="mini"
                placeholder="" style="width:100%">
                <el-option
                  v-for="item in auFlagArr"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="5" :offset="3">
            <el-form-item>
              <el-button type="primary" size="mini" @click="search()"></el-button>
              <el-button size="mini" @click="resetSearchCondition()"></el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div class="tableDiv">
        <el-table
          class="customer-table"
          v-loading="loading"
          :data="tableData"
          :header-cell-style="{background:'#F2F2F2', textAlign:'left', border: 0}"
          style="width: 100%">
          <el-table-column
            prop="funName"
            label="">
          </el-table-column>
          <!-- <el-table-column
            prop="funType"
            label="">
            <template slot-scope="scope">
              <span>{{ scope.row.businessType | businessType }}</span>
            </template>
          </el-table-column> -->
          <el-table-column
            prop="funType"
            label="">
            <template slot-scope="scope">
              <span>{{ scope.row.funType | funType }}</span>
            </template>
          </el-table-column>
          <el-table-column width="150px"
            prop="ruleStruts"
            label="">
            <template slot-scope="scope">
              <span>{{ scope.row.ruleStruts | ruleStruts }}</span>
            </template>
          </el-table-column>
          <el-table-column width="150px"
            prop="isBus"
            label="">
            <template slot-scope="scope">
              <span>{{ scope.row.isBus | isBus }}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="funLevel"
            label="">
            <template slot-scope="scope">
              <span>{{ scope.row.funLevel | funLevel }}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="areaName"
            label="">
            <template slot-scope="scope">
              <span>{{ scope.row.areaName ? scope.row.areaName : ''}}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="auFlag"
            label="">
            <template slot-scope="scope">
              <span>{{ scope.row.auFlag | auFlag }}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="isLeading"
            label="">
            <template slot-scope="scope">
              <span>{{ scope.row.isLeading | isLeading }}</span>
            </template>
          </el-table-column>
          <el-table-column width="100px"
            label="">
            <template slot-scope="scope">
              <el-button @click="editRule(scope.row)" type="text"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          style="margin-top:15px"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="searchInfo.page"
          :page-sizes="[10, 20]"
          :page-size="searchInfo.pageSize"
          layout="prev, pager, next, jumper, sizes"
          :total="pageSum">
        </el-pagination>
      </div>
    </div>
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      @close="handleClose"
      width="60%">
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="150px">
        <el-form-item label="" prop="funName">
          <span>{{ ruleForm.funName }}</span>
        </el-form-item>
        <!-- <el-form-item label="" prop="businessType">
          <el-select
            v-model="ruleForm.businessType"
            filterable
            placeholder=""
            style="width: 85%">
            <el-option
              v-for="item in businessTypeArr"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item> -->
        <el-form-item label="" prop="funType">
          <!-- <el-select class="pop_select" popper-class="pop_select_down" v-model="ruleForm.funType" @change="selectSys" multiple collapse-tags size="small" placeholder="" style="width: 85%">
            <el-checkbox v-model="isSelectAll" style="padding-left:18px" @change="selectAll"></el-checkbox>
            <el-option
              v-for="item in funTypeArr"
              :key="item.value"
              :label="item.label"
              :value="item.value">
              <span class="check"></span>
              <span>{{ item.label }}</span>
            </el-option>
          </el-select> -->
          <el-select v-model="ruleForm.funType" clearable
            placeholder="">
            <el-option
              v-for="item in funTypeArr"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="ruleStruts">
          <el-switch
            v-model="ruleForm.ruleStruts"
            active-color="#13ce66">
          </el-switch>
        </el-form-item>
        <el-form-item label="" prop="isBus">
          <el-switch
            v-model="ruleForm.isBus"
            active-color="#13ce66">
          </el-switch>
        </el-form-item>
        <el-form-item label="" prop="funLevel">
          <el-radio-group v-model="ruleForm.funLevel">
            <el-radio :label="item.value" :value="item.label" v-for="item in funLevelArr" :key="item.value">{{ item.label }}</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="auFlag">
          <el-radio-group v-model="ruleForm.auFlag">
            <el-radio :label="item.value" :value="item.label" v-for="item in auFlagArr" :key="item.value">{{ item.label }}</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="isLeading">
          <el-switch
            v-model="ruleForm.isLeading"
            active-color="#13ce66">
          </el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="confirmRule"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'auditConfigRule',
  data() {
    return {
      loading: false,
      searchInfo: { // 
        funName: null,
        funType: null,
        // businessType: null,
        ruleStruts: null,
        isBus: null,
        funLevel: null,
        auFlag: null,
        page: 1,
        pageSize: 10
      },
      pageSum: 0,
      dialogVisible: false,
      ruleForm: { // 
        id: null,
        orgId: null,
        funId: null,
        funName: null,
        // businessType: null, // 
        // funType: [], // 
        funType: null,
        ruleStruts: null,
        isBus: null,
        funLevel: null,
        auFlag: null,
        isLeading: null
      },
      isSelectAll: false,
      rules: {
        funType: [{required: true, message: '', trigger: 'change'}],
        ruleStruts: [{required: true, message: '', trigger: 'change'}],
        isBus: [{required: true, message: '', trigger: 'change'}],
        funLevel: [{required: true, message: '', trigger: 'change'}],
        auFlag: [{required: true, message: '', trigger: 'change'}],
        isLeading: [{required: true, message: '', trigger: 'change'}]
      },
      funTypeArr: [
        {label: "", value: '1'},
        {label: "", value: '2'},
        {label: "", value: '3'},
        {label: "", value: '4'},
        {label: "", value: '5'},
        {label: "", value: '6'},
        {label: "", value: '10'}
      ],
      // businessTypeArr: [
      //   {label: "", value: '1'},
      //   {label: "", value: '2'}
      // ],
      ruleStrutsArr: [
        {label: "", value: '1'},
        {label: "", value: '2'}
      ],
      isBusArr: [
        {label: "", value: "1"},
        {label: "", value: "2"}
      ],
      funLevelArr: [
        {label: "", value: "1"},
        {label: "", value: "2"},
        // {label: "", value: "3"}
      ],
      auFlagArr: [
        {label: "", value: "0"},
        {label: "", value: "1"}
      ],
      tableData: []
    }
  },
  mounted() {
    this.getTableDate()
  },
  methods: {
    handleSizeChange(val) {
      this.loading = true
      this.searchInfo.pageSize = val
      this.getTableDate()
    },
    handleCurrentChange(val) {
      document.getElementById("go").scrollIntoView()
      this.loading = true
      window.scrollBy(0, -10)
      this.searchInfo.page = val
      this.getTableDate()
    },
    /**
     * 
     */
    search() {
      this.searchInfo.page = 1
      this.getTableDate()
    },
    /**
     * 
     */
    resetSearchCondition() {
      this.searchInfo = {
        funName: null,
        funType: null,
        // businessType: null,
        ruleStruts: null,
        isBus: null,
        funLevel: null,
        auFlag: null,
        page: 1,
        pageSize: 10
      },
      this.getTableDate()
    },
    /**
     * 
     */
    getTableDate() {
      this.$get("/sys/auditconfigrule/getAuditConfigRuleDTOListPage", this.searchInfo).then(res => {
        if (res.data.success) {
          this.tableData = res.data.result.dataList
          this.pageSum = res.data.result.totalCount
          this.loading = false
        } else {
          this.$message({
            type: "error",
            message: res.data.resultDes
          })
        }
      }).catch(error => {
        console.log(error)
      })
    },
    /**
     * 
     */
    selectSys(val) {
      if (val.length === this.funTypeArr.length) {
        this.isSelectAll = true
      } else {
        this.isSelectAll = false
      }
    },
    /**
     * 
     */
    selectAll(val) {
      this.ruleForm.funType = []
      if (val) {
        this.funTypeArr.forEach(item => {
          this.ruleForm.funType.push(item.value)
        })
      } else {
        this.ruleForm.funType = []
      }
    },
    /**
     * 
     */
    editRule(row) {
      this.dialogVisible = true
      this.getDetail(row)
    },
    /**
     * 
     */
    getDetail(row) {
      let params = {
        id: row.id
      }
      this.$get("/sys/auditconfigrule/getAuditConfigRuleInfo", params).then(res => {
        if (res.data.success) {
          Object.keys(res.data.result).forEach(key => {
            if (key in this.ruleForm) {
              this.ruleForm[key] = res.data.result[key]
            }
          })
          // 
          this.ruleForm.ruleStruts = res.data.result.ruleStruts == '1' ? true : false
          this.ruleForm.isBus = res.data.result.isBus == '1' ? true : false
          this.ruleForm.isLeading = res.data.result.isLeading == '0' ? true : false
        } else {
          this.$message({
            type: "error",
            message: res.data.resultDes
          })
        }
      }).catch(error => {
        console.log(error)
      })
    },
    /**
     * 
     */
    confirmRule() {
      this.$refs["ruleForm"].validate((valid) => {
        let params = {}
        Object.keys(this.ruleForm).forEach(key => {
          params[key] = this.ruleForm[key]
        })
        
        // 
        params.ruleStruts =this.ruleForm.ruleStruts === true ? '1' : '2'
        params.isBus = this.ruleForm.isBus === true ? '1' : '2'
        params.isLeading = this.ruleForm.isLeading === true ? '0' : '1'
        this.$post("/sys/auditconfigrule/updAuditConfigRuleInfo", params).then(res => {
          if (res.success) {
            this.dialogVisible = false
            this.getTableDate()
            this.$message({
              type: "success",
              message: ''
            })
          } else {
            this.$message({
              type: "error",
              message: res.resultDes
            })
          }
        }).catch(error => {
          console.log(error)
        })
      })
    },
    // 
    handleClose() {
      this.isSelectAll = false
      this.ruleForm = {
        id: null,
        orgId: null,
        funId: null,
        funName: null,
        // businessType: null,
        // funType: [],
        funType: null,
        ruleStruts: null,
        isBus: null,
        funLevel: null,
        auFlag: null,
        isLeading: null
      },
      this.$refs["ruleForm"].resetFields();
    }
  }
}
</script>
<style lang="less" scoped>
.tableDiv {
  margin-top: 20px;
  .el-table__row>td {
    border-right: none;
    border-left: none
  }
}
.searchFrom /deep/ .el-form-item {
  display: flex;
}
.el-col {
  height: 48px;
}
.customer-table /deep/ th{
  border:none;
}
/deep/ .el-form-item {
  width: 100%;
}
/deep/ .el-form-item__content {
  width: 100%;
}
.customer-table /deep/ td,.customer-table /deep/ th.is-leaf {
  border:none;
  border-bottom: 1px solid #EBEEF5;
}
.el-select-dropdown.is-multiple .el-select-dropdown__item.selected::after {
  top: 1px;
  left: 24px;
  font-weight: 400;
  z-index: 1;
  color: #fff;
}
.el-select-dropdown.is-multiple .el-select-dropdown__item.selected .check {
  background-color: #409eff;
}
.el-select-dropdown.is-multiple .el-select-dropdown__item .check {
  display: inline-block;
  position: relative;
  width: 15px;
  height: 15px;
  top: 3px;
  border: 1px solid #DCDFE6;
  margin: 0 6px 0 3px;
  border-radius: 2px;
  box-sizing: border-box;
  background-color: #FFF;
  z-index: 1;
  transition: border-color .25s cubic-bezier(.71,-.46,.29,1.46),background-color .25s cubic-bezier(.71,-.46,.29,1.46);
}
</style>


