<template>
  <div class="content">
    <div class="head">
      <div class="com">
        <el-input placeholder="" class="space in" v-model="newsName"></el-input>
      <div class="space">
        <span class="demonstration"></span>
        <el-date-picker
          unlink-panels
          v-model="date"
          type="daterange"
          range-separator=""
          start-placeholder=""
          end-placeholder=""
        @change="changeDate">
        </el-date-picker>
      </div>
        <el-select v-model="view" class="space" placeholder="">
          <el-option
            v-for="item in optionA"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <el-select v-model="viewStatus" class="space" placeholder="">
          <el-option
            v-for="item in optionB"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <el-button type="primary" class="space" @click="Search"></el-button>
        <el-button type="primary" class="space" @click="addClick"></el-button>
      </div>
    </div>
    <el-table class="table"
      :data="tableData"
      style="width: 100%"
      :header-cell-style="tableHeaderColor">
      <el-table-column
        prop="name"
        label=""
        width="300">
      </el-table-column>
      <el-table-column
        prop="visibility"
        label=""
        width="150">
      </el-table-column>
      <el-table-column
        prop="createTime"
        label=""
        width="200">
      </el-table-column>
      <el-table-column
        prop="createName"
        label=""
        width="120">
      </el-table-column>
      <el-table-column
        prop="status"
        label=""
        width="120">
      </el-table-column>
      <el-table-column
        label="">
        <template slot-scope="scope" prop="state">
          <el-button type="text" @click="checkTable(scope.row)"></el-button>
          <el-button type="text" v-if="scope.row.status === ''" @click="editTable(scope.row)"></el-button>
          <el-button type="text" v-if="scope.row.status !== ''" @click="deleteTable(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      :visible.sync="dialogVisible"
      width="30%">
      <i class="el-icon-warning" style="color: orange;"></i><span></span>
      <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false"> </el-button>
    <el-button type="primary" @click="deleteConfirm"> </el-button>
  </span>
    </el-dialog>
    <div class="block">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="100"
        layout="total, prev, pager, next"
        :total="1000">
      </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      newsName: "",
      date: "",
      view: [],
      viewStatus: [],
      optionA: [{
        value: "1",
        label: ""
      }, {
        value: "2",
        label: ""
      }],
      optionB: [{
        value: "on",
        label: ""
      }, {
        value: "off",
        label: ""
      }, {
        value: "del",
        label: ""
      }, {
        value: "wait",
        label: ""
      }],
      tableData: [],
      currentPage: 5,
      dialogVisible: false,
      detailData: {},
      arr: {},
      btnState: "",
      params: {
        "pageNum": 1,
        "pageSize": 10
      },
      date1: "",
      date2: ""
    }
  },
  created() {
    this.getList()
  },
  methods: {
    // 
    changeDate() {
      this.date1 = this.util.formatDate(new Date(this.date[0]), "yyyy-MM-dd hh:mm:ss")
      this.date2 = this.util.formatDate(new Date(this.date[1]), "yyyy-MM-dd hh:mm:ss")
    },
    // 
    Search() {
      this.params.name = this.newsName
      /* for ( let i=0; i<length; i++ ) {
          dateArr[i] = this.date1 + i;
          return dateArr;
        } */
      this.params.startTime = this.date1
      this.params.endTime = this.date2
      this.params.visibility = this.view
      this.params.status = this.viewStatus
      this.getList()
    },
    // 
    getList() {
      let obj = {
        ...this.params,
        "pageNum": 1,
        "pageSize": 10
      }
      console.log("-----obj:", obj)
      this.$post("/ybapp/news/list", obj).then((resData) => {
        if (resData.success) {
          console.log("", resData.result)
          this.tableData = resData.result.dataList
          for (let i = 0; i < this.tableData.length; i++) {
            let formatDate = this.util.formatDate(new Date(this.tableData[i].createTime), "yyyy-MM-dd hh:mm:ss")
            this.tableData[i].createTime = formatDate
          }
        }
      }).catch((error) => {
        console.log(error)
      })
    },
    handleSizeChange(val) {
      console.log(` ${val} `)
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`)
    },
    addClick() {
      this.$router.push("/home/addNews")
    },
    getDetail(id) {
      this.$post("/ybapp/news/detail", {
        "id": id
      }).then((resData) => {
        if (resData.success) {
          console.log("", resData.result)
          this.detailData = {...this.detailData, ...resData.result}
          console.log("---state", this.btnState)
          console.log("9999", this.detailData)
          if (this.btnState === "edit") {
            this.$router.push({path: "/home/addNews", query: {param: this.detailData, formState: "edit"}})
          } else {
            this.$router.push({path: "/home/detailNews", query: {param: this.detailData}})
          }
        }
      }).catch((error) => {
        console.log(error)
      })
    },
    checkTable(row) {
      this.btnState = "check"
      this.getDetail(row.id, this.btnState)
    },
    editTable(row) {
      console.log("row", row)
      this.btnState = "edit"
      this.getDetail(row.id, this.btnState)
    },
    // table header
    tableHeaderColor({ row, column, rowIndex, columnIndex }) {
      if (rowIndex === 0) {
        return "background-color: #ddd;color: black;font-weight: 500;"
      }
    },
    deleteTable(row) {
      this.tableData.id = row.id
      console.log("----id", row.id)
      this.dialogVisible = true
    },
    deleteConfirm() {
      this.$post("/ybapp/news/delete", {
        "id": this.tableData.id
      }).then((resData) => {
        if (resData.success) {
          this.dialogVisible = false
          console.log("", resData)
          this.getList()
        }
      }).catch((error) => {
        console.log(error)
      })
    }
  }
}
</script>

<style scoped lang="less">
  .content {
    overflow: auto;
    height: 800px;
    background-color: white;
    width: 100%;
    .head {
      display: flex;
      margin-bottom: 20px;
      justify-content: space-between;
      .com {
        display: flex;
        .space {
          margin-right: 10px;
        }
        .in {
          width: 200px;
        }
      }
      .add {
        align-self: flex-end;
      }
    }
    .block {
      position: fixed;
      bottom: 20px;
      left: 50%;
      transform: translateX(-50%);
    }
  }
</style>



