<template>
    <div class="indexPageConfig">
        <side-nav @clearIcon="clearIcon" @matters="getMatter" @static="getStatic"></side-nav>
        <phone-content :iconList="iconList" style="margin-left:20px"></phone-content>
        <config-content @iconConfig="changeIcon" :unitContent="unitContent"  :personContent="personContent" :matterList="matterList" style="margin-left:20px"></config-content>
    </div>
</template>

<script>
import sideNav from "./sideNav"
import phoneContent from "./phoneContent"
import configContent from "./configContent"
export default {
  components: {
    "side-nav": sideNav,
    "phone-content": phoneContent,
    "config-content": configContent
  },
  data() {
    return {
      iconList: [],
      matterList: [],
      unitContent: [],
      personContent: []
    }
  },
  methods: {

    changeIcon(val) {
      console.log("", val)
      this.iconList = val
    },
    getMatter(val) {
      console.log("", val)
      this.matterList = val
    },
    getStatic(val) {
      this.unitContent = []
      this.personContent = []
      val.unitList.forEach(ele => {
        let obj = {
          text: ele.mattersName,
          imgUrl: ele.picUrl
        }
        this.unitContent.push(obj)
      })
      val.personList.forEach(ele => {
        let obj = {
          text: ele.mattersName,
          imgUrl: ele.picUrl
        }
        this.personContent.push(obj)
      })
      // this.unitContent = val.unitList;
      // this.personContent = val.personList;
    },
    clearIcon() {
      this.iconList = []
    }
  }
}
</script>

<style lang="less" scoped>
.indexPageConfig{
    background-color: #fff;
    overflow: auto;
    display: flex;
}
</style>



