<template>
  <div class="NewAreas">
    <p class="p">{{form.name}}</p>
    <!--  -->
    <div class="baseInfo clearfix">
      <p></p>
      <el-form :model="form">
        <el-form-item label="ID: " :label-width="formLabelWidth" style="width:49%;float:left">
          <el-input style="width:70%;" disabled v-model="form.id" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label=": " :label-width="formLabelWidth" style="width:49%;float:left">
          <el-input style="width:70%;" disabled placeholder="" v-model="form.shortName"></el-input>
        </el-form-item>
        <el-form-item label=": " :label-width="formLabelWidth" style="width:49%;float:left">
          <el-input style="width:70%;" disabled placeholder="" v-model="form.shortName"></el-input>
        </el-form-item>
        <el-form-item label=": " :label-width="formLabelWidth" style="width:49%;float:left">
          <el-input style="width:70%;" disabled placeholder="" v-model="form.createTime"></el-input>
        </el-form-item>
        <el-form-item label=": " :label-width="formLabelWidth" style="width:49%;float:left">
          <el-input
            style="width:70%;"
            disabled
            placeholder=""
            v-model="form.iconCategoryName"
          ></el-input>
        </el-form-item>
        <el-form-item label=": " :label-width="formLabelWidth" style="width:49%;float:left">
          <el-cascader
            :options="options"
            v-model="areaList"
            style="width:70%;"
            :show-all-levels="false"
            @change="getArea"
          ></el-cascader>
        </el-form-item>
        <el-form-item label=": " :label-width="formLabelWidth" style="width:86%;float:left">
          <el-input type="textarea" style="width:100%;" v-model="form.desc"></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div class="configInfo">
      <p></p>
      <el-form :model="appBlock">
        <el-form-item label=": " :label-width="formLabelWidth" style="width:100%;float:left">
          <el-checkbox label="" v-model="appChecked" name="type" @change="checkApp"></el-checkbox>
        </el-form-item>
        <div v-if="appVisible">
          <div class="ListLine">
            <p style="text-indent:60px;color:red;margin-bottom: 20px;">jpgjpeg,5M</p>
            <div class="PhotoBox">
              <svg-icon @click="choosePhoto('app')" icon-class="plusPhoto"></svg-icon>
              <img :src="appBlock.picUrl" v-if="appBlock.picUrl != ''" @click="choosePhoto('app')" />
              <input
                accept="image/jpeg, image/jpg"
                id="photoInput"
                @change="uploadFile($event)"
                type="file"
                name="file"
                style="display:none"
              />
            </div>
          </div>
          <el-form-item label=": " :label-width="formLabelWidth" style="width:49%;float:left">
            <el-input style="width:70%;" placeholder="" v-model="appBlock.jumpUrl"></el-input>
          </el-form-item>
          <el-form-item label=": " :label-width="formLabelWidth" style="width:49%;float:left">
            <el-input style="width:70%;" placeholder="" v-model="appBlock.sortNumber"></el-input>
          </el-form-item>
          <el-form-item label=": " :label-width="formLabelWidth" style="width:49%;float:left">
            <el-radio-group v-model="appBlock.status">
              <el-radio :label="1"></el-radio>
              <el-radio :label="2"></el-radio>
            </el-radio-group>
          </el-form-item>
        </div>
      </el-form>
      <el-form :model="pcBlock">
        <el-form-item label=": " :label-width="formLabelWidth" style="width:100%;float:left">
          <el-checkbox label="" v-model="pcChecked" name="type" @change="checkPc"></el-checkbox>
        </el-form-item>
        <div v-if="pcVisible == true">
          <div class="ListLine" style="padding-top: 120px;">
            <p style="text-indent:60px;color:red;margin-bottom: 20px;">jpgjpeg,5M</p>
            <div class="PhotoBox">
              <svg-icon @click="choosePhoto('pc')" icon-class="plusPhoto"></svg-icon>
              <img :src="pcBlock.picUrl" v-if="pcBlock.picUrl != ''" @click="choosePhoto('pc')" />
            </div>
          </div>
          <el-form-item label=": " :label-width="formLabelWidth" style="width:49%;float:left">
            <el-input style="width:70%;" placeholder="" v-model="pcBlock.jumpUrl"></el-input>
          </el-form-item>
          <el-form-item label=": " :label-width="formLabelWidth" style="width:49%;float:left">
            <el-input style="width:70%;" placeholder="" v-model="pcBlock.sortNumber"></el-input>
          </el-form-item>
          <el-form-item label=": " :label-width="formLabelWidth" style="width:49%;float:left">
            <el-radio-group v-model="pcBlock.status">
              <el-radio :label="2"></el-radio>
              <el-radio :label="1"></el-radio>
            </el-radio-group>
          </el-form-item>
        </div>
      </el-form>
    </div>
    <div class="btns">
      <el-button type="primary" @click.stop="save"></el-button>
      <el-button type="success" @click.stop="cancel"></el-button>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      form: {},
      areaList: [330100, 330199], // 
      appBlock: {
        // 
        jumpUrl: "", // 
        picUrl: "", // url
        sortNumber: 0, // 
        status: 2 // 12
      },
      pcBlock: {
        // 
        jumpUrl: "",
        picUrl: "",
        sortNumber: 0,
        status: 2
      },
      areaId: 0, // ID
      mattersId: 0, // ID
      sittersAreaId: 0, // 
	  uploadMethod: "", // 
	  modified: false, // 
	  appChecked: false, // 
	  pcChecked: false, // 
      formLabelWidth: "120px",
      options: [],
      appVisible: false,
      pcVisible: false,
      areaId: "330199"
    }
  },
  created() {
    this.form = JSON.parse(sessionStorage.getItem("MattersConfigItem"))
    this.form.createTime = this.util.formatDate(new Date(this.form.createTime), "yyyy-MM-dd")
    console.log("", this.form)
    // 
    if (this.form.areaId != undefined) {
      this.modified = true
      this.getAreaDetail()
    }
    //  
    this.getGinsengLand()
  },
  watch: {
    // 
    areaList(val) {
      console.log(val)
    }
  },
  methods: {
    getArea(val) {
      console.log("---val", val[1])
      this.areaId = val[1]
    },
    choosePhoto(method) {
      this.uploadMethod = method
      document.getElementById("photoInput").click()
    },
    // 
    uploadFile(e) {
      // 
      if (e.target.files[0] == undefined) {
        return false
      }
      // 
      let fileSize = e.target.files[0].size
      if (fileSize > 5242880) {
        this.$message({
          message: "5M",
          type: "warning"
        })
        return false
      }
      // formdata
      var params = new FormData()
      params.append("photoFile", e.target.files[0])
      this.$post("/ybapp/upload/uploadPhoto", params).then(resData => {
        //      1000
        if (resData.success) {
          if (this.uploadMethod == "app") {
            this.appBlock.picUrl = resData.result
          } else if (this.uploadMethod == "pc") {
            this.pcBlock.picUrl = resData.result
          }
        } else if (!resData.success) {
          this.$message({
            type: "warning",
            message: ""
          })
          return
        } else {
          return
        }
      })
    },
    save() {
      let submitForm = {}
      if (this.appChecked) {
        submitForm.appBlock = this.appBlock
      }
      if (this.pcChecked) {
        submitForm.pcBlock = this.pcBlock
      }
      // 
      if (this.modified) {
        submitForm.sittersAreaId = this.form.mattersAreaId // 
      }
      submitForm.areaId = this.areaList[1] // ID
      submitForm.mattersId = this.form.id // ID
      console.log(submitForm)
      this.$post("/ybapp/siteMattersArea/saveSiteMattersAreaAndBlock", submitForm)
        .then(resData => {
          if (resData.success) {
            console.log("", resData.result)
            this.$router.go(-1)
          } else {
            this.$message({
              type: "warning",
              message: resData.resultDes
            })
          }
        })
        .catch(error => {
          console.log(error)
        })
    },
    cancel() {
      this.$router.go(-1)
    },
    // 
    getGinsengLand() {
      var formdata = {
        bussType: 1,
        pageNum: 1,
        pageSize: 1000
      }
      this.$post("/ybapp/siteArea/tree", formdata)
        .then(resData => {
          if (resData.success) {
            console.log("", resData.result.dataList)
            resData.result.dataList.map((item, index) => {
              item.children.map((item1, index1) => {
                delete item1.children
              })
            })

            this.options = resData.result.dataList
            // 
          } else {
          }
        })
        .catch(error => {
          console.log(error)
        })
    },
    // 
    getAreaDetail() {
      let submitForm = {
        areaId: this.form.areaId,
        mattersId: this.form.id
      }
      this.$post("/ybapp/siteMattersArea/getAreaDetail", submitForm)
        .then(resData => {
          console.log(resData)
          if (resData.success) {
            this.areaName = resData.result.areaName
            this.areaList[1] = resData.result.areaId
            if (resData.result.appBlock != null) {
              this.appBlock = resData.result.appBlock
              this.appChecked = true
              this.appVisible = true
            }
            if (resData.result.pcBlock != null) {
              this.pcBlock = resData.result.pcBlock
              this.pcChecked = true
              this.pcVisible = true
            }
          } else {
          }
        })
    },
    checkPc(val) {
      let that = this
      that.pcVisible = val === true
    },
    checkApp(val) {
      let that = this
      that.appVisible = val === true
    }
  }
}
</script>
<style lang="less" scoped>
.NewAreas {
  .p {
    color: black;
    font-size: 30px;
    font-weight: 700;
  }
  .baseInfo {
    margin-top: 15px;
  }
  .configInfo {
    .ListLine {
      margin: 15px 0;
      .PhotoBox {
        position: relative;
        left: 60px;
        height: 100px;
        cursor: pointer;
        img {
          position: absolute;
          z-index: 10;
          left: 0;
          top: 0;
          height: 100px;
          width: 100px;
        }
        .svg-icon {
          height: 100px;
          width: 100px;
        }
      }
    }
  }
}
</style>



