<template>
  <div>
    <el-button class="right" v-if="formState == 'edit'" size="large"></el-button>
    <el-form ref="form" :model="sizeForm" label-width="100px" size="mini">
      <el-form-item label=":">
        <el-col :span="12">
          <el-input v-model="sizeForm.name"></el-input>
        </el-col>
      </el-form-item>
      <el-form-item label=":">
        <p style="font-size: 12px; color: #888;">jpgpng2M5</p>
        <div class="ListLine">
          <div class="PhotoBox">
            <div class="photo" v-for="(item,index) in sizeForm.photoList" :key="index">
              <img  :src="item"/>
              <svg-icon icon-class="delete" @click="deletePhoto(item,index)"></svg-icon>
            </div>
            <svg-icon @click="choosePhoto()" icon-class="plusPhoto"></svg-icon>
            <input
              accept="image/jpeg, image/jpg"
              id="photoInput"
              @change="uploadFile($event)"
              type="file"
              name="file"
              style="display:none"
            />
          </div>
        </div>
      </el-form-item>
      <el-form-item label=":">
        <el-radio-group v-model="sizeForm.publish" @change="changePublish" size="medium">
          <el-radio label="1"></el-radio>
          <el-radio label="2"></el-radio>
          <span v-if="btnStatus == false">
            <span class="demonstration"></span>
            <el-date-picker
              @change="selectTime"
              v-model="value3"
              type="datetime"
              placeholder=""
              default-time="12:00:00">
            </el-date-picker>
          </span>
        </el-radio-group>
      </el-form-item>
      <el-form-item label=":">
        <el-radio-group v-model="sizeForm.visibility" @change="changeType" size="medium">
          <el-radio label="1"></el-radio>
          <el-radio label="2"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" v-if="btnType == false">
        <el-checkbox-group v-model="sizeForm.check">
          <el-checkbox  label="1" @change="checkUnit"></el-checkbox>
          <el-checkbox v-model="sizeForm.person1" label="2" @change="checkPerson"></el-checkbox>
        </el-checkbox-group>
        <div>
          <el-cascader
            placeholder=""
            v-if="btnUnit == true"
            :options="sizeForm.unitList1"
            v-model="sizeForm.unitList"
            :props="props"
            @change="getArea"
            clearable
          ></el-cascader>
        </div>
        <div>
          <el-cascader
            placeholder=""
            v-if="btnPerson == true"
            v-model="sizeForm.personList"
            :options="sizeForm.personList1"
            :props="props"
            @change="getPerson"
            clearable
          ></el-cascader>
        </div>
      </el-form-item>
      <el-form-item label=":" prop="words">
        <div class="edit_container">
          <quill-editor
            v-model="synopsis"
            ref="myQuillEditor"
            :options="editorOption"
            @blur="onEditorBlur($event)" @focus="onEditorFocus($event)"
            @change="onEditorChange($event)">
          </quill-editor>
        </div>
      </el-form-item>
      <el-form-item size="large">
        <el-button type="primary" @click="onSubmit"></el-button>
        <el-button @click="back"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      props: {
        multiple: true
      },
      limitNum: "6",
      sizeForm: {
        unitEdit: [], // 
        personEdit: [], // 
        photoList: [],
        name: "",
        publish: "",
        visibility: "",
        desc: "",
        unitList: [],
        personList: [],
        unitList1: [],
        personList1: [],
        check: []
      },
      defaultMsg: "",
      config: {
        initialFrameWidth: null,
        initialFrameHeight: 350
      },
      synopsis: "",
      editorOption: {
        placeholder: ""
      },
      dialogImageUrl: "",
      dialogVisible: false,
      formLabelWidth: "80px",
      limitNum: 3,
      formState: "",
      pickerOptions: {
        text: "",
        onClick(picker) {
          const date = new Date()
          date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
          picker.$emit("pick", date)
        }
      },
      value3: "",
      btnStatus: true,
      btnType: true,
      btnUnit: false,
      btnPerson: false,
      time: "",
      arr1: [], // 
      arr2: [], // ,
      changeArr: [],
      editState: false, // 
      editUnit: [], // 
      editPerson: [] // 
    }
  },
  created() {
    this.getGinsengLand()
    this.formState = this.$route.query.formState
    let editForm = this.$route.query.param
    console.log("edit---:", editForm)
    if (this.formState === "edit") {
      this.editState = true
      this.sizeForm.photoList = editForm.photolist
      this.sizeForm.name = editForm.newsSynopsis.name
      this.synopsis = editForm.newsSynopsis.synopsis
      this.time = editForm.newsSynopsis.publishTime
      this.sizeForm.id = editForm.newsSynopsis.id
      this.value3 = new Date(this.time)
      if (editForm.visibility == "2") {
        this.btnType = false
      } else {
        this.btnType = true
      }
      if (this.btnType == false) {
        this.btnUnit = true
        this.btnPerson = true
        if (editForm.unitList.length > 0) {
          this.sizeForm.check.push("1")
          this.editUnit = editForm.unitList
        }
        if (editForm.personList.length > 0) {
          this.sizeForm.check.push("2")
          this.editPerson = editForm.personList
        }
      }
      if (editForm.publish == "1") {
        this.sizeForm.publish = "1"
      } else if (editForm.publish == "2") {
        this.sizeForm.publish = "2"
        this.btnStatus = false
      }
      if (editForm.visibility == "1") {
        console.log("-----visib", editForm.visibility)
        this.sizeForm.visibility = "1"
      } else if (editForm.visibility == "2") {
        this.sizeForm.visibility = "2"
      }
      console.log("----sizeForm1122", this.$route.query.param)
      console.log("---id", this.sizeForm.id)
    }
  },
  computed: {
    editor() {
      return this.$refs.myQuillEditor.quill
    }
  },
  methods: {
    getArea(val) {
      this.getChange(val)
      this.arr1 = this.changeArr
      if (this.editState == true) {
        this.sizeForm.check.push("1")
      }
    },
    getChange(val) {
      this.changeArr = []
      val.forEach(ele => {
        for (let item of this.sizeForm.unitList1) {
          if (ele[0] == item.value) {
            for (let innerItem of item.children) {
              if (ele[1] == innerItem.value) {
                let obj = {
                  id: innerItem.value,
                  name: innerItem.label
                }
                this.changeArr.push(obj)
                break
              }
            }
            break
          }
        }
      })
      return this.changeArr
      console.log("changeArr:", this.changeArr)
    },
    getPerson(val) {
      this.getChange(val)
      this.arr2 = this.changeArr
      if (this.editState == true) {
        this.sizeForm.check.push("2")
      }
      console.log("arr2:", this.arr2)
    },
    onEditorBlur() {
    }, // 
    onEditorFocus(event) {
    }, // 
    onEditorChange() {
    }, // 
    onSubmit() {
      let newsSynopsis = {
        "name": this.sizeForm.name,
        "publishTime": this.sizeForm.publish == 2 ? this.value3 : "",
        "synopsis": this.synopsis
      }
      console.log("----pub2", this.sizeForm.publish)
      if (this.formState == "edit") {
        newsSynopsis.id = this.sizeForm.id
      }
      let submitArr = {
        "newsSynopsis": newsSynopsis,
        "photolist": this.sizeForm.photoList,
        "publish": this.sizeForm.publish,
        "visibility": this.sizeForm.visibility,
        "unitList": this.formState == "edit" ? this.getChange(this.sizeForm.unitList) : this.arr1,
        "personList": this.formState == "edit" ? this.getChange(this.sizeForm.personList) : this.arr2
      }
      this.$post("/ybapp/news/saveNews", submitArr).then((resData) => {
        if (resData.success) {
          console.log("", resData.result)
        }
      }).catch((error) => {
        console.log(error)
      })

      this.$router.push("/home/NewsManagement")
    },
    choosePhoto() {
      document.getElementById("photoInput").click()
    },
    // 
    uploadFile(e) {
      console.log("upload")
      // 
      if (e.target.files[0] == undefined) {
        return false
      }
      // 
      let fileSize = e.target.files[0].size
      if (fileSize > 5242880) {
        this.$message({
          message: "5M",
          type: "warning"
        })
        return false
      }
      console.log("event;", e)
      let formData = new FormData()
      formData.append("photoFile", e.target.files[0])
      this.$axios.post("/ybapp/upload/uploadPhoto",
        formData
      )
        .then(res => {
          console.log("", res)
          this.sizeForm.photoList.push(res.data.result)
          console.log("---photoList:", this.sizeForm.photoList)
        })
        .catch(err => {
          console.log("error:", err)
        })
    },
    back() {
      this.$router.go(-1)
    },
    // 
    getGinsengLand() {
      var formdata = {
        bussType: 1,
        pageNum: 1,
        pageSize: 1000
      }
      this.$post("/ybapp/siteArea/tree", formdata)
        .then(resData => {
          if (resData.success) {
            console.log("", resData.result.dataList)
            resData.result.dataList.map((item, index) => {
              item.children.map((item1, index1) => {
                delete item1.children
              })
            })

            this.sizeForm.unitList1 = resData.result.dataList
            this.sizeForm.personList1 = resData.result.dataList
            if (this.editState == true) {
              console.log(this.editUnit)
              this.editUnit.forEach(e => {
                for (let i of this.sizeForm.unitList1) {
                  for (let k of i.children) {
                    if (e.id == k.value) {
                      let obj = []
                      obj[0] = i.value
                      obj[1] = k.value
                      this.sizeForm.unitList.push(obj)
                    }
                  }
                }
              })
              this.editPerson.forEach(e => {
                for (let i of this.sizeForm.personList1) {
                  for (let k of i.children) {
                    if (e.id == k.value) {
                      let obj = []
                      obj[0] = i.value
                      obj[1] = k.value
                      this.sizeForm.personList.push(obj)
                    }
                  }
                }
              })
              console.log("newList1:", this.sizeForm.unitList)
              console.log("newList2:", this.sizeForm.personList)
            }
            // 
          } else {
          }
        })
        .catch(error => {
          console.log(error)
        })
    },
    // 
    changePublish(val) {
      let that = this
      that.btnStatus = (val === "1")
      console.log("-----pub1", this.sizeForm.publish)
    },
    // 
    selectTime() {
      // this.value3 = this.util.formatDate(new Date(this.value3), 'yyyy-MM-dd hh:mm:ss');
      console.log("-----datetime", this.value3)
    },
    // 
    changeType(val) {
      let that = this
      that.btnType = (val === "1")
      console.log("-----type", this.sizeForm.visibility)
      if (this.sizeForm.visibility === "1") {
        this.sizeForm.unitList = []
        this.sizeForm.personList = []
      }
    },
    // 
    checkUnit(val) {
      let that = this
      that.btnUnit = (val === true)
      if (val === false) {
        this.sizeForm.unitList = []
      }
    },
    checkPerson(val) {
      let that = this
      that.btnPerson = (val === true)
      if (val === false) {
        this.sizeForm.personList = []
      }
    },
    //  
    deletePhoto(item, index) {
      this.sizeForm.photoList.splice(index, 1)
    }
  }
}
</script>

<style lang="less" scoped>
  .right {
    float: right;
    background-color: orange;
    color: white;
  }
  .ListLine {
    margin: 0px;
    padding: 0px;
    .PhotoBox {
      position: relative;
      left: 0px;
      top: 0px;
      height: 100px;
      display: flex;
      .photo{
        position: relative;
        height: 100px;
        width: 100px;
        margin-right: 10px;
        img {
          height: 100%;
          width: 100%;
        }
        .svg-icon{
          height: 20px;
          width: 20px;
          position: absolute;
          top: -5px;
          right: -5px;
          cursor: pointer;
        }
      }
      .svg-icon {
        height: 100px;
        width: 100px;
      }
    }
  }
  .edit_container {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    /deep/.ql-container {
      height: 300px;
    }
  }
</style>



