import Vue from "vue"
import axios from "axios"
import { Loading,
  Message
} from "element-ui"

// import { log } from "util"
// import { trimExt } from 'upath';
axios.defaults.timeout = 500000
axios.defaults.header = "Access-Control-Allow-Origin"
// axios.defaults.baseURL = "/test"
axios.defaults.baseURL = "/api"
// axios.defaults.baseURL = ""
let loadingInstance
// 
axios.interceptors.request.use(function(config) {
  // console.log("", config)
  let q = config.url.split("/").pop()
  if (q.indexOf("save") != "-1" || q.indexOf("add") != "-1" || q.indexOf("manualAssign") != "-1" || q.indexOf("check") != "-1" || q.indexOf("reject") != "-1") {
    loadingInstance = Loading.service({
      lock: true,
      text: "...",
      spinner: "el-icon-loading",
      background: "rgba(0, 0, 0, 0.7)"
    })
  }
  // 
  if (config.url == "/static/menuData.json") {
    config.baseURL = ""
    config.url = "/static/menuData.json"
  }
  return config
}, function(error) {
  // 
  Message({
    message: "",
    type: "warning"
  })
  if (loadingInstance) { loadingInstance.close() }
  return Promise.reject(error)
})

// 
axios.interceptors.response.use(function(response) {
  // 
  // if (response.request.responseURL.indexOf("login.html?") < 0) {
  //   Message({
  //     message: "",
  //     type: "warning"
  //   })
  //   location.href = "/"
  // }
  // console.log(loadingInstance)
  if (loadingInstance) {
    loadingInstance.close()
  }
  if (response.hasOwnProperty("data") && response.data.hasOwnProperty("result") && response.data.result != null && response.data.result.hasOwnProperty("opseno") && response.data.result.opseno && response.data.result.opseno != "0") {
    Vue.prototype.$creatHtml(response.data.result.opseno)
  }
  if (typeof (response.data) == "string") {
    if (response.data.trim() == "") {
      if (response.request.responseURL.indexOf("getAllAreaTreeNodes") == -1) {
        Message({
          message: "",
          type: "warning"
        })
      }
    }
  }
  return response
}, function(error) {
  // 
  if (loadingInstance) {
    loadingInstance.close()
  }
  Message({
    message: "",
    type: "warning"
  })
  return Promise.reject(error)
})

/**
 * post
 * @param url
 * @param data
 * @returns {Promise}
 */

export function post(url, data = {}) {
  return new Promise((resolve, reject) => {
    axios.post(url, data)
      .then(response => {
        resolve(response.data)
      }, err => {
        reject(err)
      })
  })
}

/**
 * get
 * @param url
 * @param data
 * @returns {Promise}
 */

export function get(url, params = {}) {
  return new Promise((resolve, reject) => {
    axios.get(url, {
      params: params
    })
      .then(response => {
        resolve(response)
      })
      .catch(err => {
        reject(err)
      })
  })
}

/**
 * delete
 * @param url
 * @param data
 * @returns {Promise}
 */

export function deleteUrl(url, params = {}) {
  return new Promise((resolve, reject) => {
    axios.delete(url, {
      data: params
    })
      .then(response => {
        resolve(response)
      })
      .catch(err => {
        reject(err)
      })
  })
}

/**
 * patch
 * @param url
 * @param data
 * @returns {Promise}
 */

export function patchUrl(url, params = {}) {
  return new Promise((resolve, reject) => {
    axios.patch(url, {
      params: params
    })
      .then(response => {
        resolve(response)
      })
      .catch(err => {
        reject(err)
      })
  })
}



