<template>
  <span @click="open">
    <slot></slot>
  </span>
</template>

<script>
export default {
  props: {
    id: [String, Number],
    url: String,
    parmeId: {
      default: "userId",
      type: String
    },
    info: {
      default: "",
      type: String
    },
    init: {
      default: false,
      type: Boolean
    }
  },
  methods: {
    open() {
      this.$confirm("" + this.info, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$get(this.url, {
            [this.parmeId]: this.id
          }).then(data => {
            if (data.data.success) {
              this.$message({
                type: "success",
                message: data.data.resultDes
              })
              if (this.init) {
                this.$emit("init")
              }
            }
            if (!data.data.success) {
              this.$message({
                type: "error",
                message: data.data.resultDes
              })
            }
          })
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          })
        })
    }
  }
}
</script>

<style scoped lang="css">
</style>



