<template>
  <div class="indexPageConfig">
    <iframe class="TaskManagement_iframe" :src="iframeUrl" frameborder="0"></iframe>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      iframeUrl: "http://10.85.94.57:8090/#/page/TaskManagement?isHide=true"
    }
  },
  methods: {
    clearIcon() {
      this.iconList = []
    }
  }
}
</script>

<style lang="less" scoped>
.indexPageConfig,
.TaskManagement_iframe{
  border: 0 none;
  width: 100%;
  height:100%;
}
body .el-main{
  padding: 0;
}
</style>



