<script>
import dict from "../../utils/Dictionaries.js"
export default {
  data() {
    var validateTel = (rule, value, callback) => {
      let pattern = /0?(13|14|15|17|18|19)[0-9]{9}/
      let judge = true
      if (!pattern.test(value)) {
        judge = false
      }
      if (!judge) {
        callback(new Error(""))
      } else {
        callback()
      }
    }
    var validatePass = (rule, value, callback) => {
      if (value !== this.userInfo.passwd) {
        callback(new Error("!"))
      } else {
        callback()
      }
    }
    return {
      ls_userType: "", //  userType
      dict_userType: [], // 
      // 
      uploadData: {
        isPublic: "1"
      },
      // departmentList: [], // 
      defaultCheck: [], // 
      defaultExpand: [], // 
      areaProps: {
        // 
        label: "areaName"
      },
      areaEdit: false, // 
      canEdit: false, // 
      pickerOptions1: {
        disabledDate(time) {
          return time.getTime() <= Date.now()
        }
      },
      orgTableData: [
        {
          orgId: [],
          departmentId: [],
          departmentList: [],
          roleList: [],
          transferList: []
        }
      ],
      disabledOrgId: false,
      treeData: [],
      areaList: [],
      pwTime: true,
      orgIdOptions: [],
      props: {
        label: "orgName",
        value: "id"
      },
      props1: {
        label: "regionCode",
        value: "id"
      },
      formInline: {
        logonName: "",
        displayName: "",
        orgId: [],
        userState: "",
        userType: ""
      },
      dialogFormRules: {
        logonName: [
          { required: true, message: "", trigger: "blur" }
        ],
        displayName: [
          { required: true, message: "", trigger: "blur" }
        ],
        userType: [
          { required: true, message: "", trigger: "blur" }
        ],
        orgName: [
          { required: true, message: "", trigger: "blur" }
        ],
        // 
        departmentId: [
          { required: true, message: "", trigger: "blur" }
        ],
        orgId: [{ required: true, message: "", trigger: "blur" }],
        passwd: [{ required: true, message: "", trigger: "blur" }],
        cardId: [
          { required: true, message: "", trigger: "blur" }
        ],
        tel: [
          { required: true, message: "", trigger: "blur" },
          { validator: validateTel, trigger: "blur" }
        ],
        checkPass: [
          { required: true, message: "", trigger: "blur" },
          { validator: validatePass, trigger: "blur" }
        ],
        // roleList: [
        //   { required: true, message: "", trigger: "change" }
        // ],
        orgIds: [
          { required: true, message: "", trigger: "change" }
        ]
      },
