  },
  created() {
    this.ls_userType = this.$Base64.decode(localStorage.getItem("userType"))

    // console.log('this.ls_userType',this.ls_userType);
    // console.log('dict.dict_userType',dict.dict_userType);

    const _distArr = []
    switch (this.ls_userType) {
      case "1":
        // 
        this.userType_dfval = "2"
        this.userInfo.userType = "2"
        dict.dict_userType.forEach(function(item) {
          let _item = JSON.parse(JSON.stringify(item))
          _item.queryVisible = true
          if (item.id === "2") { _item.addVisible = true }
          if (item.id === "3") { _item.addVisible = true }
          _distArr.push(_item)
        })
        this.dict_userType = _distArr
        break
      case "2":
        // 
        this.userType_dfval = "3"
        this.userInfo.userType = "3"

        dict.dict_userType.forEach(function(item) {
          let _item = JSON.parse(JSON.stringify(item))
          if (item.id === "3") {
            _item.queryVisible = true
            _item.addVisible = true
          }
          if (item.id === "4") { _item.queryVisible = true }
          _distArr.push(_item)
        })
        this.dict_userType = _distArr
        break
      case "3":
        // 
        this.userType_dfval = "4"
        this.userInfo.userType = "4"

        dict.dict_userType.forEach(function(item) {
          let _item = JSON.parse(JSON.stringify(item))
          if (item.id === "4") {
            _item.queryVisible = true
            _item.addVisible = true
          }
          _distArr.push(_item)
        })
        this.dict_userType = _distArr
        break
    }
    // console.log(this.dict_userType);

    this.initTable()
    this.$get("/sys/org/queryTree").then(data => {
      if (data.data.success) {
        this.treeData = data.data.result
      }
    })
  },
  mounted() {},
  computed: {
    // 
    downloadUrl() {
      let url = "/api/test/exportuser?"
      for (let item in this.formInline) {
        if (item === "orgId") {
          if (this.formInline[item].length > 0) {
            url = url + "orgId=" + this.formInline[item][this.formInline[item].length - 1] + "&"
          }
        } else if (this.formInline[item] !== "") {
          url = url + item + "=" + this.formInline[item] + "&"
        }
      }
      return url.substring(0, url.length - 1)
    },
    tableHeight() {
      return window.innerHeight - 230
    }
  },
