      // console.log(file.raw)
      // file.response.result.key
      this.avatarUrl = "http://10.85.94.238:10540/openapiApp/download?key=" + res.result.key
      this.userInfo.headUrl = res.result.key
      // this.pcuserInfo.headUrl = URL.createObjectURL(file.raw)
    },
    // 
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg" || "image/png"
      // const isLt2M = file.size / 1024 / 1024 < 2
      if (!isJPG) {
        this.$message.error(" JPGPNG !")
      }
      // if (!isLt2M) {
      //   this.$message.error(' 2MB!')
      // }
      return isJPG
    },
    // //departChange
    // resquestDepartList(orgId) {
    //   let params = {
    //     orgId: Number(orgId)
    //   }
    //   this.$post("/sysOrgDepartment/list", params).then(res => {
    //     if (res.success) {
    //       this.departmentList = res.result
    //     } else {
    //       this.$mess(res.data.msg)
    //     }
    //   })
    // },
    // 
    isLastRow(row) {
      if (row === this.userInfo.orgList.length - 1) {
        return true
      }
      return false
    },
    roleAdd(row) {
      // console.log(row)
    },
    addOrg(row) {
      this.userInfo.orgList.push({
        orgId: [],
        roleList: [],
        areaId: "",
        departmentId: [],
        departmentList: [],
        transferList: []
      })
    },
    minusOrg(row, index) {
      this.userInfo.orgList.splice(index, 1)
    },
    initTable(back) {
      this.$get("sys/user/queryTable", {
        page: this.page,
        size: this.size,
        ...this.formInline,
        orgId: back
          ? ""
          : this.formInline.orgId.length
            ? this.formInline.orgId[this.formInline.orgId.length - 1]
            : ""
      }).then(data => {
        this.tableData = data.data.pageList.result
        this.settingTotal = Number(data.data.count)
        // this.user = data.data.user
        // console.log('user:', data.data.user);
        // this.userInfo.userType = this.user.userType == "1" ? "3" : "4"
      })
    },
    objectSpanMethod({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 1 || columnIndex === 0) {
        if (rowIndex % 2 === 0) {
          return {
            rowspan: 2,
            colspan: 1
          }
        } else {
          return {
            rowspan: 0,
            colspan: 0
          }
        }
      }
    },
    closeDialog(formRule) {
      this.$refs[formRule].resetFields()
    },
    userDateChange(value) {
      this.userInfo.userExpireDate = value
    },
    pwChange(val) {
      if (val == 3) {
        this.pwTime = false
      } else {
        this.pwTime = true
        this.userInfo.pwExpireDate = ""
      }
    },
    // 
    orgIdChange(row) {
      //  areaId 
      let pOrgId = row.orgId[row.orgId.length - 2]
      function recursion(arr) {
        for (var item in arr) {
          if (arr[item].children) {
            recursion(arr[item].children)
          }
          if (arr[item].id == pOrgId) {
            row.areaId = arr[item].regionCode
            break
          }
        }
      }
      recursion(this.orgNameOptions)

      // reset
      const lastVal = row.orgId[row.orgId.length - 1]

      this.$get("sys/user/getRoleTreeNodes", {
        orgId: lastVal,
        userType: this.userInfo.userType
      }).then(data => {
        if (data.data.success) {
          row.roleList = []
          row.transferList = data.data.result
        }
      })

      // reset
      this.$post("/sysOrgDepartment/list", {
        orgId: lastVal
      }).then(data => {
        if (data.success) {
          row.departmentId = []
          row.departmentList = data.result
        }
      })
    },
    dialogSave(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          // if (!this.checkList.length) {
          //   this.$message({
          //     type: "error",
          //     message: ""
          //   });
          //   return;
          // }

          if (!this.userInfo.userType) {
            this.$message({
              type: "error",
              message: ""
            })
            return
          }

          let _orgTableData = JSON.parse(JSON.stringify(this.userInfo.orgList))

          let __arr = []
          // 
          for (let index in _orgTableData) {
            let ids = _orgTableData[index].orgId
            _orgTableData[index].orgId = ids[ids.length - 1]

            let departmentId = _orgTableData[index].departmentId
            __arr = __arr.concat(departmentId)
            _orgTableData[index].departmentId = departmentId.join()

            // 
            delete _orgTableData[index].transferList
            delete _orgTableData[index].departmentList
          }

          this.userInfo.department = __arr.join()

          const params = {
            ...this.userInfo,
            orgList: _orgTableData,
            areaIds: this.$refs.areaList.getCheckedKeys(),
            areaId: this.userInfo.orgName[this.userInfo.orgName.length - 1]
          }
          if (this.dialogTitle == "") {
            if (params.userExpireDate) {
              params.userExpireDate = new Date(params.userExpireDate)
            }
            if (params.pwExpireDate) {
              params.pwExpireDate = new Date(params.pwExpireDate)
            }
          }

          this.$post("sys/user/doSave", params).then(data => {
            if (data.success) {
              this.dialogFormVisible = false
              this.initTable(true)
            }
            this.$mess(data)
          })
        } else {
          return false
        }
      })
    },
    clearableChange(val) {
      this.formInline.userState = val === null ? "" : val
    },
    clearableChangeUserTpye(val) {
      this.formInline.userType = val === null ? "" : val
    },
    transfeChange(value, direction, movedKeys) {},

    handleChangeOrgName(val, isAdd = false) {
      this.defaultCheck = [val[val.length - 1]]
      //  id
      if (val.length === 1 && this.areaNameOptions[0].children) {
        this.defaultCheck = this.areaNameOptions[0].children.map(item => {
          return item.id
        })
      }
      // console.log(this.defaultCheck, '3333')
      this.defaultExpand =
        val.length == 3 ? [val[val.length - 2]] : [val[val.length - 1]]
      this.userInfo.orgId = ""
      this.userInfo.orgName = val
      if (this.userInfo.orgName.length < 3 && val[1] != "339900") {
        this.disabledOrgId = true
        this.userInfo.orgId = ""
      } else {
        this.disabledOrgId = false
      }
      this.$get("sys/user/getOrgTreeNodes", {
        areaId: val[val.length - 1]
      }).then(data => {
        if (data.data.success) {
          this.userInfo.orgId = ""
          this.orgIdOptions = data.data.result[0].children
        }
      })

      this.$get("sys/user/getAreaList", {
        areaId: val.length == 1 ? val[0] : val[val.length - 2]
      }).then(data => {
        if (data.data.success) {
          let dg = arr => {
            for (var item in arr) {
              if (arr.length == 1) {
                arr[0].disabled = true
                // console.log(arr[0])
              }
              if (arr[item].children) {
                dg(arr[item].children)
              }
              if (this.defaultCheck.includes(arr[item].id)) {
                arr[item].disabled = true
                if (arr[item].children) {
                  for (let i in arr[item].children) {
                    arr[item].children[i].disabled = true
                    this.defaultCheck.push(arr[item].children[i].id)
                  }
                }
              }
            }
          }
          // if (!isAdd) {
          dg(data.data.result)
          // }
          // 
          this.areaList = data.data.result
          this.checkList = []
          this.checkList.push(val[val.length - 1])
        }
      })
    },
    onSearch() {
      this.page = 1
      this.initTable()
    },
    onAddUser() {
      if (this.formInline.orgId.length === 0) {
        this.$message({ type: "error", message: "" })
        return
      }
      // console.log(this.userInfo.orgList[0])
      // return
      this.avatarUrl = ""
      this.areaEdit = false
      this.transferData = []
      this.areaList = []
      this.checkList = []
      this.userInfo.orgName = []
      this.userInfo = {
        orgName: [],
        // 
        headUrl: "",
        logonName: "",
        displayName: "",
        userType: this.userType_dfval,
        checkPass: "",
        passwd: "",
        userExpireDate: "",
        pwExpireType: "",
        pwExpireDate: "",
        cardType: "",
        cardId: "",
        tel: "",
        department: "",
        shortName: "",
        orgId: "",
        orgList: [
          {
            orgId: [],
            roleList: [],
            areaId: "",
            transferList: [],
            departmentId: [],
            departmentList: []
          }
        ]
      }
      // console.log("111", this.formInline.orgId)
      // this.resquestDepartList(this.formInline.orgId[this.formInline.orgId.length - 1])
      this.userInfo.orgList[0].orgId = this.formInline.orgId
      let pOrgId = this.formInline.orgId[this.formInline.orgId.length - 2]
      let recursion = arr => {
        for (var item in arr) {
          if (arr[item].children) {
            recursion(arr[item].children)
          }
          if (arr[item].id == pOrgId) {
            this.userInfo.orgList[0].areaId = arr[item].regionCode
            break
          }
        }
      }
      recursion(this.treeData)

      this.dialogTitle = ""
      this.canEdit = true
      this.dialogFormVisible = true
      this.userInfo.orgList[0].roleList = []

      // reset
      const lastVal = this.formInline.orgId[this.formInline.orgId.length - 1]
      this.$get("sys/user/getRoleTreeNodes", {
        orgId: lastVal,
        userType: this.userInfo.userType
      }).then(data => {
        if (data.data.success) {
          this.userInfo.orgList[0].transferList = []
          this.userInfo.orgList[0].transferList = data.data.result
        }
      })
      // reset
      this.$post("/sysOrgDepartment/list", {
        orgId: lastVal
      }).then(data => {
        if (data.success) {
          this.userInfo.orgList[0].departmentId = []
          this.userInfo.orgList[0].departmentList = data.result
        }
      })

      // 
      this.$get("/sys/user/getAreaTreeNodes").then(data => {
        if (data.data.success) {
          this.areaNameOptions = data.data.result

          this.userInfo.orgName.push(data.data.result[0].id)
          if (data.data.result[0].children) {
            this.userInfo.orgName.push(data.data.result[0].children[0].id)
            if (data.data.result[0].children[0].children) {
              this.userInfo.orgName.push(data.data.result[0].children[0].children[0].id)
            }
          }
          this.handleChangeOrgName(this.userInfo.orgName, true)
        }
      })
      if (!this.orgNameOptions.length) {
        this.$get("/sys/org/queryTree").then(data => {
          if (data.data.success) {
            this.orgNameOptions = data.data.result
          }
        })
      }
    },
    handleSizeChange(val) {
      this.size = val
      this.initTable()
    },
    checkListChange(val) {
      this.checkList = val
    },
    handleCurrentChange(val) {
      this.page = val
      this.initTable()
    },
    // 
    tableEdit(index, row) {
      this.dialogTitle = ""
      this.canEdit = false
      this.areaEdit = true
      this.$get("sys/user/queryOneUser", {
        userId: row.id
      }).then(data => {
        if (data.data.success) {
          // console.log(data.data.result, "sys/user/queryOneUser")
          const result = data.data.result
          const sysUser = result.sysUser

          this.defaultCheck = []
          this.defaultExpand = []
          for (let i in result.areaList) {
            this.defaultCheck.push(result.areaList[i].areaId)
          }
          this.defaultExpand =
            result.areaArray.length == 3
              ? [result.areaArray[result.areaArray.length - 2]]
              : [result.areaArray[result.areaArray.length - 1]]
          this.userInfo = {
            // 
            headUrl: sysUser.headUrl,
            logonName: sysUser.logonName,
            displayName: sysUser.displayName,
            userType: sysUser.userType,
            checkPass: sysUser.checkPass,
            passwd: sysUser.passwd,
            userExpireDate: sysUser.userExpireDate,
            pwExpireType: sysUser.pwExpireType,
            pwExpireDate: sysUser.pwExpireDate,
            cardType: sysUser.cardType,
            cardId: sysUser.cardId,
            tel: sysUser.tel,
            department: sysUser.department ? parseInt(sysUser.department) : "",
            shortName: sysUser.shortName,
            orgId: sysUser.orgId,
            ythUserId: sysUser.ythUserId,
            orgList: [
              {
                orgId: [],
                roleList: [],
                areaId: "",
                transferList: [],
                departmentId: [],
                departmentList: []
              }
            ],
            id: sysUser.id
          }
          if (sysUser.headUrl) {
            this.avatarUrl = "http://10.85.94.238:10540/openapiApp/download?key=" + this.userInfo.headUrl
          } else {
            this.avatarUrl = ""
          }
          if (result.userRoleList.length > 0) {
            for (let i = 0; i < result.userRoleList.length - 1; i++) {
              this.userInfo.orgList.push({
                orgId: [],
                roleList: [],
                areaId: "",
                transferList: [],
                departmentId: [],
                departmentList: []
              })
            }
            for (let i = 0; i < result.userRoleList.length; i++) {
              let arr = result.userRoleList[i].idpath.split("/")
              arr.forEach(item => {
                this.userInfo.orgList[i].orgId.push(parseInt(item))
              })
            }
            for (let i = 0; i < this.userInfo.orgList.length; i++) {
              // reset
              this.$get("sys/user/getRoleTreeNodes", {
                orgId: this.userInfo.orgList[i].orgId[
                  this.userInfo.orgList[i].orgId.length - 1
                ],
                userType: this.userInfo.userType
              }).then(data => {
                if (data.data.success) {
                  this.userInfo.orgList[i].transferList = []
                  this.userInfo.orgList[i].transferList = data.data.result
                }
              })
              // reset
              this.$post("/sysOrgDepartment/list", {
                orgId: this.userInfo.orgList[i].orgId[
                  this.userInfo.orgList[i].orgId.length - 1
                ]
              }).then(data => {
                if (data.success) {
                  this.userInfo.orgList[i].departmentList = data.result
                }
              })
            }

            // table - 
            for (let i = 0; i < result.userRoleList.length; i++) {
              this.userInfo.orgList[i].roleList = result.userRoleList[i].roleIds

              if (result.orgList[i].departmentId) {
                this.userInfo.orgList[i].departmentId = result.orgList[i].departmentId.split(",").map(Number)
              }
            }
          }
          if (result.orgList.length > 0) {
            for (let i in result.orgList) {
              this.userInfo.orgList[i].areaId = result.orgList[i].areaId
            }
          }
          if (this.userInfo.pwExpireType == "3") {
            this.pwTime = false
          }

          this.dialogFormVisible = true
          this.userInfo.checkPass = sysUser.passwd
          this.userInfo.orgName = []
          data.data.result.areaArray.forEach(item => {
            this.userInfo.orgName.push(parseInt(item))
          })
          if (this.userInfo.orgName.length < 3) {
            this.disabledOrgId = true
          } else {
            this.disabledOrgId = false
          }
          this.$get("/sys/user/getAreaTreeNodes").then(data => {
            if (data.data.success) {
              this.areaNameOptions = data.data.result
            }
          })
          this.$get("sys/user/getOrgTreeNodes", {
            areaId: result.areaArray[result.areaArray.length - 1]
          }).then(data => {
            if (data.data.success) {
              this.orgIdOptions = data.data.result[0].children
            }
          })
          this.$get("/sys/org/queryTree").then(data => {
            if (data.data.success) {
              this.orgNameOptions = data.data.result
            }
          })
          this.$get("sys/user/getAreaList", {
            areaId: result.areaArray.length === 1 ? result.areaArray[0] : result.areaArray[result.areaArray.length - 2]
          }).then(data => {
            if (data.data.success) {
              // let dg = arr => {
              //   for (var item in arr) {
              //     if (arr.length == 1) {
              //       arr[0].disabled = true;
              //     }
              //     if (arr[item].children) {
              //       dg(arr[item].children);
              //     }
              //     if (this.defaultCheck.includes(arr[item].id)) {
              //       arr[item].disabled = true;
              //       if (arr[item].children) {
              //         for (let i in arr[item].children) {
              //           arr[item].children[i].disabled = true;
              //           this.defaultCheck.push(arr[item].children[i].id);
              //         }
              //       }
              //     }
              //   }
              // };
              // dg(data.data.result);
              // let disId = []
              if (result.areaArray.length > 1) {
                for (let i in data.data.result) {
                  if (data.data.result[i].id == result.areaArray[1]) {
                    data.data.result[i].disabled = true
                    if (result.areaArray.length === 2) {
                      if (result.areaArray[1] == 339900) {
                        break
                      }
                      for (let j in data.data.result[i].children) {
                        data.data.result[i].children[j].disabled = true
                        this.defaultCheck.push(data.data.result[i].children[j].id)
                      }
                    } else {
                      for (let k in data.data.result[i].children) {
                        if (data.data.result[i].children[k].id == result.areaArray[2]) {
                          data.data.result[i].children[k].disabled = true
                        }
                      }
                    }
                  }
                }
              }
              this.areaList = data.data.result
              this.checkList = []
              for (let item of result.areaList) {
                this.checkList.push(item.areaId)
              }
            }
          })
          this.$get("sys/user/getRoleTreeNodes", {
            orgId: result.sysUser.orgId,
            userType: this.userInfo.userType
          }).then(data => {
            if (data.data.success) {
              this.transferData = data.data.result
            }
          })
        }
        if (!data.data.success) {
          this.$message({
            type: "error",
            message: data.data.resultDes
          })
        }
      })
    }
  }
}
</script>

<style scoped lang="css">
/* ~  table   */
/deep/ .newUserDialogTable .el-select .el-tag__close.el-icon-close::before{
  font-size: 12px;
}

.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409EFF;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
/deep/ [class*=" el-icon-"],
[class^="el-icon-"] {
  color: #5fb9ee;
  font-size: 20px;
  cursor: pointer;
}
.pageblock {
  width: 100%;
  text-align: center;
  margin-top: 30px;
}
.iconfont {
  color: #5fb9ee;
  font-size: 19px;
  cursor: pointer;
}
.icon-jiesuo {
  font-size: 22px;
}
.searchFrom /deep/ .el-form-item {
  display: flex;
}

.roleSelect /deep/ .el-form-item__content {
  margin-left: 0 !important;
}
</style>



