<template>
  <div id="app">
    <el-form ref="searchForm" :model="searchForm">
      <el-row :gutter="20">
        <ep-input
          @input-message="onChangeSearchForm"
          colspan="5"
          :value="searchForm.sysName"
          placeholder=""
          :isIcon="true"
          size="mini"
          name="sysName"
        ></ep-input>
        <el-col :span="4">
          <el-form-item>
            <el-cascader
              clearable
              placeholder=""
              expand-trigger="hover"
              :options="cascaderOptions"
              v-model="cascaderVal"
              @change="handleChange"
              size="mini"
              :props="cascaderProps1"
              :show-all-levels="false"
              filterable
            ></el-cascader>
          </el-form-item>
        </el-col>
        <el-col :span="4">
          <el-form-item>
            <el-select
              @select-message="onChangeSearchForm"
              colspan="4"
              v-model="searchForm.sysModel"
              placeholder=""
              size="mini"
              name="sysModel"
              clearable
            >
              <el-option label="" value="1"></el-option>
              <el-option label="" value="2"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="4">
          <el-form-item>
            <el-select
              @select-message="onChangeSearchForm"
              colspan="4"
              v-model="searchForm.isAgency"
              placeholder=""
              size="mini"
              name="isAgency"
              clearable
            >
              <el-option label="" value="1"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="2" :offset="0">
          <el-form-item>
            <el-button @click="onSearch" type="primary" size="mini"></el-button>
          </el-form-item>
        </el-col>
        <el-col :span="4" :offset="1">
          <el-form-item>
            <el-button @click="beforeAdd" type="primary" size="mini"></el-button>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>

    <el-table :data="tableData" :height="tableHeight" style="width: 100%">
      <el-table-column align="center" prop="systemName" label=""></el-table-column>
      <el-table-column align="center" prop="areaName" label=""></el-table-column>
      <el-table-column align="center" label="">
        <template slot-scope="scope">
          <span>{{scope.row.accessWay == "1" ? "" : ""}}</span>
        </template>
      </el-table-column>
      <el-table-column align="center" prop="channelCode" label=""></el-table-column>
      <el-table-column align="center" prop="systemAddress" label=""></el-table-column>

      <el-table-column align="center" label="">
        <template slot-scope="scope">
          <el-button @click="editClick(scope.row)" type="text" size="small"></el-button>
          <el-button @click="deleteClick(scope.row)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <ep-page :total="total" @page="pageInit"></ep-page>


    <el-dialog
      :title="diaTitle"
      :visible.sync="asVisible"
      width="40%"
      >
      <el-form class="asyForm" :model="sysInfo" label-width="100px" :rules="dialogFormRules" ref="ruleFrom">
          <el-form-item label=":" prop="sysName">
            <el-input v-model="sysInfo.sysName" placeholder="" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label=":" prop="sysAddress">
            <el-input v-show="false"></el-input>
            <el-input v-model="sysInfo.sysAddress" placeholder="" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label=":">
            <el-cascader
              ref="cascaderArea"
              clearable
              expand-trigger="hover"
              :options="cascaderOptions"
              v-model="cascaderVal1"
              @change="handleChange1"
              :props="cascaderProps1"
              :show-all-levels="false"
              filterable
            ></el-cascader>
          </el-form-item>
          <el-form-item label=":">
            <el-radio v-model="sysInfo.sysModel" label="1"></el-radio>
            <el-radio v-model="sysInfo.sysModel" label="2"></el-radio>
          </el-form-item>
          <el-form-item label=":">
            <el-radio v-model="sysInfo.isAgency" label="1"></el-radio>
            <el-radio v-model="sysInfo.isAgency" label="0"></el-radio>
          </el-form-item>
          <el-form-item label=":">
            <el-upload
              :data="uploadData"
              list-type="picture-card"
              class="avatar-uploader"
              :action="action"
              :show-file-list="true"
              :on-success="hanleIconSuccess"
              :on-preview="hanleIconPreview"
              :on-remove="hanleIconRemove"
              :limit="1"
              :on-exceed="handleExceed"
              :before-upload="beforeIconUpload"
              :file-list="sysFileList"
            >
              <i class="el-icon-plus"></i>
              <!-- <div class="el-upload__text">icon</div> -->
            </el-upload>
            <el-dialog :visible.sync="iconDialog" appendToBody>
              <img
                width="100%"
                :src="imageUrl"
                alt=""
              >
            </el-dialog>
          </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="asVisible = false"> </el-button>
        <el-button type="primary" @click="addSys"> </el-button>
      </span>
    </el-dialog>
    <el-dialog
      :title="sysInfo.sysName + '--'"
      :visible.sync="defaultUserDia">
      <el-form
       :model="defaultUser"
       label-width="100px">
        <el-form-item label="" prop="logonName">
          <el-input :placeholder="defaultUser.logonName" readonly v-model="defaultUser.logonName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input :placeholder="defaultUser.password" readonly v-model="defaultUser.password"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="addDefaultUser('userForm')"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      defaultUserRules: {
        logonName: this.$rules(""),
        password: this.$rules("")
      },
      // 
      defaultUser: {
        logonName: "xx",
        password: "xx",
        id: ""
      },
      // 
      defaultUserDia: false,
      // 
      uploadData: {
        isPublic: "1"
      },
      // 
      imageUrl: "",
      // 
      iconDialog: false,
      // 
      sysFileList: [],
      // 
      action: "/api/fileUpload/upload",
      // 
      asVisible: false,
      // 
      sysInfo: {
        // 
        sysName: "",
        // 
        sysAddress: "",
        // id
        sysArea: "",
        // 
        sysModel: "1",
        // 
        isAgency: "1",
        // 
        areaName: "",
        // 
        icon: ""
      },
      // 
      diaTitle: "",
      // 
      total: "",
      // 
      modelOptions: [
        {text: "", value: "1"},
        {text: "", value: "2"}
      ],
      // 
      cascaderProps: {
        value: "id",
        label: "areaName",
        checkStrictly: true
      },
      cascaderProps1: {
        value: "regionCode",
        label: "orgName",
        checkStrictly: true
      },
      // 
      cascaderOptions: [],
      // ID
      cascaderVal: "",
      // ID
      cascaderVal1: "",
      // id
      selectId: "",
      // 
      tableData: [],
      // 
      dialogFormRules: {
        icon: this.$rules("icon"),
        isAgency: this.$rules(""),
        sysName: this.$rules(""),
        sysAddress: this.$rules(""),
        sysArea: this.$rules(""),
        sysModel: this.$rules("")
      },
      // 
      searchForm: {
        sysName: "",
        sysArea: "",
        sysModel: "",
        isAgency: ""
      },
      page: 1,
      size: 10
    }
  },
  created() {
    this.init(this.page, this.size)
    this.getAreaTree()
  },
  computed: {
    tableHeight() {
      return window.innerHeight - 230
    }
  },
  methods: {
    // 
    addDefaultUser(form) {
      this.defaultUserDia = false
      // this.$refs[form].validate(validate => {
      //   if (validate) {
      //     this.$post("/a/sys/user/doSave", {
      //       logonName: this.defaultUser.logonName,
      //       passwd: this.defaultUser.password,
      //       id: this.defaultUser.id,
      //       orgList: [],
      //       areaIds: [],
      //       userExpireDate: null,
      //       areaId: "",
      //       cardId: "",
      //       checkPass: this.defaultUser.password,
      //       department: "",
      //       displayName: this.defaultUser.displayName,
      //       orgId: "",
      //       tel: "",
      //       userType: this.defaultUser.userType,
      //       ythUserId: ""
      //     }).then(data => {
      //       if (data.success) {
      //         this.defaultUserDia = false
      //       }
      //       this.$mess(data)
      //     })
      //   } else {
      //     return false
      //   }
      // })
    },
    // 
    beforeIconUpload(file) {
      const isJPG = file.type === "image/jpeg" || "image/png"
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!isJPG) {
        this.$message.error(" JPGPNG !")
      }
      if (!isLt2M) {
        this.$message.error(" 2MB!")
      }
      // console.log()

      return isJPG && isLt2M
    },
    // 
    handleExceed(files, fileList) {
      // console.log("", fileList)

      this.$message.warning("")
    },
    // 
    hanleIconRemove(file, fileList) {
      // console.log(file, fileList)
      this.sysInfo.icon = ""
    },
    // 
    hanleIconPreview(file) {
      this.imageUrl = file.url
      this.iconDialog = true
    },
    // 
    hanleIconSuccess(res, file) {
      console.log(file)
      // file.response.result.key

      this.sysInfo.icon = file.response.result.key
      // this.pcimageUrl = URL.createObjectURL(file.raw)
      // this.
    },
    // 
    getAreaTree() {
      let dg = (arr, parent) => {
        for (let i = 0; i < arr.length; i++) {
          if (arr[i].children) {
            dg(arr[i].children, arr[i])
          }
          if (arr[i].type != 0) {
            arr.splice(i, 1)
            i--
          }
        }
        if (parent && parent.children && parent.children.length === 0) {
          delete parent.children
        }
      }
      this.$get("/sys/org/getAllAreaTreeNodes").then(res => {
        if (res.data.success) {
          let list = JSON.parse(JSON.stringify(res.data.result))
          dg(list)
          console.log("list:", list)
          console.log("result:", res.data.result)
          this.cascaderOptions = list
        }
      })
    },
    beforeAdd() {
      this.diaTitle = ""
      // 
      this.sysInfo.sysName = ""
      // 
      this.sysInfo.sysAddress = ""
      // id
      this.sysInfo.sysArea = ""
      // 
      this.sysInfo.sysModel = "1"
      // 
      this.sysInfo.isAgency = "1"
      // 
      this.sysInfo.areaName = ""
      // 
      this.sysInfo.icon = ""
      this.cascaderVal1 = ""
      this.sysFileList = []
      this.asVisible = true
    },
    // 
    addSys() {
      this.$refs["ruleFrom"].validate(validate => {
        if (validate) {
          let params = {
            systemName: this.sysInfo.sysName,
            areaId: this.sysInfo.sysArea.toString(),
            systemAddress: this.sysInfo.sysAddress,
            accessWay: this.sysInfo.sysModel,
            areaName: this.sysInfo.areaName,
            isAgency: this.sysInfo.isAgency,
            icon: this.sysInfo.icon
          }
          // 
          if (this.diaTitle == "") {
            params.id = this.selectId
          }

          this.$post("/manage/updateAndInsert", params).then(res => {
            if (res.success) {
              this.asVisible = false
              this.init(this.page, this.size)
              // this.defaultUserDia = true
            }
            // console.log('res:::::::',res);
            this.$mess(res)
          })
        } else {
          return false
        }
      })
    },
    // 
    init(page, size) {
      this.$post("/manage/list", {
        pageNum: page,
        pageSize: size,
        accessWay: this.searchForm.sysModel,
        areaId: this.searchForm.sysArea ? this.searchForm.sysArea.toString() : "",
        systemName: this.searchForm.sysName,
        isAgency: this.searchForm.isAgency
      }).then(data => {
        console.log(data)
        this.tableData = data.result.list
        this.total = data.result.count
      })
    },
    // 
    deleteClick(row) {
      this.$post("/manage/delete", {
        id: row.id
      }).then(res => {
        if (res.success) {
          this.init(this.page, this.size)
        }
      })
    },
    // 
    editClick(row) {
      this.selectId = row.id
      this.diaTitle = ""
      this.$post("/manage/select", {
        id: row.id
      }).then(res => {
        if (res.success) {
          this.sysInfo.sysName = res.result.systemName
          this.sysInfo.sysAddress = res.result.systemAddress
          this.sysInfo.sysArea = res.result.arrList[res.result.arrList.length - 1]
          this.cascaderVal1 = res.result.arrList
          if (this.sysInfo.sysArea == "330000") {
            this.cascaderVal1 = ["330000"]
          }
          this.sysInfo.sysModel = res.result.accessWay
          this.sysInfo.areaName = res.result.areaName
          this.sysInfo.isAgency = res.result.isAgency
          this.sysInfo.icon = res.result.icon
          this.sysFileList = []
          if (this.sysInfo.icon) {
            this.sysFileList.push({
              url: "http://10.85.94.238:10160/openapiApp/download?key=" + res.result.icon
            })
          }
          this.asVisible = true
        } else {
          this.$mess(res)
        }
      })
    },
    // 
    pageInit(page, size) {
      this.page = page
      this.size = size
      this.init(page, size)
    },
    // 
    onChangeSearchForm(data, type) {
      console.log(data)
      console.log(this.searchForm)

      this.searchForm[type] = data
      console.log(data)
    },
    // 
    onSearch() {
      this.page = 1
      this.init(this.page, this.size)
    },
    // 
    handleChange(val) {
      this.searchForm.sysArea = val[val.length - 1]
    },
    // 
    handleChange1(val) {
      console.log(val)
      this.sysInfo.sysArea = val[val.length - 1]
      let labels = this.$refs["cascaderArea"].getCheckedNodes()[0].pathLabels
      this.sysInfo.areaName = labels[labels.length - 1]
    }
  },
  watch: {}
}
</script>

<style scoped lang="less">
/deep/.el-dialog__body {
  display: flex;
  justify-content: center;
}
.page {
  display: flex;
  justify-content: flex-end;
  margin-top: 10px;
}
.black {
  display: inline-block;
  background-color: rgba(244, 148, 71, 1);
  font-weight: 400;
  color: #ffffff;
  padding: 3px;
  font-size: 12px;
}
.asyForm {
  padding: 0 10%;
  /deep/ .el-form-item__label {
    width: 120px !important;
  }
  /deep/ .el-form-item__content {
    margin-left: 120px !important;
  }
}
/* .el-table{
  height: calc(100vh - 100px);
  overflow: auto;
} */
</style>



