<script>
import dict from "../../utils/Dictionaries.js"
export default {
  inject: ["reload"],
  data() {
    return {
      ls_userType: "", //  type 
      dict_userType: [], // 
      treePopor: {
        children: "children",
        label: function(data, node) {
          if (data.orgName) {
            return data.orgName
          }
          if (data.displayName) {
            return data.displayName
          }
        }
      },
      upDataOrgId: "",
      upDataRoleId: "",
      upLoadUserList: [],
      roleSelected_userCode: [],
      userAndRoleData: [], // 
      formInline: {
        roleName: "",
        roleDesc: "",
        roleType: "",
        orgId: []
      },
      KYCXarr: [],
      KYCXsavearr: [],
      KYCXprop: {
        label: "orgName",
        children: "children"
      },
      KYCXloading: true,
      KYCXallarr: [],
      KYCXallsavearr: [],
      muToKYCXFlag: false,
      getTreeData: [],
      localSearchFlag: false,
      selectItemType: [],
      localSearchInfo: {
        itemType: null,
        row: null
      },
      showAreaName: null,
      localSearchitemTypeaRR: [],
      options: [],
      roleTitle: "",
      tableData: [],
      multipleSelection: [], // table 
      defaultSysData: [],
      defaultTreeData: [],
      treeData: [],
      sysData1: [],
      sysData2: [],
      sysData3: [],
      sysData4: [],
      sysData5: [],
      sysData6: [],
      sysData7: [],
      sysData8: [],
      sysData9: [],
      sysData10: [],
      defaultProps: {
        children: "children",
        label: "label"
      },
      orgProps: {
        children: "children",
        label: "orgName",
        value: "id",
        checkStrictly: true
      },
      props: {
        label: "name",
        children: "zones"
      },
      roleForm: {
        roleName: "",
        roleDesc: "",
        roleType: "",
        orgModel: [],
        position: ""
      },
      currentSysList: [],
      roleTotal: 1,
      dialogRules: {
        roleName: [
          { required: true, message: "", trigger: "blur" },
          { max: 20, message: "20", trigger: "blur" }
        ],
        orgModel: [{ required: true, message: "", trigger: "blur" }]
      },
      dialogFormVisible: false,
      count: 1,
      activeName: "2",
      currentPage: 1,
      dialogTableVisible: false,
      dialogTableAdd: false,
      systemName: true,
      page: 1,
      size: 10,
      treeId: "", //  id
      roleIdList: [], //  id

      editId: "",
      positionEdit: false // 
    }
  },
  created() {
    // console.log('userType###',this.dict_userType);
    this.initTable()
    this.initTree()

    this.ls_userType = this.$Base64.decode(localStorage.getItem("userType"))

    // console.log('ls_userType',ls_userType);
    const _distArr = []
    switch (this.ls_userType) {
      case "1":
        // 
        dict.dict_userType.forEach(function(item) {
          let _item = JSON.parse(JSON.stringify(item))
          _item.queryVisible = true
          if (item.id === "2") { _item.addVisible = true }
          if (item.id === "3") { _item.addVisible = true }
          _distArr.push(_item)
        })
        this.dict_userType = _distArr
        break
      case "2":
        // 
        dict.dict_userType.forEach(function(item) {
          let _item = JSON.parse(JSON.stringify(item))
          if (item.id === "3") {
            _item.queryVisible = true
            _item.addVisible = true
          }
          if (item.id === "4") {
            _item.queryVisible = true
          }
          _distArr.push(_item)
        })
        this.dict_userType = _distArr
        break
      case "3":
        // 
        this.roleForm.roleType = "4"
        dict.dict_userType.forEach(function(item) {
          let _item = JSON.parse(JSON.stringify(item))
          if (item.id === "4") {
            _item.queryVisible = true
            _item.addVisible = true
          }
          _distArr.push(_item)
        })
        this.dict_userType = _distArr
        break
    }

    this.$post("/sysTranScopeManage/tranList").then((res) => {
      if (res.success) {
        this.localSearchitemTypeaRR = res.result.map(function(item) {
          return {
            label: item.title + (item.groupName && item.groupName !== "" ? `${item.groupName}` : ""),
            value: item.id
          }
        })
        this.selectItemType.push(this.localSearchitemTypeaRR[0].value)
      }
    })
    this.$get("/sys/user/getMyUserInfo").then((res) => {
      if (res.data.success) {
        this.showAreaName = res.data.result.areaNameWithParent
      }
    })
  },
  watch: {
    roleTitle(val) {
      if (val == "") {
        this.positionEdit = true
      } else {
        this.positionEdit = false
      }
    },
    localSearchFlag(val) {
      if (val === false) {
        this.selectItemType = []
        this.localSearchInfo.row = null
        this.KYCXsavearr = []
        this.KYCXallarr = []
        this.KYCXallsavearr = []
        this.muToKYCXFlag = false
      }
    }
  },
  computed: {
    downloadUrl() {
      let url = "/api/test/exportrole?"
      for (let item in this.formInline) {
        if (item === "orgId") {
          if (this.formInline[item].length > 0) {
            url = url + "orgId=" + this.formInline[item][this.formInline[item].length - 1] + "&"
          }
        } else if (this.formInline[item] !== "") {
          url = url + item + "=" + this.formInline[item] + "&"
        }
      }
      return url.substring(0, url.length - 1)
    },
    tableHeight() {
      return window.innerHeight - 240
    }
  },
  methods: {
    CheckKYCXChange() {
      this.$nextTick(() => {
        this.KYCXallsavearr = this.$refs.KYCXtree.getCheckedKeys()
      })
    },
    localSearch(scopeRow) {
      // console.log("scopeRow:", scopeRow)
      let params = {}

      if (scopeRow) {
        this.localSearchInfo.row = scopeRow
        params.belongRoleId = scopeRow.id
      } else if (this.localSearchInfo.row) {
        params.belongRoleId = this.localSearchInfo.row.id
      }

      params.functionIds = this.selectItemType.join()

      this.KYCXloading = true
      // for (let i = 0; i < this.localSearchitemTypeaRR.length; i++) {
      this.$get("/sysTranScopeManage/sysFunctionScopeTreeForOrgs", params).then((res) => {
        if (res.data.success) {
          this.KYCXallarr = res.data.result.orgTree
          this.KYCXallsavearr = res.data.result.hasPermissionList

          this.$nextTick(() => {
            this.$refs.KYCXtree.setCheckedKeys(this.KYCXallsavearr)
          })
          this.KYCXloading = false
        } else {
          this.$message({
            type: "error",
            message: res.data.resultDes
          })
        }
      })
      // }
      this.localSearchFlag = true
      // this.getlocalSearchTree()
    },
    // 
    muToLocalSearch() {
      if (this.multipleSelection.length === 0) {
        this.$message({
          type: "warning",
          message: ""
        })
      } else {
        this.muToKYCXFlag = true
        this.localSearchInfo.itemType = this.localSearchitemTypeaRR[0].value
        this.KYCXloading = true
        // for (let i = 0; i < this.localSearchitemTypeaRR.length; i++) {
        this.$get("/sysTranScopeManage/sysFunctionScopeTreeForOrgs", {
          functionIds: this.selectItemType.join()
        }).then((res) => {
          // console.log(res)
          if (res.data.success) {
            this.KYCXallarr = res.data.result.orgTree
            this.KYCXallsavearr = res.data.result.hasPermissionList

            this.$nextTick(() => {
              this.$refs.KYCXtree.setCheckedKeys(this.KYCXallsavearr)
              this.KYCXloading = false
            })
          } else {
            this.$message({
              type: "error",
              message: res.data.resultDes
            })
          }
        })
        // }
        this.localSearchFlag = true
      }
    },
    changeKYCXxz(i) {
      console.log("changeKYCXxz", i)
      console.log("this.localSearchInfo.itemType", JSON.parse(JSON.stringify(this.localSearchInfo.itemType)))
      console.log("this.localSearchitemTypeaRR", JSON.parse(JSON.stringify(this.localSearchitemTypeaRR)))
      console.log("map", JSON.parse(JSON.stringify(this.localSearchitemTypeaRR.map(cc => cc.value).indexOf(this.selectItemType))))

      // this.selectItemType
      this.localSearch()

      // this.KYCXloading = true
      // this.$nextTick(() => {
      //   this.$refs.KYCXtree.setCheckedKeys(this.KYCXallsavearr)
      //   this.KYCXloading = false
      // })
    },
    saveLocalSearch() {
      let funcIds
      if (this.selectItemType.length) {
        funcIds = this.selectItemType.join().split(",")
      } else {
        funcIds = this.selectItemType
      }
      if (this.muToKYCXFlag) {
        // for (let i = 0; i < this.localSearchitemTypeaRR.length; i++) {
        this.$post("/sysTranScopeManage/batchSaveSysFunctionScopeForOrgs", {
          belongRoleIds: this.multipleSelection.map((cc) => cc.id),
          funcIds: funcIds,
          orgIds: this.KYCXallsavearr
        }).then((res) => {
          if (res.success) {
            this.localSearchFlag = false
            this.$message({
              type: "success",
              message: ""
            })
          } else {
            this.$message({
              type: "error",
              message: ""
            })
          }
        })
        // }
      } else {
        // for (let i = 0; i < this.localSearchitemTypeaRR.length; i++) {
        this.$post("/sysTranScopeManage/saveSysFunctionScopeForOrgs", {
          belongRoleId: this.localSearchInfo.row.id,
          funcIds: funcIds,
          orgIds: this.KYCXallsavearr
        }).then((res) => {
          if (res.success) {
            this.localSearchFlag = false
            this.$message({
              type: "success",
              message: ""
            })
          } else {
            this.$message({
              type: "error",
              message: ""
            })
          }
        })
        // }
      }
    },
    // table  CheckBox 
    selectableFilterFn(row, index) {
      // let obj = { 1: "", 2: "", 3: "", 4: "" }
      // console.log('selectableFilterFn:', row, index);
      //  admin
      const isAdmin = !!(this.$Base64.decode(localStorage.getItem("uesr")) === "admin")

      // admin 
      if (isAdmin && row.roleType !== "3") {
        return false
      } else {
        return true
      }
    },
    // table 
    handleSelectionChange(val) {
      this.multipleSelection = val
    },
    roleCheckChange(data) {
      let array = []
      for (let item of this.$refs.roleTree.getCheckedNodes()) {
        if (item.userCode) {
          array.push({ userId: item.userCode })
        }
      }
      this.upLoadUserList = array
    },
    //  - 
    tableAdd(index, row) {
      this.dialogTableAdd = true
      this.upDataOrgId = row.orgId
      this.upDataRoleId = row.id
      this.$get("/sys/role/querySysUserByRoleId", {
        orgId: row.orgId,
        roleId: row.id,
        roleType: row.roleType
      }).then((res) => {
        if (res.data.success) {
          this.userAndRoleData = []
          this.userAndRoleData = res.data.result.userListTreeArray || []

          this.roleSelected_userCode = []
          for (const item in res.data.result.userAndRoleList) {
            this.roleSelected_userCode.push(Number(res.data.result.userAndRoleList[item].userId))
          }
        } else {
          this.$message({
            type: "error",
            message: res.data.resultDes
          })
        }
      })
    },
    settingAddSave() {
      // console.log(this.upDataOrgId, this.upDataRoleId)
      // console.log("this.upLoadUserList::", this.upLoadUserList)
      this.$post("/sys/role/addUserInRole", {
        orgId: this.upDataOrgId,
        roleId: this.upDataRoleId,
        userlist: this.upLoadUserList
      }).then((res) => {
        if (res.success) {
          this.dialogTableAdd = false
          this.$message({
            type: "success",
            message: "!"
          })
        } else {
          this.$message({
            type: "error",
            message: res.resultDes
          })
        }
      })
    },
    tableCopy(index, row) {
      // console.log("", index, row)
      this.$post("/sys/role/addCopySysRole", {
        id: row.id
      }).then((res) => {
        if (res.success) {
          this.initTable()
          this.$message({
            type: "success",
            message: "!"
          })
        } else {
          this.$message({
            type: "error",
            message: res.resultDes
          })
        }
      })
    },
    // Excel
    beforeUpload() {
      if (!this.formInline.orgId) {
        this.$message({
          type: "error",
          message: ""
        })
        return false
      }
    },
    initTree() {
      this.$get("/sys/org/queryTree").then((data) => {
        if (data.data.success) {
          this.options = data.data.result
        }
      })
    },
    clearableChange(val) {
      this.formInline.roleType = val === null ? "" : val
    },
    handleChange() {
      // if (this.roleForm.orgModel.length < 4 && this.roleForm.orgModel[1] != "271") {
      //   this.roleForm.orgModel = []
      // }
    },
    handleChange1() {
      // if (this.formInline.orgId.length < 4 && this.formInline.orgId[1] != "271") {
      //   this.formInline.orgId = []
      // }
    },
    initTable() {
      this.$get("/sys/role/queryTable", {
        page: this.page,
        size: this.size,
        ...this.formInline,
        orgId: this.formInline.orgId.length ? this.formInline.orgId[this.formInline.orgId.length - 1] : ""
      }).then((data) => {
        if (data.data.pageList.success) {
          this.tableData = data.data.pageList.result
          this.roleTotal = Number(data.data.count)
        }
      })
    },
    closeDialog(formRule) {
      this.$refs[formRule].resetFields()
    },
    onSearch() {
      this.page = 1
      this.initTable()
    },
    sysChange(a, b, c) {
      this.currentSysList = []
      for (let item of this.$refs.sysTree1.getCheckedKeys()) {
        this.currentSysList.push({ functionId: item })
      }
    },
    // 
    settingEditSave() {
      // TODO 
      let aa1 = this.$refs.sysTree1.getCheckedKeys() // 
      let cc1 = this.$refs.sysTree1.getHalfCheckedKeys() // 
      let aa2 = this.$refs.sysTree2.getCheckedKeys() // 
      let cc2 = this.$refs.sysTree2.getHalfCheckedKeys() // 
      let aa3 = this.$refs.sysTree3.getCheckedKeys() // 
      let cc3 = this.$refs.sysTree3.getHalfCheckedKeys() // 
      let aa4 = this.$refs.sysTree4.getCheckedKeys() // 
      let cc4 = this.$refs.sysTree4.getHalfCheckedKeys() // 
      let aa5 = this.$refs.sysTree5.getCheckedKeys() // 
      let cc5 = this.$refs.sysTree5.getHalfCheckedKeys() // 
      let aa6 = this.$refs.sysTree6.getCheckedKeys() // 
      let cc6 = this.$refs.sysTree6.getHalfCheckedKeys() // 
      let aa7 = this.$refs.sysTree7.getCheckedKeys() // 
      let cc7 = this.$refs.sysTree7.getHalfCheckedKeys() // 
      let aa8 = this.$refs.sysTree8.getCheckedKeys() // 
      let cc8 = this.$refs.sysTree8.getHalfCheckedKeys() // 
      let aa9 = this.$refs.sysTree9.getCheckedKeys() // 
      let cc9 = this.$refs.sysTree9.getHalfCheckedKeys() // 
      let aa10 = this.$refs.sysTree10.getCheckedKeys() // 
      let cc10 = this.$refs.sysTree10.getHalfCheckedKeys() // 
      let _treeInfo = []
      for (let item of aa1) {
        _treeInfo.push({ functionId: item, selectState: 0 })
      }
      for (let item of cc1) {
        _treeInfo.push({ functionId: item, selectState: 1 })
      }
      for (let item of aa2) {
        _treeInfo.push({ functionId: item, selectState: 0 })
      }
      for (let item of cc2) {
        _treeInfo.push({ functionId: item, selectState: 1 })
      }
      for (let item of aa3) {
        _treeInfo.push({ functionId: item, selectState: 0 })
      }
      for (let item of cc3) {
        _treeInfo.push({ functionId: item, selectState: 1 })
      }
      for (let item of aa4) {
        _treeInfo.push({ functionId: item, selectState: 0 })
      }
      for (let item of cc4) {
        _treeInfo.push({ functionId: item, selectState: 1 })
      }
      for (let item of aa5) {
        _treeInfo.push({ functionId: item, selectState: 0 })
      }
      for (let item of cc5) {
        _treeInfo.push({ functionId: item, selectState: 1 })
      }
      for (let item of aa6) {
        _treeInfo.push({ functionId: item, selectState: 0 })
      }
      for (let item of cc6) {
        _treeInfo.push({ functionId: item, selectState: 1 })
      }
      for (let item of aa7) {
        _treeInfo.push({ functionId: item, selectState: 0 })
      }
      for (let item of cc7) {
        _treeInfo.push({ functionId: item, selectState: 1 })
      }
      for (let item of aa8) {
        _treeInfo.push({ functionId: item, selectState: 0 })
      }
      for (let item of cc8) {
        _treeInfo.push({ functionId: item, selectState: 1 })
      }
      for (let item of aa9) {
        _treeInfo.push({ functionId: item, selectState: 0 })
      }
      for (let item of cc9) {
        _treeInfo.push({ functionId: item, selectState: 1 })
      }
      for (let item of aa10) {
        _treeInfo.push({ functionId: item, selectState: 0 })
      }
      for (let item of cc10) {
        _treeInfo.push({ functionId: item, selectState: 1 })
      }

      // 
      let url = ""
      let params = {}

      // 
      if (this.treeId) {
        params = { roleId: this.treeId, treeInfo: _treeInfo }
        url = "/sys/role/addRoleRef"
      }
      // 
      if (this.roleIdList.length) {
        params = { roleIdList: this.roleIdList, treeInfo: _treeInfo }
        url = "/sys/role/patchAddRoleRef"
      }

      this.$post(url, params).then((data) => {
        if (data.success) {
          this.initTable()
          this.dialogTableVisible = false
          this.$message({ type: "success", message: data.resultDes })

          // 
          this.treeId = ""
          this.roleIdList = []
        }
        // table 
        this.$refs.multipleTable.clearSelection()
      })
    },
    roleEditSave(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.roleTitle == "") {
            this.$post("/sys/role/updateRole", {
              roleName: this.roleForm.roleName,
              roleDesc: this.roleForm.roleDesc,
              roleType: this.roleForm.roleType,
              orgId: this.roleForm.orgModel[this.roleForm.orgModel.length - 1],
              id: this.editId
            }).then((data) => {
              this.$mess(data)
              this.dialogFormVisible = false
              this.initTable()
              // this.reload()
            })
          } else {
            this.$post("/sys/role/addSysRole", {
              roleName: this.roleForm.roleName,
              roleDesc: this.roleForm.roleDesc,
              roleType: this.roleForm.roleType,
              position: this.roleForm.position,
              orgId: this.roleForm.orgModel[this.roleForm.orgModel.length - 1]
            }).then((data) => {
              this.$mess(data)
              if (data.success) {
                this.initTable()
                // this.$creatHtml(data.result.opseno)
                this.dialogFormVisible = false
              }
            })
          }
        } else {
          return false
        }
      })
    },
    onAddUser() {
      this.roleForm.roleName = ""
      this.roleForm.roleDesc = ""
      this.roleForm.position = ""
      this.dialogFormVisible = true
      this.positionEdit = false
      this.roleTitle = ""
    },
    // 
    onAccessConfig() {
      if (this.multipleSelection.length === 0) {
        this.$message({
          type: "warning",
          message: ""
        })
      } else {
        const self = this
        const queryTreeList = []
        this.multipleSelection.forEach((item, index) => {
          this.roleIdList.push(item.id) //  id
          queryTreeList.push({
            id: item.id,
            roleType: item.roleType
          })
        })

        // 
        this.$post("/sys/role/pitchQueryTree", { queryTreeList: queryTreeList }).then((data) => {
          // this.$mess(data.result);
          self.reQueryTreeData(data, this.roleIdList)
        })
      }
    },
    handleSizeChange(val) {
      this.size = val
      this.initTable()
    },
    handleCurrentChange(val) {
      this.page = val
      this.initTable()
    },
    //  data reset
    reQueryTreeData(data, type) {
      this.defaultSysData = []
      this.dialogTableVisible = true

      this.sysData1 = []
      this.sysData2 = []
      this.sysData3 = []
      this.sysData4 = []
      this.sysData5 = []
      this.sysData6 = []
      this.sysData7 = []
      this.sysData8 = []
      this.sysData9 = []
      this.sysData10 = []
      if (data.result.result[1]) {
        this.sysData1 = data.result.result[1]
      }
      if (data.result.result[2]) {
        this.sysData2 = data.result.result[2]
      }
      if (data.result.result[3]) {
        this.sysData3 = data.result.result[3]
      }
      if (data.result.result[4]) {
        this.sysData4 = data.result.result[4]
      }
      if (data.result.result[5]) {
        this.sysData5 = data.result.result[5]
      }
      if (data.result.result[6]) {
        this.sysData6 = data.result.result[6]
      }
      if (data.result.result[7]) {
        this.sysData7 = data.result.result[7]
      }
      if (data.result.result[8]) {
        this.sysData8 = data.result.result[8]
      }
      if (data.result.result[9]) {
        this.sysData9 = data.result.result[9]
      }
      if (data.result.result[10]) {
        this.sysData10 = data.result.result[10]
      }
      for (let item of data.rfList) {
        if (item.selectState == "0" || item.selectState == "") {
          this.defaultSysData.push(item.functionId)
        }
      }
    },
    // 
    settingDialog(index, row) {
      const self = this
      this.treeId = row.id

      // 
      this.$get("/sys/role/queryTree", {
        roleType: row.roleType,
        roleId: row.id
      }).then((data) => {
        // console.log('sys/role/queryTree',data);
        self.reQueryTreeData(data.data, row.id)
      })
    },
    tableEdit(index, row) {
      this.roleTitle = ""
      this.editId = row.id
      this.dialogFormVisible = true
      var obj = Object.assign({}, row)
      // this.roleForm = obj
      this.roleForm.roleName = obj.roleName
      this.roleForm.roleDesc = obj.roleDesc
      this.roleForm.roleType = obj.roleType
      this.$get("sys/role/detail", {
        roleId: row.id
      }).then((data) => {
        if (data.data.success) {
          this.roleForm.orgModel = data.data.result.idpath.split("/")
          let len = this.roleForm.orgModel.length
          for (let i = 0; i < len; i++) {
            this.roleForm.orgModel[i] = parseInt(this.roleForm.orgModel[i])
          }
        }
      })
    }
  }
}
</script>

<style scoped lang="less">
.authorizeDialog /deep/ [class*=' el-icon-'],
[class^='el-icon-'] {
  color: #5fb9ee;
  font-size: 20px;
  cursor: pointer;
}
.authorizeDialog /deep/ .el-dialog__header {
  border-bottom: 1px solid #ccc;
}
.authorizeDialog /deep/ .el-dialog {
  border-radius: 14px;
  width: 80%;
  min-height: 70%;
}

.createDialog /deep/ [class*=' el-icon-'],
[class^='el-icon-'] {
  color: #5fb9ee;
  font-size: 20px;
  cursor: pointer;
}
.createDialog /deep/ .el-dialog__header {
  border-bottom: 1px solid #ccc;
  margin-bottom: 50px;
}
.createDialog /deep/ .el-dialog {
  border-radius: 14px;
  /* width: 80%; */
  /* padding: 20px 100px; */
}

/* .authorizeDialog /deep/ .el-col-6 {
  width: 15%;
  margin-right: 20px;
} */
.authorizeDialog_box {
  overflow-x: auto;
  overflow-y: hidden;
  width: 100%;
  height: 90%;
  display: flex;
}
.authorizeDialog_div {
  margin-right: 30px;
  /* display: inline-block; */
  /* vertical-align: top; */
}

.pageblock {
  width: 100%;
  text-align: center;
  margin-top: 30px;
}
.dialogText {
  margin-bottom: 20px;
}
.searchFrom {
  display: flex;
  justify-content: space-around;
}
/*  */
.el-form--inline .el-form-item {
  width: 100%;
  display: flex;
}
.card-top {
  margin-top: 15px;
}
.diaPadding /deep/ .el-dialog__body {
  padding: 15px 15px;
}
</style>



