    }
    // console.log(123, this.activeName)
    this.isInit = true
    this.initTree()
    this.menu_type = this.activeName
    next()
  },
  watch: {
    "menuFrom.funOrder": {
      deep: true,
      handler(val) {
        if (Number(val) < 10 && String(val)[0] != "0") {
          this.menuFrom.funOrder = "0" + val
        }
        if (Number(val) > 99) {
          this.menuFrom.funOrder = "99"
        }
      }
    }
  },
  computed: {
    userTypeDisabled() {
      return this.$Base64.decode(localStorage.getItem("userType")) != "1"
    }
    // pageIsShow() {
    //   if (this.node_type == 2) {
    //     return true
    //   } else if (this.node_type == 1) {
    //     return true
    //   } else {
    //     return false
    //   }
    // }
  },
  methods: {
    // dialog
    showQXPZ() {
      this.QXPZloading = true
      this.QXPZif = true
      // this.$nextTick(() => {
      //   this.$refs.QXPZtree.setCheckedKeys(this.QXPZsavearr)
      // })
      this.$get("/sys/function/sysFunctionSetPermissionForOrgs", {
        functionId: this.isAdd ? null : this.QXPZparameter
      }).then(data => {
        if (data.data.success) {
          this.QXPZarr = data.data.result.orgTree
          this.QXPZsavearr = data.data.result.hasPermissionList
          this.$nextTick(() => {
            this.$refs.QXPZtree.setCheckedKeys(this.QXPZsavearr)
          })
          if (!this.isUserType) {
            this.dealTreeOnceChecked(this.QXPZarr)
          }
          this.QXPZloading = false
        }
      })
      this.flageQXPZ = true
    },
    // admin
    dealTreeOnceChecked(arrMenus) {
      if (arrMenus.length > 0) {
        // let disarr = [];
        arrMenus.forEach(item => {
          if (item.children) {
            let arrChildren = item.children
            if (arrChildren !== null) this.dealTreeOnceChecked(arrChildren)
          }
          item.disabled = true
        })
      }
    },
    // tree
    CheckQXPZChange(data, checked, indeterminate) {
    },
    // 
    saveQXPZsave() {
      this.QXPZsavearr = this.$refs.QXPZtree.getCheckedKeys()
      // console.log(this.QXPZsavearr)
    },
    // 
    handleAvatarSuccess(res, file) {
      console.log(file.raw)
      // file.response.result.key
      this.menuFrom.icon = res.result.path
      // this.pcuserInfo.headUrl = URL.createObjectURL(file.raw)
      // this.
    },
    // 
    beforeAvatarUpload(file) {
      // const isJPG = (file.type === "image/jpeg" || "image/png" || "image/svg+xml" || "image/svg")
      const isJPG = file.type === "image/jpeg" || file.type === "image/png" || file.type === "image/svg+xml" || file.type === "image/svg"
      // const isLt2M = file.size / 1024 / 1024 < 2
      if (!isJPG) {
        this.$message.error(" JPGPNGSVG !")
      }
      // if (!isLt2M) {
      //   this.$message.error(' 2MB!')
      // }
      return isJPG
    },
    // 
    allowDrag(draggingNode) {
      // return draggingNode.data.nodeType == "123"
      return draggingNode.data.nodeType !== "1"
    },
    allowDrop(draggingNode, dropNode, type) {
      if (draggingNode.data.nodeType == "2" && dropNode.data.nodeType == "1") {
        return type == "inner"
      }
      // else if (draggingNode.data.nodeType == "2" && dropNode.data.nodeType == "2") {
      //   return type == "next"
      // }
      else if (draggingNode.data.nodeType == "3" && dropNode.data.nodeType == "1") {
        return type == "inner"
      } else if (draggingNode.data.nodeType == "3" && dropNode.data.nodeType == "2") {
        return type == "inner"
      }
      // else if (draggingNode.data.nodeType == "3" && dropNode.data.nodeType == "3") {
      //   return type == "next"
      // }
      else {
        return false
      }
    },
    isDrop(oldNode, newNode) {
      let a = oldNode.data.title
      let b = newNode.data.title
      this.$confirm("" + a + "" + b + ", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$get("/sys/function/updateFunNode", {
          id: oldNode.data.id,
          parentId: newNode.data.id
        }).then(data => {
          this.initTree([oldNode.data.id])
          this.$mess(data.data)
        })
        // this.$message({
        //   type: "success",        //   message: "!"
        // })
      }).catch(() => {
        this.initTree([oldNode.data.id])
        this.$message({
          type: "info",
          message: ""
        })
      })
    },
    handleDrop(oldNode, newNode, be, ev) {
      this.isDrop(oldNode, newNode)
      // console.log(oldNode.data.id)
      // console.log(newNode.data.id)

      // console.log(oldNode, newNode, ev)
    },
    initTree1() {
      const parent = this.treeNode.parent
      const children = parent.data.children || parent.data
      const index = children.findIndex(d => d.id === this.treeData.id)
      children.splice(index, 1)
    },
    initTree(val) {
      this.$get("/sys/function/queryTree", {
        funType: this.activeName
      }).then(data => {
        // console.log("function/queryTree  result:", data, JSON.stringify(data.data))
        const result = data.data.result
        this.menuTree = result
        if (this.isInit) {
          if (result.length) {
            this.defaultTreeKey[0] = result[0].id
            this.handleNodeClick(result[0])
          }
          this.isInit = false
        } else {
          this.$nextTick(() => {
            this.handleNodeClick(this.$refs.tree2.getNode(this.savaId).data, this.$refs.tree2.getNode(this.savaId))
            // this.savaId = null
          })
        }
        if (val) {
          this.defaultTreeKey = val
        }
      })
    },
    orderChange(val) {
      // 
      console.log(this.nodeLength)
      console.log(val)

      if (this.addTrue) {
        if (val > Number(this.max)) {
          console.log(this.max)
          this.menuFrom.funOrder = Number(this.max)
        }
      } else {
        if (Number(val) > Number(this.nodeLength)) {
          this.menuFrom.funOrder = Number(this.nodeLength) + 1
        }
      }
      // 
    },
    onChange(data, type) {
      this.menuFrom[type] = data
    },
    handleClick(tab, event) {
      this.isInit = true
      this.initTree()
      this.menu_type = this.activeName
    },
    handleItemChange(val) {
      if (val.length == 3) {
        let one = val[0].split("-")
        let two = val[1].split("-")
        let three = val[2].split("-")
        // this.businessOptions
        this.$get("/sys/function/queryBusinessList", {
          busPar: one[0] + two[0] + three[0]
        }).then(data => {
          if (data.data.success) {
            // eslint-disable-next-line
            this.businessOptions[one[1]].children[two[1]].children[
              three[1]
            ].children = data.data.result
          }
        })
      }
      //
    },
    handleNodeClick(data, node) {
      this.savaId = data.id
      this.QXPZsavearr = []
      this.QXPZparameter = null
      this.QXPZif = false
      this.menuFrom.icon = ""
      this.menuText.icon = ""
      this.menuFrom.businessId = []
      this.businessText = []
      this.treeData = data
      this.treeNode = node
      // length
      if (node) {
        this.nodeLength = node.parent.childNodes.length
      }
      // console.log(this.nodeLength)
      // length
      if (node) {
        this.childLength = node.childNodes.length
      }

      this.cancelType = data.nodeType
      this.addTrue = false
      this.node_type = data.nodeType
      this.isShow = true
      this.nodeId = data.id
      this.parentId = data.parentId || ""
      this.parentAndId = data.id
      for (let i in data) {
        let val = data[i]
        if (i != "businessId") {
          this.menuText[i] = val
          this.menuFrom[i] = val
        }
      }
      // this.menuText = Object.assign({}, data)
      // this.menuFrom = Object.assign({}, data)
      if (data.hasOwnProperty("children")) {
        this.funOrderId = String(Number(data.children.length) + 1)
      } else {
        this.funOrderId = 1
      }

      if (this.node_type == 3 && this.activeName != "1") {
        this.isBus = true
        this.$get("/sys/function/nodeClick", {
          functionId: data.id
        }).then(res => {
          // console.log(res.data.result.sysFunction.id)
          this.QXPZparameter = res.data.result.sysFunction.id
          if (res.data.success) {
            this.createOperName = res.data.result.createOperName
            this.isOper = res.data.result.isOper
            this.businessText[0] = res.data.result.bussArray[0]
            this.businessText[1] = res.data.result.bussArray[1]
            this.businessText[2] = res.data.result.bussArray[2]
            this.businessText[3] = res.data.result.buss ? res.data.result.buss.name : ""
            this.menuFrom.rbFlag = res.data.result.sysFunction.rbFlag == "1"
            this.rbFlag = this.menuFrom.rbFlag
            this.menuFrom.businessId = res.data.result.bussArray
            this.menuFrom.funLevel = res.data.result.sysFunction.funLevel
            this.menuFrom.icon = res.data.result.sysFunction.icon
            this.menuText.funLevel = res.data.result.sysFunction.funLevel
            let arr = []
            if (res.data.result.bussArray.length != 0) {
              arr = JSON.parse(JSON.stringify(res.data.result.bussArray))
            }
            arr.pop()
            if (res.data.result.bussArray.length != 0) {
              this.$get("/sys/function/queryBusinessList", {
                busPar: arr.join("")
              }).then(data => {
                if (data.data.success) {
                  let one = Number(arr[0]) - 1
                  let two = 0
                  let three = 0
                  switch (arr[1]) {
                    case "A":
                      two = 10
                      break
                    case "B":
                      two = 11
                      break
                    default:
                      two = arr[1]
                  }
                  switch (arr[2]) {
                    case "A":
                      three = 0
                      break
                    case "I":
                      three = 1
                      break
                    default:
                      three = 2
                  }
                  // eslint-disable-next-line
                  this.businessOptions[one].children[two].children[
                    three
                  ].children = data.data.result
                  this.menuFrom.businessId = res.data.result.bussArray
                  this.menuFrom.businessId[0] =
                    this.menuFrom.businessId[0] + "-" + one
                  this.menuFrom.businessId[1] =
                    this.menuFrom.businessId[1] + "-" + two
                  this.menuFrom.businessId[2] =
                    this.menuFrom.businessId[2] + "-" + three
                  // this.menuFrom.businessId = ["1-0", "0-0", "A-0", "6860"]
                  this.menuFrom.businessId.push()
                }
              })
            }
          }
        })
      }
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let businessIdStr = ""
          if (
            this.node_type == 3 &&
            this.activeName != "1" &&
            this.menuFrom.businessId.length > 0
          ) {
            businessIdStr = this.menuFrom.businessId[
              this.menuFrom.businessId.length - 1
            ]
          } else {
            businessIdStr = ""
          }
          if (this.QXPZif) {
            this.$post("/sys/function/saveMenu", {
              id: this.nodeId,
              parentId: this.parentId,
              title: this.menuFrom.title,
              funLevel: this.menuFrom.funLevel,
              funCode: this.menuFrom.funCode,
              nodeType: this.node_type,
              location: this.menuFrom.location || "/",
              funType: this.menu_type,
              funOrder: Number(this.menuFrom.funOrder),
              description: this.menuFrom.description,
              active: this.menuFrom.active,
              isBus: this.menuFrom.isBus,
              isPrint: this.menuFrom.isPrint,
              sysFunctionSetPermissionForOrgsVO: {
                functionId: this.nodeId,
                orgIds: this.QXPZsavearr
              },
              rbFlag: this.rbFlag ? "1" : "0",
              // eslint-disable-next-line
              businessId: businessIdStr,
              icon: this.menuFrom.icon
            }).then(data => {
              if (data.success) {
                this.initTree()
                this.QXPZif = false
                this.defaultTreeKey = [this.treeNode.key]
                this.isShow = true
              }
              this.$mess(data)
            })
          } else if (!this.QXPZif) {
            this.$post("/sys/function/saveMenu", {
              id: this.nodeId,
              parentId: this.parentId,
              title: this.menuFrom.title,
              funLevel: this.menuFrom.funLevel,
              funCode: this.menuFrom.funCode,
              nodeType: this.node_type,
              location: this.menuFrom.location || "/",
              funType: this.menu_type,
              funOrder: Number(this.menuFrom.funOrder),
              description: this.menuFrom.description,
              active: this.menuFrom.active,
              isBus: this.menuFrom.isBus,
              isPrint: this.menuFrom.isPrint,
              rbFlag: this.rbFlag ? "1" : "0",
              // eslint-disable-next-line
              businessId: businessIdStr,
              icon: this.menuFrom.icon
            }).then(data => {
              if (data.success) {
                this.initTree()
                this.QXPZif = false
                this.defaultTreeKey = [this.treeNode.key]
                this.isShow = true
              }
              this.$mess(data)
            })
          }
        } else {
          return false
        }
      })
    },
    edit() {
      this.isShow = false
      if (this.addTrue) {
        this.menuFrom = Object.assign({}, this.editFrom)
      }
    },
    addBigMenu(formRule) {
      this.menuFrom.funOrder = Number(this.menuTree.length) + 1
      this.cancelType = this.node_type
      this.resetForm()
      this.node_type = 1
      this.parentId = "0"
      console.log(this.menuTree.length)
    },
    addMenu(formRule) {
      this.menuFrom.funOrder = Number(this.menuTree.length) + 1
      this.rbFlag = true
      this.cancelType = this.node_type
      this.resetForm()
      this.node_type = 3
      this.parentId = "1"
    },
    addRadio() {
      this.menuFrom.funOrder = this.funOrderId
      this.cancelType = this.node_type
      this.resetForm()
      this.node_type = 4
      this.parentId = this.parentAndId
      // this.menuFrom.funOrder = Number(this.menuTree.length) + 1
    },
    addSmallMenu() {
      this.menuFrom.funOrder = this.funOrderId
      this.cancelType = this.node_type
      this.resetForm()
      this.node_type = 2
      this.parentId = this.parentAndId
    },
    addPage() {
      this.isAdd = true
      this.menuFrom.funOrder = this.funOrderId
      this.cancelType = this.node_type
      this.rbFlag = true
      this.cancelId = this.parentId
      this.resetForm()
      this.menuFrom.businessId = []
      this.node_type = 3
      this.parentId = this.parentAndId
    },
    cancel() {
      if (this.cancelType) {
        this.node_type = this.cancelType
      }
      this.rbFlag = this.menuFrom.rbFlag
      this.QXPZif = false
      // this.parentId = this.cancelId
      this.isShow = true
    },
    resetForm() {
      this.max = this.menuFrom.funOrder
      console.log()

      this.addTrue = true
      this.editFrom = Object.assign({}, this.menuFrom)
      this.$refs["menuFrom"].resetFields()
      this.isShow = false
      this.menuFrom.title = ""
      this.menuFrom.funCode = ""
      this.menuFrom.location = ""
      // this.menuFrom.funOrder = ""
      this.menuFrom.description = ""
      this.menuFrom.active = "1"
      this.nodeId = ""
      this.menuFrom.icon = ""
    }
  }
}
</script>

<style scoped lang="css">
#app,
.el-container {
  height: 100%;
}
.scroll{
  height: calc(100vh - 250px);
  overflow: auto;
}
.menuFrom {
  margin-top: 20px;
}
.el-aside {
  padding-top: 10px;
  padding-left: 10px;
  margin: -20px 0;
  margin-left: -10px;
  border-right: 1px solid #ccc;
}
.listWrap li {
  line-height: 3;
  color: #333;
  font-size: 14px;
}
.listWrap .list {
  display: inline-block;
  width: 100px;
  text-align: right;
  margin-right: 5px;
}
.footer {
  background-color: rgb(245, 245, 245);
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-bottom: -20px;
  margin-right: -20px;
}
.iconfont {
  font-size: 18px;
  color: #606266;
}
</style>



