<template>
  <div>
    <el-button class="check"size="large"></el-button>
    <el-form :model="sizeForm">
      <el-form-item>:
          <span name="sizeForm.name">{{sizeForm.name}}</span>
      </el-form-item>
     <el-form-item label=":">
        <el-col name="sizeForm.photoList" ><img v-for="(i, index) in sizeForm.photoList" :key="index" :src=i alt="" style="width: 300px;height: 300px;">
        </el-col>
      </el-form-item>
      <el-form-item label="/:">
          <span name="sizeForm.author">{{sizeForm.author}}/{{sizeForm.createTime}}</span>
      </el-form-item>
      <el-form-item label="/:">
          <span name="sizeForm.modifyTime">{{sizeForm.modifyTime}}</span>
      </el-form-item>
      <el-form-item label=":">
          <span name="sizeForm.publishTime">{{sizeForm.publishTime}}</span>
      </el-form-item>
      <el-form-item label=":">
          <span name ="sizeForm.visibility">{{sizeForm.visibility}}</span>
      </el-form-item>
      <el-form-item label=":" v-if="visible">
        <el-row>
          <span class="object" name="sizeForm.unitList" v-for="(i,index) in sizeForm.unitList" :key="index">{{i.name}},</span>
        </el-row>
        <el-row style="margin-left: 72px;">
          <span class="object" name ="sizeForm.pesonList" v-for="(k,index) in sizeForm.personList" :key="index">{{k.name}},</span>
        </el-row>
      </el-form-item>
      <el-form-item label=":">
        <el-col>
          <div name="sizeForm.synopsis">{{sizeForm.synopsis}}</div>
        </el-col>
      </el-form-item>
  </el-form>
    <el-button @click="back"></el-button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      sizeForm: {
        name: "1111",
        photoList: [],
        author: "",
        createTime: "",
        modifyTime: "",
        publishTime: "",
        visibility: "",
        unitList: [],
        personList: [],
        synopsis: ""
      },
      visible: false
    }
  },
  created() {
    let editForm = this.$route.query.param
    console.log("edit---:", editForm)
    this.sizeForm.name = editForm.newsSynopsis.name
    this.sizeForm.author = editForm.newsSynopsis.authorName
    this.sizeForm.photoList = editForm.photolist
    this.sizeForm.personList = editForm.personList
    this.sizeForm.unitList = editForm.unitList
    console.log("pho---:", this.sizeForm.photoList)
    this.sizeForm.modifyTime = this.util.formatDate(new Date(editForm.newsSynopsis.modifyTime), "yyyy-MM-dd hh:mm:ss")
    this.sizeForm.createTime = this.util.formatDate(new Date(editForm.newsSynopsis.createTime), "yyyy-MM-dd hh:mm:ss")
    this.sizeForm.publishTime = this.util.formatDate(new Date(editForm.newsSynopsis.publishTime), "yyyy-MM-dd hh:mm:ss")
    if (editForm.visibility == "1") {
      this.sizeForm.visibility = ""
    } else if (editForm.visibility == "2") {
      this.sizeForm.visibility = ""
      this.visible = "true"
    }
    this.sizeForm.synopsis = editForm.newsSynopsis.synopsis
  },
  methods: {
    back() {
      this.$router.go(-1)
    }
  }
}
</script>

<style lang="less" scoped>
  .check {
    float: right;
    background-color: darkgreen;
    color: white;
  }
</style>



