<template>
  <iframe :src="src" style="width:100%;height:100%" frameborder="0"></iframe>
</template>

<script>
export default {
  name: 'MessageList',
  data() {
    return {
      src: ""
    }
  },
  mounted() {
    this.confirmUrl()
  },
  methods: {  
    confirmUrl() {
      let url = window.location.href
      if (url.includes('sysbasebk.insigma.com')) {
        this.src = 'http://sysbasebk.insigma.com:10781/#/MessageList?onlyPage=true'
        return
      }
      if (url.includes('10.85.94.57')) {
        this.src = 'http://10.85.94.57:10781/#/MessageList?onlyPage=true'
        return
      }
    }  
  }
}
</script>
<style lang="less" scoped>
</style>


