<template>
    <div class="MattersManagement">
        <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
            <el-tab-pane label="" name="first">
                <MattersConfig v-if="activeName == 'first' "></MattersConfig>
            </el-tab-pane>
            <el-tab-pane label="" name="second">
                <MattersType v-if="activeName == 'second' "></MattersType>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>

<script>
export default {
  data() {
    return {
      activeName: "first"
    }
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event)
    }
  }
}
</script>

<style scoped>
    .MattersManagement {
        background-color: #fff;
        height: 800px;
        overflow: auto;
    }
</style>



