import Vue from "vue"
import ElementUI from "element-ui"
import "./assets/style/reset.css"
import "./assets/style/iconfont.css"
import "element-ui/lib/theme-chalk/index.css"
import App from "./App.vue"
import router from "./router"
import store from "./vuex/"
import { Base64 } from "js-base64"
import CryptoJS from "crypto-js"
import axios from "axios"// axios
import {
  post,
  get,
  deleteUrl,
  patchUrl
} from "./api/http"
import EpDelete from "./components/common/EpDelete.vue"
import EpInput from "./components/common/EpInput.vue"
import EpSelect from "./components/common/EpSelect.vue"
import EpPage from "./components/common/EpPage.vue"
import EpUpload from "./components/common/upload"
import MattersConfig from "./components/MattersManagement/MattersConfig.vue"
import MattersType from "./components/MattersManagement/MattersType.vue"
import SearchComponent from "./components/common/SearchComponent.vue"
import * as filters from "./utils/filters"
import Util from "./utils/util"
import "./icons"

import VueQuillEditor from "vue-quill-editor"
import "quill/dist/quill.core.css"
import "quill/dist/quill.snow.css"
import "quill/dist/quill.bubble.css"
Vue.prototype.util = Util
// import Print from 'vue-print-nb'
// Vue.use(Print); //
// import 'babel-polyfill'
// import 'event-source-polyfill'
Vue.config.productionTip = false

Vue.use(ElementUI)

Vue.use(VueQuillEditor)

// 
Vue.prototype.$crypto = CryptoJS
Vue.prototype.$post = post
Vue.prototype.$axios = axios
Vue.prototype.$get = get
Vue.prototype.$delete = deleteUrl
Vue.prototype.$patch = patchUrl
Vue.prototype.$bussType = "2" // 1  2

Vue.prototype.$Base64 = Base64
Vue.prototype.$api = "/api"
// Vue.prototype.$api = ""
Vue.prototype.$rules = function(val, size) {
  return [{
    required: true,
    message: val,
    trigger: "blur"
  },
  {
    max: size,
    message: " " + size + " ",
    trigger: "blur"
  }
  ]
}
Vue.prototype.$rulesMax = function(size) {
  return [
    {
      max: size,
      message: " " + size + " ",
      trigger: "blur"
    }
  ]
}
Vue.prototype.$mess = function(data) {
  if (data.success) {
    this.$message({
      type: "success",
      message: data.resultDes
    })
  }
  if (!data.success) {
    this.$message({
      type: "error",
      message: data.resultDes
    })
  }
}
// 
Vue.prototype.$sysCreatHtml = function(logId, obj) {
  let param = {
    logId: logId,
    // eslint-disable-next-line no-undef
    htmlText: Base64.encode(obj.el),
    version: 1,
    cssUrl: obj.linkHref
  }
  this.$post("/sys/snapshot/upload", param).then(res => {
    if (res.success) {
      console.log(res)
    }
  })
}
// 
Vue.prototype.$creatHtml = function(logId) {
  let obj = {}

  let el = document.querySelector(".el-main")
  let input = document.querySelectorAll("input")
  let textInput = document.querySelectorAll("textarea")
  let link = document.querySelectorAll("link")
  let linkHref = ""
  linkHref = link.length > 0 ? link[0].href : "http://10.85.94.238:10540/openapiApp/download?key=bizamt/rdm/1562901848299M87.css"
  for (let i = 0; i < input.length; i++) {
    input[i].setAttribute("data-test", input[i].value)
  }

  for (let i = 0; i < textInput.length; i++) {
    textInput[i].setAttribute("data-test", textInput[i].value)
  }
  obj.el = `<!DOCTYPE html><html lang="en"><head><meta charset="utf-8"><meta name="apple-mobile-web-app-capable" content="yes"><meta name="viewport" content="width=device-width,initial-scale=1,user-scalable=0"><title></title><link href="${linkHref}" rel="stylesheet"></head>
            <body><div style="position: fixed;width:100%;height:100%;z-index: 9999;"></div><div id="app"><section class="el-container" ><section  class="el-container is-vertical">
            <div  class="ep-tabs"><main class="el-main">${el.innerHTML}</main></div></section></section>
            </div></body></html>`
  let param = {
    logId: logId,
    // eslint-disable-next-line no-undef
    htmlText: Base64.encode(obj.el),
    version: 1,
    cssUrl: linkHref
  }
  this.$post("/snapshot/upload", param).then(res => {
    if (res.success) {
      console.log(res)
    }
  })
}
// 
Vue.component("EpDelete", EpDelete)
Vue.component("EpInput", EpInput)
Vue.component("EpSelect", EpSelect)
Vue.component("EpPage", EpPage)
Vue.component("EpUpload", EpUpload)
Vue.component("MattersConfig", MattersConfig)
Vue.component("MattersType", MattersType)
Vue.component("SearchComponent", SearchComponent)
// 
Object.keys(filters).forEach(key => {
  Vue.filter(key, filters[key])
})
router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
})
/* eslint-disable no-new */
new Vue({
  el: "#app",
  router,
  store,
  components: {
    App
  },
  template: "<App/>"
})



