<template>
     <!--  -->
    <div class="MattersConfig">
        <!--  -->
        <div class="clearfix">
            <SearchComponent class="SearchComponent" @SearchComponent='getMsgFormSerachComproent' plc="/"></SearchComponent>
            <el-button class="addMatters" type="primary" @click="addMatters"></el-button>
        </div>
        <!--  -->
        <div class="info">
            <div class="infoHeader">
                <span style="width:30%;height:25px"></span>
                <span style="width:10%;height:25px"></span>
                <span style="width:10%;height:25px"></span>
                <span style="width:48%;height:25px"></span>
            </div>
            <el-collapse accordion v-model="activeNames" @change="changeCollapse">
                    <el-collapse-item v-for="item in mattersConfigList" :key="item.id" :name="item.id">
                        <template slot="title" class="infoMationTemplate">
                            <span style="width:30%;height:30px;text-align:center">
                                <p class="typeName1" style="height:12px;line-height:12px">{{item.name}}</p>
                                <p class="typeName2" style="height:12px;line-height:20px">{{item.shortName}}</p>
                            </span>
                            <span style="width:10%;height:25px;text-align:center;line-height:25px">{{item.iconCategoryName}}</span>
                            <span style="width:10%;height:25px;text-align:center;line-height:25px"></span>
                            <span style="width:48%;height:25px;text-align:center;line-height:25px">
                                <el-button size='small' type="primary" @click.stop="add(item)"></el-button>
                                <el-button size='small' type="success" @click.stop="edit(item)"></el-button>
                            </span>
                        </template>
                       <div class="infoChildren">
                           <!--  -->
                            <div class="infoChildrenHeader">
                                <span style="width:30%;height:25px"></span>
                                <span style="width:40%;height:25px"></span>
                                <span style="width:28%;height:25px"></span>
                            </div>
                           <!--  -->
                           <div class="infoChildrenInfoMation" v-for="(innerItem,index) in collapseList" :key="index">
                               <span style="width:30%;height:25px">{{innerItem.areaName}}</span>
                                <span style="width:40%;height:25px">{{innerItem.status}}</span>
                                <span style="width:28%;height:25px">
                                    <i @click.stop="lookInfomation(item, innerItem.areaId)" style="color:blue"></i> |
                                    <i @click.stop="changeConfig(item, innerItem.areaId, innerItem.mattersAreaId)" style="color:blue"></i>
                                </span>
                           </div>
                           <div class="infoChildrenInfoMation" v-if="collapseList.length == 0">
                               <span style="width:100%;height:25px"></span>
                           </div>
                       </div>
                    </el-collapse-item>

            </el-collapse>
        </div>
        <div class="block">
            <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage4"
            layout="total, prev, pager, next, jumper"
            :total="totalcount">
            </el-pagination>
        </div>

        <el-dialog title="" :visible.sync="dialogFormVisible">
            <el-form :model="form">
                <el-form-item label="" :label-width="formLabelWidth" >
                    <el-input style="width:89%;" placeholder="" v-model="form.name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input style="width:89%;" placeholder="" v-model="form.shortName" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-select style="width:89%;" v-model="form.iconCategoryId" placeholder="">
                        <el-option v-for="item in mattersTypeList" :key="item.id" :label='item.name' :value="item.id"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false"> </el-button>
                <el-button type="primary" :disabled="isDisabled" @click="submit"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
  data() {
    return {
      id: 333,
      itemId: "",
      totalcount: 1,
      msgFormSon: "",
      activeNames: 0, // 
      collapseList: [], // 
      currentPage4: 1,
      dialogFormVisible: false,
      form: {
        name: "",
        shortName: "",
        iconCategoryId: ""
      },

      formLabelWidth: "120px",
      isDisabled: true,
      mattersTypeList: [],
      mattersConfigList: [],
      formData: {
        "pageNum": 1,
        "pageSize": 10
      }
    }
  },
  watch: {
    // 
    form: {
      handler: function(val) {
        if (val.name != "" && val.shortName != "" && val.iconCategoryId != "") {
          this.isDisabled = false
        } else {
          this.isDisabled = true
        }
      },
      deep: true
    },
    formData: {
      handler: function(val) {

      },
      deep: true
    }
  },
  created() {
    this.getmattersConfigList()
    console.log("localStorage.getItem('Ip')", localStorage.getItem("Ip"))
    console.log("localStorage.getItem('cityname')", localStorage.getItem("cityname"))
  },
  methods: {
    // 
    changeCollapse() {
      if (this.activeNames == "") {
        return
      }
      this.collapseList = []
      let submitForm = {mattersId: this.activeNames}
      this.$post("/ybapp/siteMattersArea/selectAreaList", submitForm).then((resData) => {
        if (resData.success) {
          this.collapseList = resData.result
        } else {
          this.$message({
            type: "warning",
            message: resData.resultDes
          })
        }
      }).catch((error) => {
        console.log(error)
      })
    },
    // 
    addMatters() {
      this.form = {}
      this.dialogFormVisible = true
      this.getmattersTypeList()
    },
    handleSizeChange(val) {
      console.log(` ${val} `)
      this.formData.pageSize = val
      this.getmattersConfigList()
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`)
      this.formData.pageNum = val
      this.getmattersConfigList()
    },
    handleClick(tab, event) {
      console.log(tab, event)
    },
    getMsgFormSerachComproent(data) {
      console.log("", data)
    },
    handleChange(val) {
      console.log(val)
    },
    add(item) {
      sessionStorage.setItem("MattersConfigItem", JSON.stringify(item))
      console.log(item)
      this.$router.push("/home/NewAreas")
    },
    edit(item) {
      this.form.name = item.name
      this.form.shortName = item.shortName
      this.form.iconCategoryId = item.iconCategoryId
      this.form.id = item.id
      console.log("-------", item)
      this.dialogFormVisible = true
      this.getmattersTypeList()
    },

    lookInfomation(item, areaId) {
      item.areaId = areaId
      sessionStorage.setItem("MattersConfigItem", JSON.stringify(item))
      console.log(item)
      this.$router.push("/home/MattersDetail")
    },
    changeConfig(item, areaId, mattersAreaId) {
      item.areaId = areaId
      item.mattersAreaId = mattersAreaId
      sessionStorage.setItem("MattersConfigItem", JSON.stringify(item))
      this.$router.push("/home/NewAreas")
    },
    // 
    submit() {
      this.dialogFormVisible = false
      console.log("this.form", this.form)
      var formData = this.form
      formData
      this.$post("/ybapp/pageLayout/saveSiteMatters", formData).then((resData) => {
        if (resData.success) {
          this.$message({
            message: "",
            type: "success"
          })
          this.getmattersConfigList()
        }
      }).catch((error) => {
        console.log(error)
      })
    },
    // 
    getmattersConfigList() {
      console.log(22222)
      var formData = this.formData
      //  "name": "",
      this.$post("/ybapp/pageLayout/getSiteMattersLikeList", formData).then((resData) => {
        if (resData.success) {
          console.log("", resData.result)
          this.totalcount = resData.result.totalCount
          this.mattersConfigList = resData.result.dataList
          console.log("mattersConfigList", this.mattersConfigList)
        }
      }).catch((error) => {
        console.log(error)
      })
    },
    // 
    getmattersTypeList() {
      console.log()
      this.$post("/ybapp/pageLayout/getSiteIconCategoryList", {
        "bussType": 1
      }).then((resData) => {
        if (resData.success) {
          console.log("", resData.result)
          this.mattersTypeList = resData.result.dataList
          console.log("------", this.mattersTypeList)
        }
      }).catch((error) => {
        console.log(error)
      })
    }
  }
}
</script>

<style scoped lang="less">
    .MattersConfig {
        // height: 1000px;
        background-color: #fff;
        .SearchComponent {
            float: left;
        }
        .addMatters {
            float: left;
            margin-left: 20px;
        }
        .info {
            margin-top: 15px;
            .infoHeader {
                background-color: #ddd;
                height: 45px;
                line-height: 45px;
                span {
                    text-align: center;
                   display: inline-block;
                }
            }
            .infoChildren {
                cursor: pointer;
              background-color: #ddd;
              padding: 10px 15px;
              .infoChildrenHeader {
                  height: 40px;
                  line-height: 40px;
                  background-color: #eee;
                  span {
                      display: inline-block;
                      text-align: center;
                  }
              }
              .infoChildrenInfoMation {
                  margin-top: 1px;
                  height: 40px;
                  line-height: 40px;
                  background-color: #fff;
                  span {
                      display: inline-block;
                      text-align: center;
                  }
              }
            }
        }
        .block {
            position: fixed;
            bottom: 20px;
            left: 50%;
            transform: translateX(-50%);
        }
    }
</style>



