<template>
  <div class="#app">
    <div class="box">
      <h3 class="h3"></h3>
      <h4 class="h4" :class="{'warning1':info.status=='0','success1':info.status=='1','error1':info.status=='2','info1':info.status=='3'}">{{info.status | taskStatus}}</h4>
      <el-row class="list">
        <el-col :span="6">{{info.name}}</el-col>
        <el-col :span="6">{{info.time}}</el-col>
        <el-col :span="6">{{info.person}}</el-col>
        <!-- <el-col :span="6">{{info.status | taskStatus}}</el-col> -->
      </el-row>
    </div>
    <div class="box">
      <h3 class="h3"></h3>
      <div style="border:1px solid #ccc">
      <iframe scrolling="auto" :src="iframeSrc" frameborder="0" marginheight="0" marginwidth="0" height="500" width="100%"></iframe>

      </div>
    </div>
    <div class="box">
      <div class="title">
        <h3 class="h3"></h3>
        <span @click="hiddenProcess">{{isProcess?"":""}}</span>
      </div>
      <ul class="proList" v-show="isProcess">
        <li class="proLi" v-for="(item,index) in proInfo" :key="index">
          <div class="left" :class="{active:item.information[0].checkResult=='0'}">
            <div class="radio"></div>
            <div class="line" v-show="index+1!=proInfo.length"></div>
          </div>
          <div class="right">
            <div class="rightList">
              <div class="num">{{item.name}}</div>
              <div class="rightText" v-show="hideDetail">
                <div class="text">
                  <ul class="textList" v-show="hideDetail">
                    <li class="textLi">
                      <div class="user" v-show="item.isModify&&item.assignList.length>0">
                        <p>{{item.assignList.length>0?item.assignList[0].assignorName:''}}</p>
                        <p class="hideDetail" @click="hideModify(index)"></p>
                      </div>
                      <div class="smallAndLine" v-show="item.isModify&&item.assignList.length>0">
                        <div class="line lineHeight"></div>
                        <div class="small">
                          <p v-for="(list,index1) in item.assignList" class="smallSpan" :key="index1">
                            <span>{{list.assignorName}}</span>
                            <span>{{list.assignTime}}</span>
                            <span>{{list.assignReason}}</span>
                          </p>
                          <p class="hideDetail"></p>
                        </div>
                      </div>
                      <div class="user bottomUser">
                        <p>{{item.information[0].checkPeopleName}}</p>
                        <p>{{item.information[0].checkTime}}</p>
                        <p>{{item.information[0].checkResult | checkResult}}</p>
                        <p class="seeData" @click="seeInfo(index)" v-show="proInfo[index].isMaterial=='0'?false:true"></p>
                        <p v-show="!item.isModify" class="hideDetail" @click="hideModify(index)"></p>
                      </div>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </li>
      </ul>
    </div>
    <div class="footer">
      <el-button type="primary" v-if="showButton == 3" @click="cancelClick"></el-button>
      <el-button type="primary" v-if="showButton == 2"></el-button>
      <el-button type="primary" @click="examineResult(true)" v-if="showButton == 1"></el-button>
      <el-button @click="examineResult(false)" v-if="showButton == 1"></el-button>
      <el-button @click="cancle"></el-button>
    </div>
    <el-dialog title="" :visible.sync="infoDialog" width="30%">
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column align="center" prop="name" label=""></el-table-column>
        <el-table-column align="center" prop="content" label=""></el-table-column>
      </el-table>
      <span slot="footer" class="dialog-footer">
        <!-- <el-button @click="dialogVisible = false"> </el-button> -->
        <el-button type="primary" @click="infoDialog = false"></el-button>
      </span>
    </el-dialog>

    <el-dialog title="" :visible.sync="resultDialog" width="30%">
      <el-form v-if="isPass" ref="passForm" :model="passForm" label-width="100px">
        <el-form-item label="">
          <span></span>
        </el-form-item>
        <el-form-item v-for="(item,index) in passFormList" :key="index"  :label="item.name">
          <el-input
            :maxlength="item.wordCount"
            show-word-limit
            type="textarea"
            v-model="item.content"></el-input>
        </el-form-item>
      </el-form>
      <el-form v-else ref="form" :model="form" label-width="100px">
        <el-form-item label="">
          <span></span>
        </el-form-item>
        <el-form-item label="">
          <el-input type="textarea" v-model="form.rejectCause"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="resultDialog = false"> </el-button>
        <el-button type="primary" @click="passData(isPass)"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      passFormList: [],
      showButton: "",
      isProcess: true,
      infoDialog: false,
      resultDialog: false,
      isPass: true,
      hideDetail: true,
      iframeSrc: "",
      tableData: [],
      passForm: {
        content1: "",
        content2: "",
        content3: ""
      },
      form: {
        rejectCause: ""
      },
      proInfo: [],
      info: {
        name: "",
        time: "",
        person: "",
        status: ""
      }
    }
  },
  created() {
    this.init()
    let str = window.location.protocol + "//" + window.location.host + this.$api + "/snapshot/download?logId="
    this.iframeSrc = this.$route.query.logId ? str + this.$route.query.logId : str + 1790
    if (this.$route.query.checkResult == 0 && Boolean(this.$route.query.taskStatus) == false) {
      this.showButton = 1
    } else if (this.$route.query.checkResult == 0 && this.$route.query.taskStatus == 1) {
      this.showButton = 2
    } else if (this.$route.query.taskStatus != 0 && this.$route.query.checkResult != 0) {
      this.showButton = 3
    } else if (this.$route.query.taskStatus == 0) {
      this.showButton = 4
    }
    if (this.$route.query.proName) {
      this.showButton = 5
    }
  },
  methods: {
    init() {
      this.$get("/sys/business/queryCheckInfo", {
        busId: this.$route.query.busId
      }).then(data => {
        console.log(data.data.result.checkBusiness.operatioName)
        if (data.data.success) {
          this.info.name = data.data.result.checkBusiness.operatioName
          this.info.time = data.data.result.checkBusiness.arraignmentTime
          this.info.person = data.data.result.checkBusiness.arraignmentPeople
          this.info.status = data.data.result.checkBusiness.taskStatus
          this.proInfo = data.data.result.proInfo
          console.log(this.proInfo)
          for (let i = 0; i < this.proInfo.length; i++) {
            this.proInfo[i].isModify = true
          }
        }
      })
    },
    passData(val) {
      // let arr = []
      // arr.push({id: this.$route.query.busId, content: this.passForm.content1})
      // arr.push({id: this.$route.query.busId, content: this.passForm.content2})
      // arr.push({id: this.$route.query.busId, content: this.passForm.content3})
      // console.log(arr)
      if (val == true) {
        let arr = JSON.parse(JSON.stringify(this.passFormList))
        for (let i = 0; i < arr.length; i++) {
          delete arr[i].createTime
          delete arr[i].hisId
          delete arr[i].modifyTime
          delete arr[i].name
          delete arr[i].proId
          delete arr[i].wordCount
        }
        this.$post("/sys/business/check", {
          busId: this.$route.query.busId,
          proId: this.$route.query.proId,
          materia: [...arr]
        }).then(data => {
          this.$mess(data)
          this.resultDialog = false
          this.$router.push("/home/sys/business/queryTable")
        })
      } else {
        this.$get("/sys/business/reject", {
          busId: this.$route.query.busId,
          proId: this.$route.query.proId,
          rejectCause: this.form.rejectCause
        }).then(data => {
          this.resultDialog = false
        })
      }
    },
    cancelClick() {
      this.$get("/sys/business/withdraw", {
        busId: this.$route.query.busId,
        proId: this.$route.query.proId
      }).then(data => {
        this.$mess(data.data)
        this.search()
      })
    },
    hiddenProcess() {
      this.isProcess = !this.isProcess
    },
    hideModify(index) {
      console.log(this.proInfo[index].isModify)
      this.proInfo[index].isModify = !this.proInfo[index].isModify
      this.proInfo.push()
    },
    seeInfo(index) {
      this.infoDialog = true
      this.tableData = this.proInfo[index].materiaList
    },
    hideDetailClick() {
      this.hideDetail = !this.hideDetail
    },
    examineResult(val) {
      this.isPass = val
      this.resultDialog = true
      if (val) {
        this.$post("/sys/business/getMateriaList", {
          proId: this.$route.query.proId
        }).then(data => {
          console.log(data)
          if (data.success) {
            this.passFormList = data.result
          }
        })
      }
    },
    cancle() {
      this.$router.push({
        path: "/home/sys/business/queryTable",
        query: {
          activeName: this.$route.query.activeName
        }
      })
    }
  }
}
</script>

<style scoped lang="css">
#app {
  position: relative;
}
h3 {
  color: #606266;
  margin-bottom: 20px;
}
.list {
  text-indent: 2em;
}
.box {
  margin-bottom: 20px;
  position: relative;
}
.footer {
  /* width: calc(100%+40px); */
  background-color: #f2f2f2;
  margin-top: 40px;
  padding: 10px 0 10px 32px;
  margin-right: -20px;
  margin-bottom: -20px;
  margin-left: -20px;
}
.title .h3 {
  display: inline-block;
}
.title span {
  display: inline-block;
  color: #409eff;
  font-size: 12px;
  margin-left: 10px;
  cursor: pointer;
}
.hideDetail {
  color: #409eff;
  cursor: pointer;
  font-size: 12px;
  line-height: 20px;
}
.proList .proLi {
  display: flex;
  align-items: stretch;
  margin-bottom: 40px;
}
.right {
  margin-left: 10px;
  font-size: 14px;
}
.num {
  border-right: 1px solid #333;
  padding: 5px 10px;
}
.text {
  margin-left: 10px;
}
.proList {
  position: relative;
}
.left {
  margin-left: 30px;
}
.rightList {
  display: flex;
  align-items: flex-start;
}
.smallAndLine {
  margin-left: 23px;
  margin-bottom: 15px;
  display: flex;
  align-items: stretch;

}
.smallSpan span{
  display: inline-block;
  min-width: 220px;
  font-size: 14px;
}
.user {
  display: flex;
  margin-top: 2px;
}
.user p {
  margin-right: 40px;
  line-height: 20px;
}
.seeData {
  color: #409eff;
  font-size: 14px;
  line-height: 20px;
  cursor: pointer;
}
.proList .radio {
  width: 11px;
  height: 11px;
  border-radius: 50%;
  background-color: #409eff;
  box-shadow: 0 0 0 3px #fff, 0 0 0 5px #409eff;
}
.lineHeight {
  /* min-height: 30px !important; */
  height: auto !important;
  position: relative;
}
.proList .line {
  border-left: 1px solid #409eff;
  width: 1px;
  height: 100%;
  position: relative;
  left: 5px;
  top: 3px;
}
.proList .line::after {
  content: "";
  width: 0;
  height: 0;
  border-width: 8px;
  border-style: solid;
  border-color: #409eff transparent transparent transparent;
  position: absolute;
  bottom: -16px;
  left: -8px;
}
.proList .active .line{
  border-left: 1px solid #ccc;
}
.proList .active .radio {
  background-color: #ccc;
  box-shadow: 0 0 0 3px #fff, 0 0 0 5px #ccc;
}
.proList .active .line::after {
  border-color: #ccc transparent transparent transparent;
}
.small {
  font-size: 12px;
  position: relative;
  /* top: -16px; */
  left: 10px;
}
.small p {
  line-height: 2;
  /* display: inline-block; */
}

.h4 {
  position: absolute;
  top: 10px;
  right: 10px;
  line-height: 2;
  background-color: #e6a23c;
  color: #fff;
  font-size: 18px;
  text-align: center;
  padding: 0 20px;
}
.warning1{
  background-color: #e6a23c;
}
.success1{
  background-color: #67C23A;
}
.error1{
  background-color: #F56C6C;
}
.info1{
  background-color: #909399;
}
.bottomUser{
  /* margin-top: 20px; */
}
</style>



