<template>
  <el-upload
    v-loading.fullscreen="fullscreenLoading"
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.6)"
    style="display:inline-block"
    :action="actionUrl()"
    name="file"
    :disabled="isDisabled"
    :http-request="uploadFile"
    :show-file-list="false"
    :before-upload="beforeUpload"
    :on-success="onSuccess"
  >
    <div style="padding: 0 20px;">{{text}}</div>
    <!-- <el-button  type="primary" :disabled="isDisabled" :size="size">
      
      <i class="el-icon-upload"></i>
    </el-button> -->
  </el-upload>
</template>
<script>
import xlsxer from 'xlsx'
import axios from 'axios'
export default {
  name: 'UpLoadExcel',
  data() {
    return {
      fullscreenLoading: false,
      fileName: ''
    }
  },
  props: {
    text: {
      type: String,
      default: ''
    },
    isDisabled: {
      type: Boolean,
      default: false
    },
    // allData: {
    //   type: Object,
    //   default: function() {
    //     return {}
    //   }
    // },
    size: {
      type: String,
      default: ''
    },
    upLoadUrl: {
      type: String,
      default: ''
    }
  },
  computed: {
    // orgId() {
    //   if (this.allData) {
    //     if (this.$route.path === '/home/sys/role/queryTable') {
    //       let id = this.allData.orgId[this.allData.orgId.length-1]
    //       return {
    //         orgId: id
    //       }
    //     }
    //   }
    // }
  },
  methods: {
    uploadFile(params) {
      // console.log(params, '123123123123')
      // console.log(this, '123123123123')
      const _file = params.file
      const _that = this
      let formData = new FormData()
      formData.append("file", _file)
      axios.post(this.upLoadUrl, formData, {timeout: 1000 * 60 * 3}).then(res => {
        console.log(res, "xxxxxxxxxxxxx")
        if (res.data.success) {
          _that.$message({
            type: 'success',
            message: res.data.resultDes,
            onClose: function() {
              _that.fullscreenLoading = false
            }
          })
          _that.$emit('uploadSuccess', res.data, this.fileName)
        } else {
          _that.$message({
            type: 'error',
            message: res.data.resultDes,
            onClose: function() {
              _that.fullscreenLoading = false
            }
          })
        }
      })
      return false
    },
    // isOrgId() {
    //   if (this.$route.path === '/home/sys/role/queryTable') {
    //     if (this.allData.orgId.length === 0) {
    //       this.$message({
    //         type: 'error',
    //         message: '!'
    //       })
    //       this.isDisabled = true
    //       setTimeout(() => {
    //         this.isDisabled = false
    //       }, 1000)
    //       return
    //     }
    //   }
    // },
    actionUrl() {
      return this.upLoadUrl
    },
    beforeUpload(file) {
      console.log(this.data)
      this.fullscreenLoading = true
      this.fileName = file.name
      const xlsx = file.name.split('.')[file.name.split('.').length - 1] === 'xlsx'
      const xls = file.name.split('.')[file.name.split('.').length - 1] === 'xls'
      if (!xlsx && !xls) {
        this.$message.error('xlsx/xls!')
        this.fullscreenLoading = false
        return false
      }
      let reader = new FileReader()
      reader.onload = e => {
        try {
          let workbook = xlsxer.read(e.target.result, {type: 'binary'})
          for (let sheet in workbook.Sheets) {
            // eslint-disable-next-line no-unused-vars
            let sheetArray = xlsxer.utils.sheet_to_json(workbook.Sheets[sheet])
            // console.log(sheetArray)
          }
        } catch (e) {
          return false
        }
      }
      // console.log(reader.readAsBinaryString(file))
      reader.readAsBinaryString(file)
    },
    onSuccess(res) {
      let _that = this
      if (res.success) {
        this.$message({
          type: 'success',
          message: res.resultDes,
          onClose: function() {
            _that.fullscreenLoading = false
          }
        })
        this.$emit('uploadSuccess', res, this.fileName)
      } else {
        this.$message({
          type: 'error',
          message: res.resultDes,
          onClose: function() {
            _that.fullscreenLoading = false
          }
        })
      }
    }
  }
}
</script>
<style lang="less">
</style>





