    this.initTree()
    this.$get("/sys/org/getInsTypeList").then(data => {
      if (data.data.success) {
        let newArr = JSON.parse(JSON.stringify(data.data.result))
        this.cities = data.data.result
        for (let i of newArr) {
          this.citiesData.push(i.id)
        }
      }
    })
  },
  watch: {
    isEdit() {
      if (this.isEdit === true) {
        this.isView = false
        this.isMainShow = false
        this.isTable = false
      }
    },
    isMainShow() {
      if (this.isMainShow === true) {
        this.isView = false
        this.isEdit = false
        this.isTable = false
      }
    },
    isView() {
      if (this.isView === true) {
        this.isEdit = false
        this.isMainShow = false
        this.isTable = false
      }
    },
    isTable() {
      if (this.isTable === true) {
        this.isView = false
        this.isEdit = false
        this.isMainShow = false
      }
    }
  },
  computed: {
    tableHeight() {
      return window.innerHeight - 260
    }
  },
  methods: {
    inputChange(val){
      this.$refs.tree2.filter(val)
    },
    filterNode(value, data) {
      // console.log('value:',value);
      // console.log('data:',data);
      if (!value) return true
      return data.orgName.indexOf(value) !== -1
    },
    // 
    handleTongbu(row) {
      this.mainLoading = true
      this.$post(`sys/org/batchSaveCheckConfig?orgIds=${(row.id).toString()}`).then(data => {
        // console.log(data)
        if (data.success) {
          // row.isSynchronization = 1
          // this.$message({
          //   type: "success",
          //   message: ""
          // })
          this.initTree()
        } else {
          this.$message({
            type: "error",
            message: `,${data.resultDes}`
          })
        }
      })
    },
    editDepart(id, row, index) {
      this.departIndex = index
      this.row = row
      // console.log("", id, row)
      this.diaTitle1 = ""
      this.departId = id
      this.$post("/sysOrgDepartment/detail", {
        id: Number(id)
      }).then(data => {
        // console.log(data)
        if (data.success) {
          this.addDia1 = true
          this.departFrom = Object.assign(this.departFrom, data.result)
          this.departFrom.regionCode = row.id
          this.departFrom.rowOrgName = row.orgName
          this.departFrom.orgName = data.result.name
          this.departFrom.id = data.result.id
          this.departFrom.orgenterCode = data.result.code
          this.departFrom.orgDesc = data.result.departDescribe
        } else {
          this.$mess(data.msg)
        }
      })
    },
    // 
    deleteDepart(id, row, index) {
      this.isRowClick = false
      // console.log(row)
      let params = {
        id: Number(id),
        orgId: row.id
      }
      // console.log("", list)
      this.$confirm(
        `ID${id}, ?`,
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).then(() => {
        this.$delete("/sysOrgDepartment/delete", params).then(data => {
          // console.log("delete", data)
          if (data.data.success) {
            this.$message({
              type: "success",
              message: "!"
            })
            // this.tableData.splice
            // this.rowExpand(row)
            // this.$set
            // this.rowClick(row)
            row.departList.splice(index, 1)
            // this.tableData.splice()
            // this.defaultTreeKey = []
            // this.defaultTreeKey.push(this.initId)
            // this.tableInit(this.parentId)
          } else {
            this.$message({
              type: "error",
              message: data.data.resultDes
            })
          }
        }).catch(err => {
          console.log(err)
        })
        // this.$post("/sysOrgDepartment/save", {
        //   code: "", // 
        //   departDescribe: "", // 
        //   id: "",
        //   leader: "", // 
        //   linkman: "", // 
        //   name: "test011", // 
        //   orgId: 1315, // 
        //   shortName: "" // 
        // }).then(data => {
        //   console.log(data)
        //   this.addDia1 = false
        // })
        // console.log("then2")
      }).catch(() => {
        this.$message({
          type: "info",
          message: ""
        })
      })
    },
    departAddEdit(formName) {
      let params = {
        code: this.departFrom.orgenterCode, // 
        departDescribe: this.departFrom.orgDesc, // 
        id: this.departFrom.id ? this.departFrom.id : "",
        leader: this.departFrom.leader, // 
        linkman: this.departFrom.linkman, // 
        name: this.departFrom.orgName, // 
        orgId: Number(this.departFrom.regionCode), // 
        shortName: this.departFrom.shortName // 
      }
      this.$refs[formName].validate(valid => {
        // console.log(params)
        this.$post("/sysOrgDepartment/save", params).then(data => {
          if (data.success) {
            this.addDia1 = false
            // console.log(this.row.departList[this.departIndex].name)
            if (this.departFrom.id) {
              this.row.departList[this.departIndex].name = this.departFrom.orgName
            } else {
              this.rowExpand(this.row)
            }
          } else {
            this.$message({
              type: "error",
              message: data.resultDes
            })
          }
        })
      })
    },
    rowClick(row) {
      this.isRowClick = true
      // this.$refs.refTable.toggleRowExpansion(row)
    },
    // ,
    rowExpand(row) {
      this.orgId = row.id
      this.departShow = false
      this.rowOrgName = row.orgName
      let params = {
        orgId: Number(this.orgId)
      }
      // console.log("isRowClick", this.isRowClick)
      if (this.isRowClick) {
        this.$refs.refTable.toggleRowExpansion(row)
      }
      this.$post("/sysOrgDepartment/list", params).then(res => {
        if (res.success) {
          row.isShow = true
          row.departList = res.result
          // console.log(row)
          // this.$set(row, "departList", res.result)
        } else {
          this.$mess(res.data.msg)
        }
        this.isRowClick = true
      })
    },
    // 
    addDepart(row) {
      // console.log("addDepart", row)
      for (let i in this.departFrom) {
        this.departFrom[i] = ""
      }
      this.isRowClick = false
      this.diaTitle1 = ""
      this.addDia1 = true
      this.orgId = row.id
      this.departFrom.regionCode = row.id
      this.row = row
    },
    // 
    logout(row) {
      this.$confirm(
        "ID" + row.id + ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      )
        .then(() => {
          // console.log("logout")
          this.$get("/sys/org/deleteOrg", {
            orgId: row.id
          }).then(data => {
            // console.log("logout", data)
            if (data.data.success) {
              this.$message({
                type: "success",
                message: "!"
              })
              this.tableData = []
              this.defaultTreeKey = []
              this.defaultTreeKey.push(this.parentId)
              this.initTree()
            } else {
              this.$message({
                type: "error",
                message: data.data.resultDes
              })
            }
          })
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          })
        })
    },
