      this.$get("/sys/org/queryTree").then(data => {
        if (data.data.success) {
          // let list = JSON.parse(JSON.stringify(data.data.result))
          // dg(list);
          // list[0].children = list[0].children.filter(item => {
          //   return item;
          // });
          // let list1 = list[0].children;
          // for (let i in list1) {
          //   if (list1[i].children) {
          //     list1[i].children = list1[i].children.filter(item => {
          //       return item;
          //     });
          //     for (let j in list1[i].children) {
          //       delete list1[i].children[j].children;
          //     }
          //   }
          // }
          // this.sideTree = list
          this.treeData = data.data.result
          this.mainLoading = false
        }
      })
    },
    regionCodeChange(val) {
      // console.log(val)
    },
    addCreate(formName) {
      // console.log("addcreate")
      this.$refs[formName].validate(valid => {
        if (valid) {
          // console.log(this.orgId, "xxx", this.parentId)
          // return
          this.$post("/sys/org/saveOrg", {
            sysOrgDTO: {
              id: this.orgId,
              parentId: Number(this.parentId),
              orgName: this.mechanismForm.orgName,
              orgenterCode: this.mechanismForm.orgenterCode,
              shortName: this.mechanismForm.shortName,
              regionCode: this.mechanismForm.regionCode,
              linkman: this.mechanismForm.linkman,
              leader: this.mechanismForm.leader,
              orgOrder: this.mechanismForm.orgOrder,
              zip: this.mechanismForm.zip,
              superdept: this.mechanismForm.superdept,
              orgAddr: this.mechanismForm.orgAddr,
              orgDesc: this.mechanismForm.orgDesc
            },
            insList: this.checkedCities
          }).then(data => {
            if (data.success) {
              this.$mess(data)
              // this.tableData.push({
              //   orgName: this.mechanismForm.orgName,
              //   createTime: this.format(new Date()),
              //   linkman: this.mechanismForm.linkman,
              // })
              this.isMainShow = false
              this.isTable = true
              this.tableData = []
              this.defaultTreeKey = []
              this.defaultTreeKey.push(this.parentId)
              this.initTree()
            }
          })
        } else {
          return false
        }
      })
    },
    // mechanismForm.regionCode
    addOrg(num, data) {
      // if (this.id == "271") {
      //   return
      // }
      this.isMainShow = true
      this.addOrEdit = ""
      for (let key in this.mechanismForm) {
        if (key == "regionCode") {
          // this.mechanismForm.regionCode = this.parentId
          // console.log(this.mechanismForm.regionCode)

          continue
        }
        this.mechanismForm[key] = ""
      }
      this.checkedCities = []
      this.orgId = ""
      if (data) {
        // console.log(num, data, 12312312)

        this.parentId = this.id
      }
    },
    addDownOrg() {
      this.isMainShow = true
      this.addOrg("num", this.mechanismForm.regionCode)
    },
    addCity() {
      for (let key in this.cityForm) {
        this.cityForm[key] = ""
      }
    },
    saveCity() {
      this.$post("/sys/org/saveOrg", {
        sysOrgDTO: {
          id: Number(this.orgId),
          parentId: Number(this.parentId),
          orgName: this.cityForm.orgName,
          orgenterCode: this.cityForm.orgenterCode,
          shortName: this.cityForm.shortName,
          regionCode: this.cityForm.regionCode,
          linkman: this.cityForm.linkman,
          leader: this.cityForm.leader,
          orgOrder: this.cityForm.orgOrder,
          zip: this.cityForm.zip,
          superdept: this.cityForm.superdept,
          orgAddr: this.cityForm.orgAddr,
          orgDesc: this.cityForm.orgDesc
        },
        insList: []
      }).then(data => {
        this.$mess(data)
        if (data.success) {
          this.defaultTreeKey = []
          this.defaultTreeKey.push(this.orgId)
          this.initTree()
        }
      })
    },
    handleCheckAllChange(val) {
      // console.log(val)

      this.checkedCities = val ? this.citiesData : []
      this.isIndeterminate = false
    },
    handleCheckedCitiesChange(value) {
      // console.log(value)

      let checkedCount = value.length
      this.checkAll = checkedCount === this.citiesData.length
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.citiesData.length
      // this.isIndeterminate =false;
    },
    handleView(row) {
      // console.log(row.id)
      this.$get("/sys/org/findOrgById", {
        orgId: row.id
      }).then(data => {
        // console.log(data)
        if (data.data.success) {
          this.isView = true
          this.mechanismForm = data.data.result.sysOrg
          // this.checkedCities = data.data.result.insList
          // this.mechanismForm.regionCode = data.data.result.sysOrg.regionCode
        } else {
          this.$mess(data.data.msg)
        }
      })
    },
    handleEdit(row) {
      this.$get("/sys/org/findOrgById", {
        orgId: row.id
      }).then(data => {
        // console.log(data)
        if (data.data.success) {
          this.isEdit = true
          this.orgId = row.id
          this.mechanismForm = data.data.result.sysOrg
          // this.checkedCities = data.data.result.insList
          // this.mechanismForm.regionCode = data.data.result.sysOrg.regionCode
        } else {
          this.$mess(data.data.msg)
        }
      })
    },
    // 
    handleCurrentChange(val) {
      this.tableData = this.tableDatas.slice((val - 1) * 10, val * 10)
    },
    //  tableDatas
    format(date) {
      // debugger
      let addZore = (s) => {
        if (s < 10) {
          return "0" + s.toString()
        }
        return s
      }
      let year = date.getFullYear()
      let month = date.getMonth() + 1
      month = addZore(month)
      let day = date.getDate()
      day = addZore(day)
      let hh = date.getHours()
      hh = addZore(hh)
      let mm = date.getMinutes()
      mm = addZore(mm)
      let ss = date.getSeconds()
      ss = addZore(ss)
      return year + "-" + month + "-" + day + " " + hh + ":" + mm + ":" + ss
    },
    getAllAgency(data) {
      // debugger
      if (data.children) {
        // console.log("getAllAgency", data)
        for (let i = 0; i < data.children.length; i++) {
          if (!data.children[i].children && data.children[i].type == 1) {
            this.tableDatas.push({
              id: data.children[i].id,
              orgName: data.children[i].orgName,
              createTime: this.format(new Date(Number(data.children[i].createTime))),
              isSynchronization: data.children[i].isSynchronization,
              linkman: data.children[i].linkman
            })
          }
        }
      } else {
        this.tableDatas.push({
          id: data.id,
          orgName: data.orgName,
          createTime: this.format(new Date(Number(data.createTime))),
          isSynchronization: data.isSynchronization,
          linkman: data.linkman
        })
      }
      // while(myData.children){
      //   console.log(111, myData)
      //   let len = myData.children.length
      //   let myData = myData.children
      //   for(let i=0; i<len; i++) {
      // dataList.push({orgName: myData.orgName, createTime: myData.createTime, linkman: myData.linkman})
      //   }
      // }
    },
    handleNodeClick(data, node) {
      // console.log("123123123123", data)
      // console.log("2222",node);
      // this.$get("/sys/org/findOrgById", {
      //   orgId: data.id
      // }).then(data => {
      //
      // });
      this.tableDatas = []
      // this.$get("/sys/org/findOrgById", {
      //   orgId: data.id
      // }).then(data => {
      //   // console.log(data)
      //   if (data.data.success) {
      //     console.log(data.data, '11111')
      //     // this.checkedCities = data.data.result.insList
      //     // this.mechanismForm.regionCode = data.data.result.sysOrg.regionCode
      //   } else {
      //     this.$mess(data.data.msg);
      //   }
      // });
      if (data.type == 1) {
        this.isTypeZore = false
      } else {
        this.isTypeZore = true
      }
      // 
      this.getAllAgency(data)
      this.tableData = this.tableDatas.slice(0, 10)
      // console.log(this.tableData)
      this.tableData.forEach(item => {
        item.isShow = false
        this.$set(item, "departList", [])
      })
      this.isTable = true
      let len = 0
      len = data.idpath.split("/").length
      if (data.type == 1) {
        this.parentId = node.parent.data.id
      } else {
        this.parentId = data.id
      }
      this.orgId = data.id
      this.id = data.id
      this.addOrEdit = ""
      var obj = Object.assign({}, data)
      this.mechanismForm = obj
      // this.mechanismForm = data;

      this.cityNameOptions = node.parent.childNodes

      // this.cityForm.orgName = data.label;
      this.cityForm = obj
      if (len == 1) {
        this.cityName = { one: "", two: "", three: "" }
      }
      if (len == 2) {
        this.cityName = {
          one: "",
          two: "",
          three: "",
          four: ""
        }
      }
      if (len == 3 || (len == 2 && data.id == 271)) {
        this.orgOptions = node.parent.childNodes
        // console.log(123, node.parent.childNodes)

        this.cityName = {
          one: "",
          two: "",
          three: "",
          four: ""
        }
        this.fourShow = true
      } else {
        this.fourShow = false
      }
      if (node.parent.data.id == 271 || data.id == 271) {
        this.areaDisabled = true
      } else {
        this.areaDisabled = false
      }
      this.orgDisabled = true
      if (len == 4 || node.parent.data.id == 271) {
        this.orgDisabled = false
        this.cityName.four = ""
        this.orgOptions = node.parent.parent.childNodes
        // console.log("orgOptions", this.orgOptions)
        //
        this.$get("/sys/org/findOrgById", {
          orgId: data.id
        }).then(data => {
          // console.log("abc", data)
          if (data.data.success) {
            this.checkedCities = data.data.result.insList
            // this.mechanismForm.regionCode = data.data.result.sysOrg.regionCode
          }
        })
      }
    }
  }
}
</script>

